import React, { Component } from 'react';
import { DatePicker, Button, Icon } from 'antd';
import Prescriptionordertable from '../Prescriptionorder/Prescriptionorder'
import Treatmentorderlayout from '../Treatmentorder/Treatmentorder'
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

class Orderview extends Component {
    state = {
        UDORDER0: [],
        TRTMNTQ1: [],
        isLoading: false,
        PickerDateRange: [moment(moment().subtract(6, 'days').format('YYYYMMDD')),
        moment(moment().format('YYYYMMDD'))],
    }
    componentDidMount() {
        this.apidata()
    }
    apidata() {
        this.setState({ isLoading: true });
        const { caseinfo } = this.props
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()
        const UDORDER0 = ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureUDORDER0 NG " + JSON.stringify(err))
            });
        const TRTMNTQ1 = ApiProvider.getStoredProcedureTRTMNTQ1(HID, CID)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureTRTMNTQ1 NG " + JSON.stringify(err))
            });
        Promise.all([UDORDER0, TRTMNTQ1]).then(result => {
            this.setState({
                UDORDER0: result[0],
                TRTMNTQ1: result[1],
                isLoading: false,
            });
        }).catch(() => { this.setState({ isLoading: false }) });
    }

    PickerDateRangeChange = (date, dateString) => {
        this.setState({ PickerDateRange: date })
    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const { PickerDateRange } = this.state
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const st = PickerDateRange[0].valueOf()
        const et = PickerDateRange[1].valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                if (bronDate <= (st - day)) {
                    const bTime = [moment(moment(st - day).format('YYYYMMDD')), moment(moment(et - day).format('YYYYMMDD'))]
                    this.PickerDateRangeChange(bTime)
                }
                break;
            case 'N':
                const nTime = [moment(moment().subtract(6, 'days').format('YYYYMMDD')), moment(moment().format('YYYYMMDD'))]
                this.PickerDateRangeChange(nTime)
                break;
            case 'A':
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                if (aTemp >= (et + day)) {
                    const aTime = [moment(moment(st + day).format('YYYYMMDD')), moment(moment(et + day).format('YYYYMMDD'))]
                    this.PickerDateRangeChange(aTime)
                }
                break;
            default:
                break;
        }
    }

    render() {
        const { caseinfo } = this.props
        const udata = this.state.UDORDER0
        const tdata = this.state.TRTMNTQ1
        const momentDate = this.state.PickerDateRange

        const { RangePicker } = DatePicker
        const PickerDateRange = [momentDate[0].valueOf(), momentDate[1].valueOf()]
        if (udata.length === 0 || tdata.length === 0) {
            //Loading
            return null
        }

        return (
            <div>
                <div style={{ overflowY: 'auto' }}>
                    <div style={{ display: "grid", gridTemplateColumns: "250px 120px", gridColumnGap: "5px", marginBottom: "5px" }}>
                        <RangePicker
                            getCalendarContainer={triggerNode => triggerNode.parentNode}
                            value={this.state.PickerDateRange}
                            format={"YYYY/MM/DD"}
                            allowClear={false}
                            locale={"zh_TW"}
                            onChange={this.PickerDateRangeChange}
                            disabledDate={(current) => { return current > moment().startOf() || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }}
                        />
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px" }}>
                            <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                            <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                            <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                        </div>
                    </div>
                    {
                        this.state.isLoading
                            ? (<Icon type="loading" />)
                            : (
                                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" }}>
                                    <Prescriptionordertable PickerDateRange={PickerDateRange} UDORDER0={udata} searchbar={true} caseinfo={caseinfo}></Prescriptionordertable>
                                    <Treatmentorderlayout PickerDateRange={PickerDateRange} TRTMNT01={tdata} searchbar={true} caseinfo={caseinfo}></Treatmentorderlayout>
                                </div>
                            )
                    }
                </div>
            </div>
        );
    }
}

export default Orderview;
