import React, { Component } from 'react';
import * as d3 from "d3";
import { growdata } from '../jsonResponse'
import moment from 'moment'

//40週後
class Wardmonthafter extends Component {
    state = {
        preWeight: null,
        preWeek: null,
        preday: null
    }
    componentDidMount() {
        const { weightList, svg_key, grewinfo } = this.props
        console.log(grewinfo)
        if (grewinfo !== undefined) {
            if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined) {
                if (weightList !== undefined) {
                    this.setState({ preWeight: weightList.length, preWeek: grewinfo.WEEK_BORN, preday: grewinfo.DAY_BORN })
                    d3.select("#" + svg_key).remove()
                    this.drawChart();
                }
            }
        }
    }

    componentDidUpdate() {
        const { weightList, svg_key, grewinfo } = this.props
        const { preWeight, preWeek, preday } = this.state
        console.log(grewinfo)
        if (grewinfo !== undefined) {
            if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined) {
                if (weightList !== undefined) {
                    if (weightList.length !== preWeight || grewinfo.WEEK_BORN !== preWeek || grewinfo.DAY_BORN !== preday) {
                        this.setState({ preWeight: weightList.length, preWeek: grewinfo.WEEK_BORN, preday: grewinfo.DAY_BORN })
                        d3.select("#" + svg_key).remove()
                        this.drawChart();
                    }
                }
            }
        }

    }

    drawChart() {
        const { gender, svg_key, weightList, grewinfo, width, height, id_key } = this.props
        if (grewinfo === null || grewinfo === undefined) {
            return null
        }
        const Birthday = grewinfo.BIRTHDAY
        const pregencyTimestamp = moment(Birthday, "YYYYMMDD").valueOf()
        //var vertigo = datarelease();

        function tickValues(growdata, gender) {
            if (gender === 'M' || gender === '1') {
                const Row = growdata.Male_after
                console.log(Row)
                return [Row[Row.length - 1].min, Row[Row.length - 1].max]
            }
            const Row = growdata.FeMale_after
            return [Row[Row.length - 1].min, Row[Row.length - 1].max]

        }

        const tick = tickValues(growdata, gender)
        const X_axis_array = [0, 30, 60, 90, 120, 150, 180]

        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([1500, 7000])
            .range([height, 0])



        var x = d3.scaleLinear()
            .domain([0, 180])
            .range([0, width])

        var yAxis = d3.axisLeft(y)
            .ticks(10)
            .tickSize(0, 0)
            .tickPadding(10)
            .tickFormat(function (d, index) { return index === 11 ? d + "(g)" : d });

        var yRightAxis = d3.axisLeft(y)
            .tickValues(tick)
            .tickSize(0, 0)
            .tickPadding(-40)
            .tickFormat(function (d, index) {
                if (index === 0) { return "10th%" }
                if (index === 1) { return "90th%" }
                return null
            });


        var xAxis = d3.axisTop(x)
            .tickValues(X_axis_array)
            .tickSize(0, 0)
            .tickPadding(-15)
            .tickFormat(function (d) {
                return d / 30 + "m";
            });


        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .style("height", "435px")
            .style("width", "100%")
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 30 + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");



        svg.append("g")
            .call(xAxis)
            .attr("class", "axis")
            .attr("transform", "translate(" + 0 + ", " + height + ")");


        svg.append("g")
            .call(yRightAxis)
            .attr("class", "axis")
            .attr("transform", "translate(" + width + ", " + 0 + ")");

        for (let index = 0; index < 7; index++) {
            svg.append('line')
                .attr('x1', x(index * 30))
                .attr('y1', 0)
                .attr('x2', x(index * 30))
                .attr('y2', height)
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);

        }

        for (let index = 1; index < 13; index++) {
            const number = index * 500 + 1000
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(number))
                .attr('x2', width)
                .attr('y2', y(number))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);

        }

        // for (let index = 22; index < 45; index++) {
        //     svg.append('line')
        //         .attr('x1', x(index))
        //         .attr('y1', 0)
        //         .attr('x2', x(index))
        //         .attr('y2', height)
        //         .style('stroke', 'rgba(187, 187, 187, 1)')
        //         .style('stroke-width', 1)
        //         .style('stroke-dasharray', 5.5);
        // }

        if (gender === 'M' || gender === '1') {
            for (let index = 0; index < growdata.Male_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_after[index].y))
                    .attr('y1', y(growdata.Male_after[index].min))
                    .attr('x2', x(growdata.Male_after[index + 1].y))
                    .attr('y2', y(growdata.Male_after[index + 1].min))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }

            for (let index = 0; index < growdata.Male_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_after[index].y))
                    .attr('y1', y(growdata.Male_after[index].max))
                    .attr('x2', x(growdata.Male_after[index + 1].y))
                    .attr('y2', y(growdata.Male_after[index + 1].max))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }
        }
        else {
            for (let index = 0; index < growdata.FeMale_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_after[index].y))
                    .attr('y1', y(growdata.FeMale_after[index].min))
                    .attr('x2', x(growdata.FeMale_after[index + 1].y))
                    .attr('y2', y(growdata.FeMale_after[index + 1].min))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }

            for (let index = 0; index < growdata.FeMale_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_after[index].y))
                    .attr('y1', y(growdata.FeMale_after[index].max))
                    .attr('x2', x(growdata.FeMale_after[index + 1].y))
                    .attr('y2', y(growdata.FeMale_after[index + 1].max))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }
        }


        function formatPremature(Weekborn, Dayborn, birthdayTimestamp, elementTimeStamp) {
            const startDate = moment(birthdayTimestamp);
            const endDate = moment(elementTimeStamp);
            const minus = endDate.diff(startDate, 'days');
            const tempWeek = Math.floor(Weekborn + (Dayborn + minus) / 7);
            const tempDay = Math.floor((Dayborn + minus) % 7);
            if (tempWeek >= 40) {
                const overDay = (tempWeek - 40) * 7 + tempDay
                return overDay

            }
            return null
        }

        function formmatWeightTimeForBorn(Weekborn, Dayborn, birthdayTimestamp, elementTimeStamp) {
            // console.log(Weekborn, Dayborn, birthdayTimestamp, elementTimeStamp)
            if (Number(Weekborn) >= 37) {
                return (elementTimeStamp - birthdayTimestamp) / (24 * 60 * 60 * 1000)
            }
            return formatPremature(Weekborn, Dayborn, birthdayTimestamp, elementTimeStamp)
            //早產
        }
        const datasource = weightList
        //x時間 y體重
        for (let index = 0; index < datasource.length; index++) {
            const element = datasource[index];
            const elementDay = element.rec_dat.toString().trim()
            const elementTime = element.rec_tim.toString().trim()
            const time = elementDay + elementTime
            const elementTimestamp = moment(time, "YYYYMMDDHHmm").valueOf() //體重時間
            const Month = formmatWeightTimeForBorn(grewinfo.WEEK_BORN, grewinfo.DAY_BORN, pregencyTimestamp, elementTimestamp)
            console.log(Month)
            if (Month < 0 || Month > 180 || Month === null) {
                continue
            }
            if (Number(element.value2) * 1000 > 1500) {
                svg.append('circle')
                    .attr('cx', x(Month))
                    .attr('cy', y(Number(element.value2) * 1000))
                    .attr('r', 1.5)
                    .style('fill', 'green')
            }
            // svg.append('circle')
            // .attr('cx', x(Month))
            // .attr('cy', y(Number(element.value2) * 1000))
            // .attr('r', 1.5)
            // .style('fill', 'green')

        }
        // const datacustom = wardchartoncustom
        // for (let index = 0; index < datacustom.length; index++) {
        //     const element = datacustom[index];
        //     svg.append('circle')
        //         .attr('cx', x(element.x))
        //         .attr('cy', y(element.y))
        //         .attr('r', 3)
        //         .style('fill', 'blue')
        // }

    }

    render() {
        const { id_key, svg_key } = this.props
        return (
            <div id={id_key}>
                <svg id={svg_key}></svg>
            </div>
        );
    }
}

export default Wardmonthafter;
