import React, { Component } from 'react';
import { Modal, Input, Button } from 'antd';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

export default class RemarkModal extends Component {
    state = {
        preremark: null,
        preeditor: null,
    }

    componentDidMount() {
        this.onInputChange = this.onInputChange.bind(this);
        this.onOkClick = this.onOkClick.bind(this);
        const remark = this.props.remarkValue.remark
        console.log(this.props.remarkValue.remark)
        this.setState({ preremark: remark })

    }

    componentDidUpdate() {
        const remark = this.props.remarkValue.remark
        const { preremark } = this.state
        console.log(preremark)
        if (preremark === null || preremark === "") {
            if (preremark !== remark) {
                this.setState({ preremark: remark })
            }
        }

    }
    onInputChange(event) {
        // const preString = this.state.preremark + event.target.value
        console.log(event.target.value)
        this.setState({ preremark: event.target.value })
    }

    onOkClick(event) {
        const { transinId, type, remarkId } = this.props.remarkValue
        const { preremark, preeditor, } = this.state;
        let fixTime = moment().format('YYYY-MM-DD[T]HH:mm:ss');
        // console.log(name, remark, remarkId, fixTime, transinId, type, editor)
        const name = "breathSetting"

        let tempRemark = ''

        if (event.target.value == '1') {
            tempRemark = preremark
        } else {
            tempRemark = ''
            this.setState({ preremark: null })
        }

        ApiProvider.createRemark(name, tempRemark, remarkId, fixTime, transinId, type, preeditor)
            .then(result => {
                console.log("createRemark OK " + JSON.stringify(result));
                this.props.onOk(tempRemark);
            })
            .catch(err => {
                console.log("createRemark NG " + JSON.stringify(err));
            });
    }

    render() {
        const { TextArea } = Input;
        const { createTime, name } = this.props.remarkValue;
        const onCancel = this.props.onCancel;
        const onOk = this.onOkClick.bind(this);
        console.log(this.state.preremark)
        return (
            <Modal
                title="註記功能"
                visible={this.props.visible}
                onCancel={onCancel}
                okText="儲存"
                cancelText="取消"
                footer={[
                    <Button style={{ float: 'left' }} onClick={onOk} value="0">刪除</Button>,
                    <Button onClick={onCancel}>取消</Button>,
                    <Button type="primary" onClick={onOk} value="1">儲存</Button>
                ]}
            >
                <div style={{ display: "grid", gridTemplateRows: "30px 30px 30px 30px" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>開立時間: {createTime}</div>
                        <div>上次編輯時間: </div>
                        {/* <div>上次編輯時間: {this.state.editTime}</div> */}
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>處理名稱: {name}</div>
                        <div>上次編輯者: {this.state.editor}</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>頻次:</div>
                        <div>開立醫師: {this.state.editor}</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>註記內容:</div>
                    <TextArea rows={5} placeholder="請輸入備註內容" value={this.state.preremark} onChange={this.onInputChange} />
                </div>
            </Modal>
        )
    }
}
