import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import wardviewlogo from '../Image/svg/Wardview.svg'
import historylogo from '../Image/svg/Historyview.svg'
import controllogo from '../Image/svg/Control.svg'
import './Sidermenu.scss';


class Sidermenu extends Component {

    state = {
        powerLogin: Number(sessionStorage.getItem('role'))
    }

    componentDidMount() {
        const powerLogins = Number(sessionStorage.getItem('role'))
        this.setState({ powerLogin: powerLogins })
    }

    Selectkeynumber(key) {

        switch (key) {
            case '/nicu':
                return 0;
            case '/nicu/history':
                return 1;
            case '/nicu/system':
                return 2;
            default:
                if (key.match('patient') != null) {
                    if(this.props.location.state && this.props.location.state.fromHistory){
                        return 1;
                    }
                    return 0;
                }
                if (key.match('ward') != null) {
                    return 0;
                }
                return '';
        }
    }

    select_hover(value) {
        const { path } = this.props
        const keynumber = this.Selectkeynumber(path)
        if (keynumber === value) {
            return true
        }
        return false
    }
    render() {
        const rule = this.state.powerLogin
        return (
            <div style={{ display: 'flex', alignItems: 'center', marginLeft: "20px", height: '100%' }}>
                <div style={{ display: "grid", gridTemplateColumns: "auto auto auto", gridColumnGap: "10px" }}>
                    <Link to="/nicu/ward/wardindex" className={this.select_hover(0) ? "linkstate" : "dislinkstate"} style={{ display: rule == 2 ? "none" : "flex" }}>
                        <img src={wardviewlogo} alt='wardviewlogo'></img>
                        <centerstyle>
                            <div className={this.select_hover(0) ? "linkfont" : "dislinkfont"}>住院病人資訊</div>
                        </centerstyle>
                    </Link>
                    <Link to="/nicu/history" className={this.select_hover(1) ? "linkstate" : "dislinkstate"} style={{ display: rule == 1 ? "flex" : "none" }}>
                        <img src={historylogo} alt='historyviewlogo'></img>
                        <centerstyle>
                            <div className={this.select_hover(1) ? "linkfont" : "dislinkfont"} >歷史病歷管理</div>
                        </centerstyle>
                    </Link>
                    <Link to='/nicu/system' className={this.select_hover(2) ? "linkstate" : "dislinkstate"} style={{ display: rule == 2 ? "flex" : "none" }}>
                        <img src={controllogo} alt='systemviewlogo'></img>
                        <centerstyle>
                            <div className={this.select_hover(2) ? "linkfont" : "dislinkfont"}>系統設定</div>
                        </centerstyle>
                    </Link>
                </div>
            </div>
        );
    }
}

export default withRouter(Sidermenu);
