import React, { Component } from 'react'
import { DatePicker, Icon, Button } from 'antd'
import Vitalxaxis from '../../Module/Vitalxaxis'
// import Infectionvitalsign from './Infectionvitalsign'
import Infectionlab from './Infectionlab'
import Orderonchronic from '../ChronicleView/ChronicOrder'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'
import './Infection.scss'
import Vitalxaxisbot from '../../Module/Vitalxaxisbot'
import Vitalonchronic from '../ChronicleView/Chronic_vital'

class Infectionview extends Component {
    state = {
        datasource: [],
        statussource: "CM",
        intervalselect: 2,
        PickerDate: moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf(),
        UDORDER0: [],
        RESLAB02: [],
        RESLAB01: [],
        classParser: undefined,
        isLoading: false,
        hrdatasource: [],
        rrdatasource: [],
        spo2datasource: [],
        abpdatasource: [],
        nbpdatasource: [],
        btdatasource: [],
        QDVS: [],
        QOXY: [],
    }
    componentDidMount() {
        this.apidata()
    }

    apidata() {
        const { caseinfo } = this.props
        this.setState({ isLoading: true });
        const dateTime = this.state.PickerDate
        const et = dateTime + (31 * 60 * 60 * 1000)
        const st = moment(et - (7 * 24 * 60 * 60 * 1000)).format("YYYY-MM-DD")
        const apiClass = ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Prescription");
                console.log("getGitHubJson Prescription OK " + JSON.stringify(parser))
                this.setState({
                    classParser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
        const apiUDO = ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const apiLAB2 = ApiProvider.getStoredProcedureRESLAB02(caseinfo.HISID, st)
            .then(data => {
                this.setState({
                    RESLAB02: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const apiLAB1 = ApiProvider.getStoredProcedureRESLAB01(caseinfo.HISID, st)
            .then(data => {
                this.setState({
                    RESLAB01: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });

        Promise.all([apiClass, apiUDO, apiLAB2, apiLAB1]).then(() => { this.setState({ isLoading: false }) }).catch(() => { this.setState({ isLoading: false }) });
    }
    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });

        inputs.forEach(input => {
            let content = input.UDGNAME;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });
        });
        return allResult;
    }
    sourcehandleChange = (value) => {
        this.setState({
            statussource: value
        })

    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const PickerDate = this.state.PickerDate
        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = bTime - (6 * day)
                bronDate <= bTemp && this.setState({ PickerDate: bTime })
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.setState({ PickerDate: nTime })
                // this.PickerDateRangeChange(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.setState({ PickerDate: aTime })
                break;
            default:
                break;
        }
    }

    render() {
        function vallofnull(value) {
            if (value === null || value === undefined || value === "") {
                return ""
            }
            return value.toString().trim()
        }
        // const { Option } = Select;
        const { caseinfo } = this.props
        const { classParser, UDORDER0 } = this.state
        const LabData = this.state.RESLAB02
        const LabData2 = this.state.RESLAB01
        const dateTime = this.state.PickerDate
        const et = dateTime + (31 * 60 * 60 * 1000)
        const st = et - (7 * 24 * 60 * 60 * 1000)
        var WBCList = []
        var BandList = []
        var SegList = []
        var LymList = []
        var PCTList = []
        var CRPList = []
        if (Array.isArray(LabData)) {
            if (LabData.length !== 0) {
                for (let index = 0; index < LabData.length; index++) {
                    const element = LabData[index];
                    const WBC = vallofnull(element.RSV01)
                    const Band = vallofnull(element.RSV13)
                    const Seg = vallofnull(element.RSV14)
                    const Lym = vallofnull(element.RSV15)
                    const PCT = null
                    const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                    if (st < time && time < et) {
                        if (WBC !== null || WBC !== "" || WBC !== undefined) {
                            WBCList.push({
                                Time: time,
                                Data: WBC
                            })
                        }
                        if (Band !== null || Band !== "" || Band !== undefined) {
                            BandList.push({
                                Time: time,
                                Data: Band
                            })
                        }
                        if (Seg !== null || Seg !== "" || Seg !== undefined) {
                            SegList.push({
                                Time: time,
                                Data: Seg
                            })
                        }
                        if (Lym !== null || Lym !== "" || Lym !== undefined) {
                            LymList.push({
                                Time: time,
                                Data: Lym
                            })
                        }
                        if (PCT !== null || PCT !== "" || PCT !== undefined) {
                            PCTList.push({
                                Time: time,
                                Data: PCT
                            })
                        }
                    }
                }
            }
        }
        if (Array.isArray(LabData2)) {
            if (LabData2.length !== 0) {
                for (let index = 0; index < LabData2.length; index++) {
                    const element = LabData2[index];
                    const CRP = vallofnull(element.RSV30)
                    const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                    if (st < time && time < et) {
                        if (CRP !== null || CRP !== "" || CRP !== undefined) {
                            CRPList.push({
                                Time: time,
                                Data: CRP
                            })
                        }
                    }
                }
            }
        }

        let clusteredData = {}
        if (classParser !== undefined) {
            clusteredData = this.parse(UDORDER0, classParser)
        }

        //filter data by selected class
        const filteredData = clusteredData["抗生素類別"];

        return (
            <div style={{ padding: "0px 20px" }}><div style={{ display: "grid", gridTemplateColumns: "140px 120px", gridColumnGap: "5px" }}>
                <DatePicker
                    getCalendarContainer={triggerNode => triggerNode.parentNode}
                    disabledDate={(current) => { return current > moment().startOf() || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }}
                    value={moment(this.state.PickerDate)}
                    onChange={(date, datestring) => { this.setState({ PickerDate: date.valueOf() }) }}
                    allowClear={false} />
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px" }}>
                    <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                    <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                    <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                </div>
            </div>
                <div style={{ margin: "10px 0px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "150px 1fr", height: "10px", marginTop: "10px" }}>
                        <div></div>
                        <Vitalxaxisbot max={200} min={0} select_interval={2} select_date={this.state.PickerDate} id_key="xaxis_bot_infect" svg_id="xaxis_bot_svg_infect"></Vitalxaxisbot>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                        <div></div>
                        <Vitalxaxis max={200} min={0} select_interval={2} select_date={this.state.PickerDate} id_key="xaxis_infect" svg_id="xaxis_svg_infect"></Vitalxaxis>
                    </div>
                </div>
                <div className='boxshadow'></div>
                {
                    this.state.isLoading
                        ? (
                            <Icon type="loading" />
                        )
                        : (
                            <div className='Infectionlayout'>
                                <Vitalonchronic PickerDate={this.state.PickerDate} select_interval={2} fromInfection={true} caseinfo={caseinfo} source={"infection"} title="Vital"></Vitalonchronic>
                                {/* <Infectionvitalsign caseinfo={caseinfo} StatusSource={this.state.statussource} PickerDate={this.state.PickerDate}></Infectionvitalsign> */}
                                {/* Order */}
                                <Orderonchronic PickerDate={this.state.PickerDate} UDORDER0={filteredData} select_interval={2} caseinfo={caseinfo} source={"infection"} title="藥物處方"></Orderonchronic>
                                <Infectionlab PickerDate={this.state.PickerDate} WBCList={WBCList} BandList={BandList} SegList={SegList} LymList={LymList} PCTList={PCTList} CRPList={CRPList} title="檢驗結果"></Infectionlab>
                            </div>
                        )
                }
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={2} select_date={new Date().getTime()} id_key="xaxis_flootsheet_bot" svg_id="xaxis_svg_flootsheet_bot"></Vitalxaxis>
                </div>
            </div>
        );
    }
}

export default Infectionview
