import React, { Component } from 'react'
import './ChronicOrderTable.scss'
import moment from 'moment'
import { Tooltip } from 'antd'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'
import * as UDOR from '../../Resource/UDORDER0'

class Chronicordertable extends Component {
    state = {
        UDORDER: [],
        whiteBlackListParser: [],
        tpnParsers: [],
        preHID: null,
        preCID: null
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        this.setState({ preHID: HID, preCID: CID })
        this.getUDORDER(HID, CID)

    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preHID, preCID } = this.state
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        if (preHID !== HID || preCID !== CID) {
            this.setState({ preHID: HID, preCID: CID })
            this.getUDORDER(HID, CID)
        }
    }
    getUDORDER(HID, CID) {
        ApiGithub.getGitHubJson("WhiteBlackList.json")
            .then(data => {
                let parser = data.data;
                this.setState({ whiteBlackListParser: parser })
            })
            .catch(err => { console.log(err) })
        ApiGithub.getGitHubJson("TpnOrder.json")
            .then(data => {
                let parser = data.data
                this.setState({ tpnParsers: parser })
            })
            .catch(err => {
                console.log(err)
            })
        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                this.setState({ UDORDER: data.data })
                //this.setState({ UDORDER: fakeData })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        // this.setState({ UDORDER: UDOR.data })
    }
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    select_parser = (input, parser) => {
        if (parser === undefined) return undefined;
        return parser.find(x =>
            input.match(x.parser_name)
        )
    }
    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let excludeBlackListResult = [];
        const UDGNAME_condition = mappings.find(item => item.key === "UDGNAME");
        const UDRPNAME_condition = mappings.find(item => item.key === "UDRPNAME");
        if (inputs === undefined) return []
        inputs.forEach(input => {
            const UDGNAME_re = new RegExp(UDGNAME_condition.regex_str, UDGNAME_condition.regex_flags)
            const UDGNAME_match = input.UDGNAME.match(UDGNAME_re)

            const UDRPNAME_re = new RegExp(UDRPNAME_condition.regex_str, UDRPNAME_condition.regex_flags)
            const UDRPNAME_match = input.UDRPNAME.match(UDRPNAME_re)

            if (UDGNAME_match === null || UDRPNAME_match === null) {
                excludeBlackListResult.push(input);
            }
        });

        return excludeBlackListResult;
    }
    celltable() {
        function changegridnumber(key) {
            switch (key) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state, openTimeLine } = this.props

        function TimeLine(interval, index) {
            if (interval === 0) {
                if (index === 0 || (index % 24) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else if (index % 8 === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                }
                else {
                    return '1px #eeeeee solid';
                }
            }
            if (interval === 1) {
                if (index === 0 || (index % 6) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else if (index % 2 === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                }
                else {
                    return '1px #eeeeee solid';
                }
            }
            if (interval === 2) {

                if (index === 0 || (index % 3) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else {
                    return '2px rgba(0,0,0,0.2) solid';
                }
            }
            if (interval === 3) {
                return '2px rgba(0,0,0,0.2) solid';
            }
        }
        function checkLineColor(openTimeLine, chronic_state, index) {
            if (openTimeLine) {
                return TimeLine(chronic_state, index);
            }
        }
        const number = changegridnumber(chronic_state)
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div key={index} className='cell' style={{ borderLeft: checkLineColor(openTimeLine, chronic_state, index) }}></div>
            )

        }
        return arr
    }
    render() {
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'ordertableoneday'
                case 1:
                    return 'ordertablethirdday'
                case 2:
                    return 'ordertableoneweek'
                case 3:
                    return 'ordertabletwoweek'
                default:
                    return null
            }
        }
        function ListData(UDORDER0, interval, pickerdate) {

            function formatTimer(pickerdate, interval) {
                const endTime = pickerdate + (31 * 60 * 60 * 1000)
                if (interval === 0) {
                    return {
                        startTime: endTime - (24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                if (interval === 1) {
                    return {
                        startTime: endTime - (3 * 24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                if (interval === 2) {
                    return {
                        startTime: endTime - (7 * 24 * 60 * 60 * 1000),
                        endTime: endTime
                    }
                }
                return {
                    startTime: endTime - (14 * 24 * 60 * 60 * 1000),
                    endTime: endTime
                }
            }
            function formatWidth(startTime, endTime, elementStart, elementEnd) {
                function formatStart(elementStart, startTime) {
                    if (elementStart <= startTime) {
                        return startTime
                    }
                    return elementStart
                }
                function formatEnd(elementEnd, endTime) {
                    if (elementEnd <= endTime) {
                        return elementEnd
                    }
                    return endTime
                }
                const width = document.body.clientWidth - 220
                const widthRate = width / (endTime - startTime)
                const start = formatStart(elementStart, startTime)
                const end = formatEnd(elementEnd, endTime)
                return (end - start) * widthRate
            }
            function formatStartPosition(startTime, endTime, elementStart) {
                const width = document.body.clientWidth - 220
                if (elementStart <= startTime) {
                    return 0
                }
                const widthRate = width / (endTime - startTime)
                return ((elementStart - startTime) * widthRate)
            }
            function formatUDORDER0(UDORDER0, timer) {
                var arraylist = []
                const startTime = timer.startTime
                const endTime = timer.endTime
                for (let index = 0; index < UDORDER0.length; index++) {
                    const element = UDORDER0[index];
                    const elementStart = moment(element.UDBGNDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    const elementEnd = moment(element.UDENDDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    if (endTime < elementStart || elementEnd < startTime) {
                        continue
                    }
                    const shift = formatStartPosition(startTime, endTime, elementStart)
                    const width = formatWidth(startTime, endTime, elementStart, elementEnd)
                    arraylist.push({
                        "width": width,
                        "shift": shift,
                        "element": element
                    })
                }
                return arraylist
            }
            const timer = formatTimer(pickerdate, interval)
            if (UDORDER0 === undefined || UDORDER0 === null) {
                return []
            }
            if (UDORDER0.length === 0) {
                return []
            }
            const formatUDORDER = formatUDORDER0(UDORDER0, timer)
            return formatUDORDER
        }

        const { chronic_state, PickerDate, source } = this.props


        const rawData = this.state.UDORDER

        //filter TPN items for infection page
        let filteredRawData = []
        if (source === "infection") {
            const selected_tpn_parser = this.select_parser("TPN Order", this.state.tpnParsers)
            if (selected_tpn_parser !== undefined) {
                rawData.forEach(item => {
                    const found = item.UDDRGNAM.match(RegExp(selected_tpn_parser.apply_condition.name, selected_tpn_parser.apply_condition.name_regex_flags))
                    if (found !== null && found !== "") filteredRawData.push(item)
                })
            }
        } else {
            filteredRawData = rawData
        }

        let filteredData = [];
        const selected_white_black_list_parser = this.select_parser("Black List", this.state.whiteBlackListParser)
        if (selected_white_black_list_parser !== undefined) {
            filteredData = this.parse(filteredRawData, selected_white_black_list_parser)
        }

        const datalist = ListData(filteredData, chronic_state, PickerDate)
        // const datalist = ListData(UDORDER0, chronic_state, PickerDate)

        const Tablecell = datalist.map(function (value, index, array) {
            function formatString(value) {

                if (value === undefined || value === null) {
                    return "--"
                }
                return value.toString().trim()
            }

            function ToolTipRender(value) {
                function statusFormat(Status) {
                    if (Status === 30){
                        return "使用中"
                    }
                    if (Status === 60){
                        return "已完成"
                    }
                    if (Status === 80){
                        return "已取消"
                    }
                    return Status
                }
                const startTimeFormat = moment(value.element.UDBGNDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
                const endTimeFormat = moment(value.element.UDENDDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
                const status = statusFormat(Number(value.element.UDSTATUS))
                return (
                    <div>
                        <div>時間起迄: {`${startTimeFormat} - ${endTimeFormat}`}</div>
                        <div>用法: {`${value.element.UDDOSAGE} - ${value.element.UDDUNIT} - ${value.element.UDFREQN} - ${value.element.UDROUTE}`}</div>
                        <div>狀態: {status}</div>
                    </div>
                );
            }

            return (
                <div key={index} style={{ padding: "2px 0px", paddingLeft: value.shift + 2 + "px", cursor: "pointer" }}>
                    <Tooltip placement="bottom" autoAdjustOverflow={true} title={ToolTipRender(value)} overlayStyle={{ maxWidth: "800px" }}>
                        <div style={{ width: value.width + "px", height: "20px", backgroundColor: "rgba(0, 120, 230, 0.1)", color: "black", padding: "0px 5px", textOverflow: "ellipsis", whiteSpace: "nowrap", fontWeight: "600" }}>{formatString(value.element.UDGNAME)}</div>
                    </Tooltip>
                </div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", position: 'relative' }}>
                <div style={{ background: "#eeeeee", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>甘特圖</t2>
                </div>
                <div className={changegridstyle(chronic_state)} style={{ height: Tablecell.length ? Tablecell.length * 30 : 40 + "px" }}>{this.celltable()}</div>
                <div style={{ position: 'absolute', left: 140, minHeight: "40px", overflowX: "hidden" }}>
                    {Tablecell}
                </div>
            </div>
        );
    }
}

export default Chronicordertable
