import React, { useState, useEffect } from "react";
import './Wardinfouser.scss'
import moment from 'moment'
import { Modal } from 'antd';
import * as Wardstate from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
// import * as ApiGithub from '../API/ApiGithub'
import { gender } from '../Function/Wardbedfunction'
import Wardgrewchart from './Wardgrewchart'
import Notice from '../Patientview/Schedule/Notice'
import Labviewlayout from '../Patientview/CrossSectionView/Labview'
import WardChart from './Wardchart'
import Wardinfousercheckbox from './Wardunfousercheckbox'
import { Link } from "react-router-dom";
// import * as Reg from '../Data/RegularData';
import Patientinfo from '../Patientview/Patientviewinfo';
import Wardcarstatus from './Wardcardstatus'

function statusPersion(persionstatus, QMNC, QTWER, Birthday) {
    // console.log(persionstatus)
    // console.log(QMNC)
    if (persionstatus.length === 0) {
        console.log(QMNC)
        // console.log("000" + Number(QMNC.weeks))
        if (QMNC.length === 0) {
            return {
                BornWeight: undefined,
                BornWeeks: undefined,
                BornDays: undefined
            }
        }
        const BornWeight = Wardstate.BornWeight(QTWER, Birthday)
        const BornWeeks = Number(QMNC.weeks)
        const BornDays = Number(QMNC.days)
        return {
            BornWeight: BornWeight,
            BornWeeks: BornWeeks,
            BornDays: BornDays
        }
    }
    else {
        const Weight = Number(persionstatus[0].bweight) * 1000
        const BornWeeks = Number(persionstatus[0].bweek)
        const BornDays = Number(persionstatus[0].bday)
        return {
            BornWeight: Weight,
            BornWeeks: BornWeeks,
            BornDays: BornDays
        }
    }
}

export function Wardinfouser(props) {
    const [Visible, setVisible] = useState(false);
    const [CenterMonitorValue, SetCenterMonitorValue] = useState([])
    const [scheduleList, SetScheduleList] = useState([]);
    const [todoList, SetTodoList] = useState([]);
    const [Timer, SetTimer] = useState(0);
    const [Persionstatus, SetPersionstatus] = useState([]);

    useEffect(() => {
        const TimerID = setInterval(() => {
            SetTimer(Timer + 1)
        }, 5000)
        return () => clearInterval(TimerID);
    }, [Timer])


    useEffect(() => {
        const data = props.location.state.data
        // const hisid = "TEST1234"
        const hisid = data.PHISTNUM.toString()
        ApiProvider.getHisidwardvalue(hisid)
            .then(data => {
                console.log(data.data)

                SetCenterMonitorValue(data.data[0]);
            })
            .catch(err => {
                console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
            });
    }, [Timer, props.location.state.data.PHISTNUM])

    useEffect(() => {
        const data = props.location.state.data
        const HID = data.PHISTNUM.toString()
        ApiProvider.getPersionalStatus(HID)
            .then(data => {
                console.log(data.data);
                SetPersionstatus(data.data)
            })
            .catch(err => { console.log(err) })
    }, [props.location.state.data.PHISTNUM,Timer])


    useEffect(() => {
        // "query schedule list"

        const data = props.location.state.data
        const source = data
        const PLOC = source.PLOC
        const TRANSINID = source.TRANSINID.toString()

        const now = new Date();
        const todayBegin = new Date(now.getFullYear(), now.getMonth(), now.getDate());
        const startTime = todayBegin.toISOString().split(".")[0];
        const endTime = new Date(todayBegin.getTime() + 24 * 60 * 60 * 1000).toISOString().split(".")[0];
        ApiProvider.getOnePatientSchedule(TRANSINID, startTime, endTime)
            .then(result => {
                // console.log("getOnePatientSchedule OK " + JSON.stringify(result.data));
                SetScheduleList(result.data);
            })
            .catch(err => {
                // console.log("getOnePatientSchedule NG " + JSON.stringify(err));
            });
    }, [props.location.state.data.TRANSINID]);

    useEffect(() => {
        // "query todo list"
        const data = props.location.state.data
        const source = data
        const PLOC = source.PLOC
        const TRANSINID = source.TRANSINID.toString()
        ApiProvider.getOneTodo(TRANSINID, source.PCASENO.toString())
            .then(result => {
                console.log("getOneTodo OK " + JSON.stringify(result.data));
                SetTodoList(result.data);
            })
            .catch(err => {
                console.log("getOneTodo NG " + JSON.stringify(err));
            });
    }, [props.location.state.data.PCASENO]);

    function renderScheduleItem() {
        if (scheduleList.length === 0) {
            return (
                <div style={{ height: "50px", paddingLeft: "15px", display: "flex", alignItems: "center" }}>
                    沒有排程
                </div>
                // <div>沒有排程</div>
            )
        } else {
            let DataFormat = scheduleList.sort((a, b) => {
                let tempTimeA = moment(a.time, "YYYY-MM-DD[T]HH:mm:ss").valueOf()
                let tempTimeB = moment(b.time, "YYYY-MM-DD[T]HH:mm:ss").valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })

            return DataFormat.map((item, index) => {
                if (item.type === "排程") {
                    // const rawDateStr = item.time;
                    const time = moment(item.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss')
                    const localTime = moment.utc(time).toDate()
                    const displayTimeStr = moment(localTime).format("MM/DD HH:mm")
                    return (
                        <div key={index} style={{ height: "50px", display: 'grid', gridTemplateColumns: "35% 65%", borderBottomWidth: "1px", borderBottomColor: "rgba(232, 232, 232, 1)", borderBottomStyle: "solid", paddingLeft: "15px" }}>
                            <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>{displayTimeStr}</div>
                            <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>{item.context}</div>
                        </div>
                    )
                }
            })
        }
    }
    function renderTodoItem() {
        if (todoList.length === 0) {
            return (
                <div style={{ height: "50px", paddingLeft: "15px", display: "flex", alignItems: "center" }}>
                    沒有待辦事項
                </div>
                // <div>沒有待辦事項</div>
            )
        } else {
            return todoList.map((item, index) => {
                if (item.type === "未完成") {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={false}></Wardinfousercheckbox>)
                } else if (item.type === "已完成") {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={true}></Wardinfousercheckbox>)
                }

            });
        }
    }





    function valueFormat(value) {
        if (value === "" || value === null || value === undefined) {
            return "--"
        }
        return value.toString()
    }
    function valueBornWeight(value) {
        if (value === "" || value === null || value === undefined || value === 0) {
            return ""
        }
        return value.toString() + "g > "
    }

    const data = props.location.state.data
    const source = data
    const Birthday = source.PBIRHDT
    const QTWER = Wardstate.XM2JSON(source.QTWPER)
    const QMNC = Wardstate.QMNCXM2JSON(source.QMNC)
    const ListWeight = Wardstate.WeightList(QTWER)
    const Weight = Wardstate.NewestWeight(QTWER)


    const TRANSINID = source.TRANSINID
    const TRANSINDT = source.TRANSINDT
    const DOCLIST = source.PDOCNEW
    const MainDoc = Wardstate.Doc(DOCLIST, "B")
    const LiveDoc = Wardstate.Doc(DOCLIST, "D")
    const MainNur = Wardstate.Doc(DOCLIST, "N")
    const ICUMainDoc = Wardstate.Doc(DOCLIST, "U")

    const BornWeight = statusPersion(Persionstatus, QMNC, QTWER, Birthday).BornWeight
    const BornWeeks = statusPersion(Persionstatus, QMNC, QTWER, Birthday).BornWeeks
    const BornDays = statusPersion(Persionstatus, QMNC, QTWER, Birthday).BornDays
    const status = {
        date: moment(Birthday, "YYYYMMDD").valueOf(),
        week: BornWeeks,
        day: BornDays
    }
    const AgeStatus = Wardstate.CalculateAge(status)

    // const PBASINFO = data.PBASINFO
    const caseinfo = {
        "HISID": source.PHISTNUM.toString(),
        "CASEID": source.PCASENO.toString(),
        "WEEK_BORN": BornWeeks,
        "DAY_BORN": BornDays,
        "TRANSINID": TRANSINID,
        "TRANSINDT": TRANSINDT,
        "WEIGHT": QTWER,
        "PBASINGNINFO": source
    }

    const grewinfo = {
        "WEEK_BORN": BornWeeks,
        "DAY_BORN": BornDays,
        "BIRTHDAY": Birthday
    }
    console.log(grewinfo)
    // const patientDetail = fixpatientDate(QENT)
    return (
        <div className='wardinfouserlayout'>
            <Modal
                width={800}
                title="入院時評估"
                visible={Visible}
                onOk={() => setVisible(false)}
                onCancel={() => setVisible(false)}
                footer={null}>
                <Patientinfo caseinfo={caseinfo}></Patientinfo>
            </Modal>
            <div className='wardinfopatient'>
                <div style={{ height: '50px', display: 'grid', gridTemplateColumns: "1fr 1fr 1fr" }}>
                    <Link to="/nicu/ward/wardindex" style={{ alignItems: "center", display: "flex" }}>
                        <t2 className='wardblacktoindex'>&larr;返回總覽</t2>
                    </Link>
                    <centerstyle>
                        <t1 sytle={{ fontSize: "22px" }}>NICU-{Number(source.HBEDNO)} {source.PNAMEC.toString().trim()}</t1>
                    </centerstyle>
                    <Link to={{ pathname: "/nicu/patient/" + source.PHISTNUM.toString().trim(), state: { "source": null, "HISID": caseinfo.HISID, "CASEID": caseinfo.CASEID, "BEDNUMBER": Number(source.HBEDNO), "TYPE": true, "TRANSINID": caseinfo.TRANSINID } }} style={{ alignItems: "center", justifyContent: "flex-end", display: "flex" }}>
                        <t2 className='wardblacktoindex'>進入病人資料頁面&rarr;</t2>
                    </Link>
                </div>
                <div style={{ height: "50px" }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <t2>病歷號:{source.PHISTNUM}</t2>
                        <t2>性別:{gender(source.PSEX)}</t2>
                        <t2>生日:{moment(Birthday, "YYYYMMDD").format("YYYY/MM/DD")}</t2>
                        <t2>住院日:{source.TRANSINDT}</t2>
                        <Wardcarstatus source={false} PCASENO={source.PCASENO} PHISTNUM={source.PHISTNUM}></Wardcarstatus>
                    </div>
                    <div style={{ display: 'flex', justifyContent: "space-between", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto" }}>
                            <t3 style={{ color: "rgba(0,0,0,0.7)" }}>
                                {valueFormat(grewinfo.WEEK_BORN)}+{valueFormat(grewinfo.DAY_BORN)}&gt;<span style={{ fontWeight: "900" }}>{AgeStatus}</span>
                            </t3>
                            <t3 style={{ color: "rgba(0,0,0,0.7)", marginLeft: 60 }}>
                                {valueBornWeight(BornWeight)}<span style={{ fontWeight: "900" }}>{valueFormat(Weight)}g</span>
                            </t3>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "repeat(5,auto)", gridColumnGap: "10px" }}>
                            <t3>主治 {MainDoc}</t3>
                            {MainDoc == ICUMainDoc || <t3>加護 {ICUMainDoc}</t3>}
                            {MainDoc == LiveDoc || <t3>住院 {LiveDoc}</t3>}
                            {MainNur == '' || <t3>NP {MainNur}</t3>}
                            <div className='wardblacktoindex' onClick={() => setVisible(true)}>入院時評估</div>
                        </div>
                    </div>
                </div>
                <div className='wardinfousercontent'>
                    <div style={{ padding: "10px 15px" }}>
                        <WardChart CID={source.PCASENO} HISID={source.PHISTNUM} CMvalue={CenterMonitorValue}></WardChart>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px", padding: "0px 3px" }}>
                        <div style={{ borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>檢驗項目</t2>
                            </div>
                            <Labviewlayout Source={"Wardview"} PickerDate={moment(moment().format('YYYYMMDD')).valueOf()} caseinfo={caseinfo}></Labviewlayout>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px", height: 446 }}>
                            <Wardgrewchart caseinfo={caseinfo} grewinfo={grewinfo} Data={source} WeightList={ListWeight} type={true}></Wardgrewchart>
                        </div>
                    </div>
                    <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px", marginTop: "10px", marginRight: "3px" }}>
                        {<Notice caseinfo={caseinfo} disableEdit={true}></Notice>}
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px", marginTop: "10px", paddingRight: "3px" }}>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>排程</t2>
                            </div>
                            <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                {renderScheduleItem()}
                            </div>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                            <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>待辦事項</t2>
                            </div>
                            <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                {renderTodoItem()}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
} 
