import React, { Component } from "react";
import { Labwarningvalue } from "../../Function/Wardstate";
import moment from "moment";
import { Tooltip } from "antd";

class Labviewitem extends Component {
  TimeRender(time) {
    function formatTime(timevalue) {
      if (timevalue === null || time === "--") {
        return "--"
      }
      return moment(time.ORRESDT.toString().trim() + time.ORRESTM.toString().trim(), "YYYY-MM-DDHH.mm.ss").format("MM/DD")
    }
    function formatTimeToolTip(timevalue) {
      if (timevalue === null || time === "--") {
        return "--"
      }
      return moment(time.ORRESDT.toString().trim() + time.ORRESTM.toString().trim(), "YYYY-MM-DDHH.mm.ss").format("HH:mm")
    }
    // console.log(time)
    if (time === null || time === "--") {
      return (
        <centerstyle>
          {"--"}
        </centerstyle>
      )
    }
    return (
      <centerstyle>
        <Tooltip placement="bottom" title={formatTimeToolTip(time)}>
          {formatTime(time)}
        </Tooltip>
      </centerstyle>
    );
  }

  fixtitle = title => {
    switch (title) {
      case "Seg":
      case "Lymp":
        return (
          <div>
            <t3>{title}</t3>
            <t3 style={{ fontSize: "10px" }}>%</t3>
          </div>
        );
      case "Na":
        return (
          <div>
            <t3>{"Na"}</t3>
            <sup>+</sup>
          </div>
        );
      case "K":
        return (
          <div>
            <t3>{"K"}</t3>
            <sup>+</sup>
          </div>
        );
      case "Ca":
        return (
          <div>
            <t3>{"Ca"}</t3>
            <sup>2+</sup>
          </div>
        );
      case "Mg":
        return (
          <div>
            <t3>{title}</t3>
            <sup>2+</sup>
          </div>
        );
      case "PCO":
        return (
          <div>
            <t3>{title}</t3>
            <sub>2</sub>
          </div>
        );
      case "PO":
        return (
          <div>
            <t3>{title}</t3>
            <sub>2</sub>
          </div>
        );
      default:
        break;
    }
    return title;
  };

  render() {
    function formatValue(value, title) {
      if (title === "FeSat") {
        if (value === "--") {
          return "--"
        }
        return value + "%"
      }
      return value
    }
    function RowsGrid(source) {
      if (source === "Wardview") {
        return "1fr 1fr 1fr";
      }
      return "1fr 1fr 1fr";
    }
    function RowsHeight(source) {
      if (source === "Wardview") {
        return "80px";
      }
      return "80px";
    }

    const { value, title, time, Source } = this.props;
    // console.log(time)
    const timeCell = this.TimeRender(time)
    const fixtitle = this.fixtitle(title);
    return (
      <div style={{
        display: "grid",
        gridTemplateRows: RowsGrid(Source),
        height: RowsHeight(Source)
      }}>
        <centerstyle>
          {/* <t3>{title}</t3> */}
          {fixtitle}
        </centerstyle>
        <centerstyle>
          <t2
            style={{
              fontSize: "20px",
              color: Labwarningvalue(value, title) ? "black" : "red"
            }}
          >
            {formatValue(value, title)}
          </t2>
        </centerstyle>
        {timeCell}
      </div>
    );
  }
}

export default Labviewitem;
