import React, { Component } from 'react'
import * as d3 from "d3";
import './Watervaluechart.scss'
import { Modal } from 'antd'
// import { getRandomfornumber } from '../../Function/Wardstate'
import moment from 'moment';

class Watervaluechart extends Component {
    state = {
        visible: false,
        select: null,
        rowdata: null
    };
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: true,
            select: null,
            rowdata: null
        });
    };
    componentDidUpdate() {
        const { svg_key } = this.props
        d3.select("#" + svg_key).remove()
        this.drawChart()

    }
    componentDidMount() {
        this.drawChart();
    }

    componentWillUnmount() {
    }

    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 130
            }
        }
        else {
            if (axisBot) {
                return 130
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 5
            }
            else {
                return 5
            }
        }
    }

    drawChart() {
        function formatShiftDays(startTime, endTime) {
            const shiftDays = parseInt((endTime - startTime) / (24 * 60 * 60 * 1000))
            return shiftDays
        }
        const { svg_key, id_key, startTime, watervalueData } = this.props
        const width = (window.screen.availWidth - 50) / 2 - 50, height = 400
        const max = 500, min = 0
        const shiftDays = formatShiftDays(moment(startTime, "YYYYMMDD").valueOf(), moment().valueOf())

        if (watervalueData.length === 0) {
            return null
        }
        const rectwidth = (width / shiftDays) - 2
        var x = d3.scaleLinear()
            .domain([0, watervalueData.length])
            .range([0, width]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])



        var yAxis = d3.axisLeft(y)
            .ticks(11)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisBottom = d3.axisBottom(x)
            .ticks(watervalueData.length)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) {
                if (d % 10 === 0) {
                    if (d > 0) {
                        return 'D' + d
                    }
                    return null
                }
                else return null;
            });


        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width + 80, height + 50])
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 30 + ")"); 


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");

        svg.append("g")
            .call(xAxisBottom)
            .attr("class", "axis");


        for (let index = 0; index < 11; index++) {
            //上下虛線
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(index * 50))
                .attr('x2', width)
                .attr('y2', y(index * 50))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }

        for (let index = 0; index < watervalueData.length; index++) {
            //上下虛線
            if (index % 10 === 0 && index > 0) {
                svg.append('line')
                    .attr('x1', x(0.5 + index))
                    .attr('y1', y(min))
                    .attr('x2', x(0.5 + index))
                    .attr('y2', y(max))
                    .style('stroke', 'rgba(187, 187, 187, 1)')
                    .style('stroke-width', 1)
                    .style('stroke-dasharray', 5.5);
            }
        }




        // function rowdatageneral(number) {
        //     const rowdata = {
        //         'IV': {
        //             "max": getRandomfornumber(number),
        //             "min": 0
        //         },
        //         'feeding': {
        //             "max": getRandomfornumber(number),
        //             "min": 0
        //         },
        //         'Other': {
        //             "max": getRandomfornumber(number),
        //             "min": 0
        //         }
        //     }
        //     return rowdata
        // }
        const select = this.state.select
        if (select === null || select === undefined || select === "") {
            d3.selectAll("rect#A123").remove()
        }
        for (let index = 0; index < watervalueData.length; index++) {
            const rowdata = watervalueData[index]
            const orindex = index + 0.5
            var svgrect = svg.append('g').on('click', () => {
                if (rowdata === null) {

                }
                else {
                    this.setState({
                        visible: true,
                        select: index,
                        rowdata: rowdata
                    });

                }
            })
            if (index === select) {
                svgrect.append('rect')
                    .attr('id', 'A123')
                    .attr('width', rectwidth + 2)
                    .attr('height', y(0) - y(rowdata.Feeding + rowdata.IV + rowdata.Other))
                    .attr('x', x(orindex) - (rectwidth / 2) - 1)
                    .attr('y', y(rowdata.Feeding + rowdata.IV + rowdata.Other))
                    .style('fill', 'rgba(0,0,0,0)')
                    .style('stroke', '#F5A623')
                    .style('stroke-width', 2)
                    .style('cursor', 'pointer');
            }
            else {
                svgrect.append('rect')
                    .attr('id', 'A123')
                    .attr('width', rectwidth + 2)
                    .attr('height', y(0) - y(rowdata.Feeding + rowdata.IV + rowdata.Other))
                    .attr('x', x(orindex) - (rectwidth / 2) - 1)
                    .attr('y', y(rowdata.Feeding + rowdata.IV + rowdata.Other))
                    .style('fill', 'rgba(0,0,0,0)')
                    .style('stroke', 'rgba(0,0,0,0)')
                    .style('stroke-width', 2)
                    .style('cursor', 'pointer')
                    .style('opacity', 1);
            }
            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(rowdata.Feeding + rowdata.IV + rowdata.Other))
                .attr('x2', x(orindex))
                .attr('y2', y(rowdata.IV + rowdata.Other))
                .style('stroke', '#2E64A4')
                .style('stroke-width', rectwidth)

            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(rowdata.IV + rowdata.Other))
                .attr('x2', x(orindex))
                .attr('y2', y(rowdata.Other))
                .style('stroke', '#C3EFFF')
                .style('stroke-width', rectwidth)


            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(rowdata.Other))
                .attr('x2', x(orindex))
                .attr('y2', y(0))
                .style('stroke', '#3B97E1')
                // .style('stroke', 'red')
                .style('stroke-width', rectwidth)
        }
    }

    ModalRender() {
        function nulldata(value) {
            if (value === null || value === undefined || value === "") {
                return 0
            }
            return value
        }
        const data = this.state.rowdata
        if (data === null || data === undefined) {
            return null
        }
        // function sumRowdata(data) {
        //     if (data === null || data === undefined) {
        //         return 0
        //     }
        //     return data * 0.8
        // }
        const select = this.state.select
        const waterdata = this.props.watervalueData
        const car = waterdata[select].Caloriessum
        return (
            <Modal
                title="Basic Modal"
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={null}
            >
                <div>IV:{nulldata(data.IV)}</div>
                <div>Feeding:{nulldata(data.Feeding)}</div>
                <div>Other:{nulldata(data.Other)}</div>
                <div>總熱量:{car}kcal</div>
            </Modal>
        );
    }
    render() {
        const { id_key } = this.props
        const model = this.ModalRender()
        return (
            <div id={id_key}>
                {model}
            </div>
        );
    }
}

export default Watervaluechart
