import React, { Component } from 'react';
import Notice from './Notice'
import Bedsidecard from './Bedsidecard'
import Scheduletodo from './Scheduletodo'
import Calender from './Calender'
import './Scheduleview.scss'

class Scheduleview extends Component {
    render() {
        function monthformat(month) {
            return month + 1
        }
        function dateformat(time) {
            return new Date(time).getFullYear() + "-" + monthformat(new Date(time).getMonth()) + "-" + new Date(time).getDate()
        }
        function exporttime(time) {
            if (time === null) {
                return "--"
            }
            else {
                return dateformat(time)
            }
        }
        const { caseinfo } = this.props
        const time = new Date().getTime()

        return (
            <div>
                <div style={{ marginBottom: "5px" }}>
                    <centerstyle>
                        <div className='scheduletitle'>轉入時間:{dateformat(time)}/轉出時間:{exporttime(null)}</div>
                    </centerstyle>
                    <div className='boxshadow'></div>
                </div>
                <div className='schedulelayout'>
                    <Bedsidecard caseinfo={caseinfo}></Bedsidecard>
                    <Notice caseinfo={caseinfo}></Notice>
                    <Calender caseinfo={caseinfo}></Calender>
                    <Scheduletodo caseinfo={caseinfo}></Scheduletodo>
                </div>
            </div>
        );
    }
}

export default Scheduleview
