import React, { Component } from 'react';
import Labviewitem from './Labviewitem'
// import { labdata } from '../../Data/Wardviewdata'
import * as FiliterReg from '../../Function/FiliterReg'
import * as ApiProvider from '../../API/ApiProvider'
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'

var MG_VALUE, PROC_VALUE, CA_VALUE;
class Labviewlayout extends Component {
    state = {
        parser: [{ "apply_condition": { "name": "" }, "mappings": [] }],
        dummy_mg: null,
        dummy_pct: null,
        dummy_ca: null,

        RESDGLU1: [],
        RESLAB02: [],
        RESLAB01: [],
        RESDBGAS: [],
        RESLAB0C: [],
        RESSECT: [],

        prePickerDate: null,
        preHID: null,
        preTRDT: null
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("Labview.json")
            .then(result => {
                console.log("getGitHubJson Labview.json OK " + JSON.stringify(result.data));
                this.setState({
                    parser: result.data
                })
            })
            .catch(err => {
                console.log("getGitHubJson Labview.json NG " + JSON.stringify(err));
            })
        const { PickerDate, caseinfo } = this.props
        const TRDT = moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD').valueOf()
        this.setState({ prePickerDate: PickerDate, preHID: caseinfo.HISID, preTRDT: TRDT })
        this.getLabData(PickerDate, caseinfo.HISID, TRDT)
    }

    componentDidUpdate() {
        const { PickerDate, caseinfo } = this.props
        const { prePickerDate, preHID, preTRDT } = this.state
        const TRDT = moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD').valueOf()
        console.log(prePickerDate, PickerDate)
        console.log(caseinfo.HISID, preHID)
        console.log(TRDT, preTRDT)
        if (prePickerDate !== PickerDate || preHID !== caseinfo.HISID || preTRDT !== TRDT) {
            this.setState({ prePickerDate: PickerDate, preHID: caseinfo.HISID, preTRDT: TRDT })
            this.getLabData(PickerDate, caseinfo.HISID, TRDT)
        }
    }
    getLabData(PickerDate, HID, TRDT) {
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - PickerDate
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const TransinDate = moment(TRDT).format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(PickerDate)
        ApiProvider.getStoredProcedureRESDGLU1(HID, TransinDate)
            .then(data => { this.setState({ RESDGLU1: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESDGLU1 NG " + JSON.stringify(err)); });
        ApiProvider.getStoredProcedureRESLAB02(HID, TransinDate)
            .then(data => { this.setState({ RESLAB02: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESLAB02 NG " + JSON.stringify(err)); });
        ApiProvider.getStoredProcedureRESLAB01(HID, TransinDate)
            .then(data => { this.setState({ RESLAB01: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESLAB01 NG " + JSON.stringify(err)); });
        ApiProvider.getStoredProcedureRESDBGAS(HID, TransinDate)
            .then(data => { this.setState({ RESDBGAS: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESDBGAS NG " + JSON.stringify(err)); });
        ApiProvider.getStoredProcedureRESLAB0C(HID, TransinDate)
            .then(data => { this.setState({ RESLAB0C: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESLAB0C NG " + JSON.stringify(err)); });
        ApiProvider.getStoredProcedureRESSECT(HID, MonthInterval, "ALL", "DOC3924B")
            .then(data => { this.setState({ RESSECT: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err)) });
    }
    findLastestTargetValue(arr, target) {
        const { caseinfo } = this.props
        const { parser } = this.state
        let latestTimestamp = 0;
        let latestvalue = undefined;
        const selected_parser = parser.find(item => item.apply_condition.name.match(target) !== null);
        const arrayLength = arr.length;
        const { preHID } = this.state
        if (preHID !== caseinfo.HISID) {
            arr.forEach((item, index) => {
                ApiProvider.getStoredProcedureRESDISP(caseinfo.HISID, item.ORMM, caseinfo.CASEID, item.ORDSEQNO)
                    .then(data => {
                        console.log("getStoredProcedureRESDISP OK " + JSON.stringify(data.data));
                        const RESDISP = data.data

                        //search for target
                        const selected_mapping = selected_parser.mappings.find(mapping => mapping.key === target)
                        const re = new RegExp(selected_mapping.regex_str, selected_mapping.regex_flags)

                        RESDISP.forEach(element => {
                            const match = element.CONTENT.match(re)
                            if (match !== null) {
                                const currentTimeStr = item.ORRCPDT + "-" + item.ORRCPTM
                                const currentItemTimestamp = moment(currentTimeStr.trim(), "YYYY-MM-DD-HH.mm.ss").valueOf()
                                const matchValue = match[selected_mapping.position]

                                if (matchValue !== "" && matchValue !== undefined && matchValue !== null && currentItemTimestamp > latestTimestamp) {
                                    latestvalue = matchValue
                                    if (target === "mg") {
                                        MG_VALUE = latestvalue
                                    } else if (target === "pct") {
                                        PROC_VALUE = latestvalue
                                    } else if (target === "ca") {
                                        CA_VALUE = latestvalue
                                    }
                                }
                            }
                        })
                        this.updateLabviewIfNeed(arrayLength, index, target)
                    })
                    .catch(err => {
                        console.log("getStoredProcedureRESDISP NG " + JSON.stringify(err));
                        this.updateLabviewIfNeed(arrayLength, index, target)
                    });
            })
        }
    }
    updateLabviewIfNeed(arrayLength, currentIdx, target) {
        const retryMax = 3
        const { dummy_mg, dummy_pct, dummy_ca } = this.state
        if (dummy_mg === null || dummy_pct === null || dummy_ca === null) {
            if (currentIdx === arrayLength - 1) {
                if (target === "mg" && this.state.dummy_mg < retryMax) {
                    this.setState({ dummy_mg: this.state.dummy_mg + 1 })
                } else if (target === "pct" && this.state.dummy_pct < retryMax) {
                    this.setState({ dummy_pct: this.state.dummy_pct + 1 })
                } else if (target === "ca++" && this.state.dummy_ca < retryMax) {
                    this.setState({ dummy_ca: this.state.dummy_ca + 1 })
                }
            }
        }
    }
    TitleRender() {
        const { Source } = this.props
        if (Source === "Wardview") {
            return null
        }
        else return (
            <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                <t1 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>最近實驗室數據</t1>
            </div>
        );
    }
    render() {
        function ListEndData(array) {
            if (array.length === 0) {
                return "--"
            }
            return array[array.length - 1]
        }
        // const data = labdata.data
        const { PickerDate, Source } = this.props
        const { RESDGLU1, RESLAB02, RESLAB01, RESDBGAS, RESSECT } = this.state

        const RESDGLU1LIST = FiliterReg.ListForTime(RESDGLU1, PickerDate)
        const RESLAB02LIST = FiliterReg.ListForTime(RESLAB02, PickerDate)
        const RESLAB01LIST = FiliterReg.ListForTime(RESLAB01, PickerDate)
        const RESDBGASLIST = FiliterReg.ListForTime(RESDBGAS, PickerDate)
        // const RESLAB0CLIST = FiliterReg.ListForTime(RESLAB0C, PickerDate)

        const RESSECT_CA_LIST = FiliterReg.ListForTimeAndTarget(RESSECT, PickerDate, "Ca")
        this.findLastestTargetValue(RESSECT_CA_LIST, "ca")

        const RESSECT_MG_LIST = FiliterReg.ListForTimeAndTarget(RESSECT, PickerDate, "Mg")
        this.findLastestTargetValue(RESSECT_MG_LIST, "mg")

        const RESSECT_PROC_LIST = FiliterReg.ListForTimeAndTarget(RESSECT, PickerDate, "Procalcitonin")
        this.findLastestTargetValue(RESSECT_PROC_LIST, "pct")

        const GLUFAST = ListEndData(RESDGLU1LIST.filter(function (value, index, array) {
            return value.GLUFAST !== null
        }))
        const WBC = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV01 !== null
        }))
        const SEG = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV14 !== null
        }))
        const LYMP = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV15 !== null
        }))
        const HGB = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))
        const PLATELET = ListEndData(RESLAB02LIST.filter(function (value, index, array) {
            return value.RSV09 !== null
        }))
        const NA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV13 !== null
        }))
        const K = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV14 !== null
        }))
        const CA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV15 !== null
        }))
        const P = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV17 !== null
        }))
        const ALB = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV02 !== null
        }))
        const CRP = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV30 !== null
        }))
        const PH = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS07 !== null
        }))
        const PCO2 = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS06 !== null
        }))
        const BE = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS01 !== null
        }))
        const PO2 = ListEndData(RESDBGASLIST.filter(function (value, index, array) {
            return value.SS08 !== null
        }))
        const BUN = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV05 !== null
        }))
        //02
        const CR = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV06 !== null
        }))
        const ALT = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV11 !== null
        }))
        const AST = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV12 !== null
        }))
        const TBIL = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV08 !== null
        }))
        const DBIL = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV25 !== null
        }))
        const ALKP = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV09 !== null
        }))
        const TOTALCA = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))
        const IRON = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV26 !== null
        }))
        const TIBC = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV27 !== null
        }))
        const FERRITIN = ListEndData(RESLAB01LIST.filter(function (value, index, array) {
            return value.RSV03 !== null
        }))
        const FeSat = feSatformat(IRON, TIBC)
        function feSatformat(IRON, TIBC) {
            console.log(IRON)
            if (IRON === null || IRON.RSV26 === null || IRON.RSV26 === undefined || Number(IRON.RSV26) === 0) {
                return null
            }
            if (TIBC === null || TIBC.RSV27 === null || TIBC.RSV27 === undefined || Number(TIBC.RSV27) === 0) {
                return null
            }
            return parseFloat(Number(IRON.RSV26) / Number(TIBC.RSV27)).toFixed(2)
        }
        function areainterval(source) {
            if (source === "Wardview") {
                return "0px"
            }
            else return "10px"
        }

        function nullvalue(value) {
            if (value === undefined || value === null || value === NaN) {
                return null
            }
            return value.toString().trim()
        }
        const TitleR = this.TitleRender()
        return (
            <div >
                {TitleR}
                <div style={{ display: "grid", gridTemplateColumns: "1fr 5fr", gridColumnGap: areainterval(Source) }}>
                    <div style={{ border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(GLUFAST.GLUFAST)} title={"Glucose"} time={(GLUFAST)}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(5,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(WBC.RSV01)} title={"WBC"} time={WBC}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(SEG.RSV14)} title={"Seg"} time={SEG}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(LYMP.RSV15)} title={"Lymp"} time={LYMP}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(HGB.RSV03)} title={"Hgb"} time={HGB}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PLATELET.RSV09)} title={"Platelet"} time={PLATELET}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "5fr 1fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(5,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(NA.RSV13)} title={"Na"} time={NA}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(K.RSV14)} title={"K"} time={K}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(CA_VALUE)} title={"Ca"} time={CA}></Labviewitem> {/*parse*/}
                        <Labviewitem Source={Source} value={nullvalue(P.RSV17)} title={"P"} time={P}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(MG_VALUE)} title={"Mg"} time={null}></Labviewitem> {/*parse*/}
                    </div>
                    <div style={{ border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALB.RSV02)} title={"ALB"} time={ALB}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 4fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(CRP.RSV30)} title={"CRP"} time={CRP}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PROC_VALUE)} title={"PCT"} time={null}></Labviewitem>  {/*parse*/}
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(PH.SS07)} title={"pH"} time={PH}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PCO2.SS06)} title={"PCO"} time={PCO2}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(BE.SS01)} title={"BE"} time={BE}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(PO2.SS08)} title={"PO"} time={PO2}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 2fr 2fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(BUN.RSV05)} title={"BUN"} time={BUN}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(CR.RSV07)} title={"Cr"} time={CR}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALT.RSV11)} title={"ALT"} time={ALT}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(AST.RSV12)} title={"AST"} time={AST}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(TBIL.RSV08)} title={"TBil"} time={TBIL}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(DBIL.RSV25)} title={"DBil"} time={DBIL}></Labviewitem>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "2fr 4fr", gridColumnGap: areainterval(Source), marginTop: areainterval(Source) }}>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(2,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(ALKP.RSV09)} title={"ALK-P"} time={ALKP}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(TOTALCA.RSV03)} title={"Total Ca"} time={TOTALCA}></Labviewitem>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)", border: "rgba(238,238,238,1) solid 1px" }}>
                        <Labviewitem Source={Source} value={nullvalue(IRON.RSV26)} title={"Iron"} time={IRON}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(TIBC.RSV27)} title={"TIBC"} time={TIBC}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(FeSat * 100)} title={"FeSat"} time={null}></Labviewitem>
                        <Labviewitem Source={Source} value={nullvalue(FERRITIN.RSV03)} title={"Ferritin"} time={FERRITIN}></Labviewitem> {/* No Data */}
                    </div>
                </div>
            </div>
        );
    }
}

export default Labviewlayout
