import React, { Component } from 'react';
import { Select, Modal } from 'antd';
// import * as ApiGithub from '../../API/ApiGithub'
import * as RegularData from '../../Data/RegularData'
import moment from 'moment'
import Examchart from './Examchart'
import { Button } from 'antd';
import './ExamLabview.scss';
import { fixControlledValue } from 'antd/lib/input/Input';

class Labview extends Component {
    state = {
        Selectus: this.props.SelectStatus,
        PickerDateRange: [moment(), moment()],
        visible: false,
        lineCharttitle: '',
        lineChart: [],
        IRRange: RegularData.GoodBadRange(),
        IRParse: RegularData.InspectionReportExtent()
    }
    componentDidMount() {
        // ApiGithub.getExamReport()
        //     .then(data => {
        //         this.setState({
        //             TitleList: data.data
        //         })
        //     })
        //     .catch(err=>{
        //         console.log("GithubAPI NG" + err)
        //     })
        // this.setState({
        //     TitleList: RegularData.ExamReportJson
        // })
    }

    callbackDateFunction = (value) => {
        this.props.parentDateCallback(value);
    }
    handleChange = (value) => {
        this.callbackDateFunction(value)
        this.setState({
            Selectus: value
        })
    }
    titleSearch = (value) => {
        this.props.parentTitleCallback(value.target.value);
    }
    CellRender(title) {
        const { DataList } = this.props
        const { IRRange, Selectus } = this.state

        const key = Object.keys(IRRange)[Selectus]
        const rangeKey = IRRange[key]

        if (DataList.length === 0) {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "110px 1fr" }}>
                    <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                        無資料
                    </div>
                </div>
            )
        }
        const DataListCell = DataList.map(function (element, index) {
            let isSameDay = false;
            if (index < DataList.length - 1) {
                isSameDay = moment(element.Time).isSame(moment(DataList[index + 1].Time), 'day');
            }
            if (index === DataList.length - 1) {
                isSameDay = true;
            }
            const cellRender = element.Data.map(function (info, index) {
                let tempstyle = true

                if (rangeKey !== undefined) {
                    const temp = rangeKey[title[index]]
                    if (temp !== undefined) {
                        const styleMin = temp.min !== undefined ? info > temp.min : true
                        const styleMax = temp.max !== undefined ? info < temp.max : true
                        tempstyle = styleMin && styleMax
                    }
                }

                return (
                    <centerstyle key={index} style={{ flex: 1, borderLeft: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", color: tempstyle ? "auto" : "red" }}>
                        <t3>{info}</t3>
                    </centerstyle>
                );
            })
            const time = moment(element.Time, "YYYY/MM/DDHH:mm:ss").format("MM/DD HH:mm")
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "110px 1fr", height: "50px", borderBottom: isSameDay ? "rgba(238, 238, 238, 1) 1px solid" : '#989898 1px solid' }}>
                    <centerstyle style={{ display: "flex" }}>
                        <t3>{time}</t3>
                    </centerstyle>
                    <div style={{ display: "flex", gridTemplateColumns: "repeat(9,1fr)" }}>
                        {cellRender}
                    </div>
                </div>
            );
        })
        return DataListCell
    }

    fixLineChartData(element) {
        const { DataList } = this.props;
        const value = element.value;
        // console.log(value)
        const number = element.index;
        let showDate = DataList.map(data => {
            let temp = {}
            temp['date'] = data.Time;
            temp['value'] = data.Data[number];
            return temp
        })
        console.log(showDate)
        if (showDate === null || showDate.length === 0) {
            return null
        }

        showDate.sort((x, y) => {
            return moment(x.date, 'YYYYMMDDHHmm').valueOf() - moment(y.date, 'YYYYMMDDHHmm').valueOf()
        })

        this.setState({
            lineCharttitle: value,
            lineChart: showDate,
        }, () => { this.showModel() });
    }
    showModel = () => {
        this.setState({
            visible: true,
        });
    }
    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    charttableview = () => {
        const { IRParse, Selectus, lineCharttitle, lineChart } = this.state
        console.log(lineCharttitle)
        if (lineCharttitle === '') { return null }

        const key = Object.keys(IRParse)[Selectus]
        const rangeKey = IRParse[key]
        const range = rangeKey[lineCharttitle]
        const itemValue = []
        //主要崩潰點在這 因為你會了key但是lineCharttitle還有值 這樣會變成undefined這樣就crash了 你下面定義是有range情況下的max是不是undefined 但是你本身range就是undefined 所以會找不到range中的max
        if (range === undefined) { return null }
        console.log(range)
        const max = range.max !== undefined ? range.max : Math.max(...itemValue)
        const min = range.min !== undefined ? range.min : Math.min(...itemValue)

        return (
            <div style={{ margin: "20px 0px" }}>
                <Examchart lineChart={lineChart} lineCharttitle={lineCharttitle} max={max} min={min} id_key={"svgExamID"} svg_id={"svgExamSVG"}></Examchart>
            </div>
        );
    }

    render() {
        const { Option } = Select;
        const visible = this.state.visible
        // const lineCharttitle = this.state.lineCharttitle
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        // const fixLineChartData = this.fixLineChartData
        const charttableview = this.charttableview()
        const LabDataList = RegularData.ExamReportJson()
        //0:CBC 1:電解質 2:肝功能 3:心臟 4:凝血 5:GAS 6:Glucose
        const SelectList = LabDataList.map(function (info, index) {
            // console.log(info.index)
            function StringFormat(formatString) {
                if (formatString === "電解質與腎功能檢驗:Na、K、Cl、Ca++、Ca、Mg++、P、BUN、Cr") {
                    return <div style={{ fontSize: "12px" }}>電解質與腎功能檢驗:Na、K、Cl、Ca<sup>2+</sup>、Ca、Mg<sup>2+</sup>、P、BUN、Cr</div>
                }
                if (formatString === "Gas檢驗:PH、pCO2、pO2、HCO3、BE、O2Sat、FiO2") {
                    return <div style={{ fontSize: "12px" }}>Gas檢驗:pH、pCO<sub>2</sub>、pO<sub>2</sub>、HCO<sub>3</sub>、BE、O<sub>2</sub>Sat、FiO<sub>2</sub></div>
                }
                if (formatString === "GAS(Additionary)檢驗:Hgb、Hct、Na、K、Ca++、BUN、Cr") {
                    return <div style={{ fontSize: "12px" }}>GAS(Additionary)檢驗:Hgb、Hct、Na、K、Ca<sup>2+</sup>、BUN、Cr</div>
                }
                return <div style={{ fontSize: "12px" }}>{formatString}</div>
            }
            const string = StringFormat(info.select)
            return (
                <Option key={info.index} value={info.index} style={{ fontSize: "12px" }}>{string}</Option>
            );
        })
        const SelectTitlefilter = LabDataList.filter(function (element, index) {
            const selectdata = Select
            return element.index = selectdata
        })
        if (SelectTitlefilter.length === 0) {
            return null
        }
        const SelectTitle = SelectTitlefilter[this.state.Selectus].title
        const SelectTitleRender = SelectTitle.map((info, index) => {
            function formatString(formatString) {
                if (formatString === "Ca++") {
                    return <div style={{ fontSize: "13px" }}>Ca<sup>2+</sup></div>
                }
                if (formatString === "Mg++") {
                    return <div style={{ fontSize: "13px" }}>Mg<sup>2+</sup></div>
                }
                if (formatString === "pCO2") {
                    return <div style={{ fontSize: "13px" }}>pCO<sub>2</sub></div>
                }
                if (formatString === "pO2") {
                    return <div style={{ fontSize: "13px" }}>pO<sub>2</sub></div>
                }
                if (formatString === "HCO3") {
                    return <div style={{ fontSize: "13px" }}>HCO<sub>3</sub></div>
                }
                if (formatString === "FiO2") {
                    return <div style={{ fontSize: "13px" }}>FiO<sub>2</sub></div>
                }
                if (formatString === "O2Sat") {
                    return <div style={{ fontSize: "13px" }}>O<sub>2</sub>Sat</div>
                }
                return formatString
            }
            const infoString = formatString(info)
            const element = {
                value: info,
                index: index
            }
            return (
                <centerstyle style={{ flex: 1 }} key={index}>
                    <Button style={{ height: "26px", backgroundColor: "transparent", border: "2px white solid", display: "flex", gridTemplateColumns: "repeat(9,1fr)" }} index={index} value={info} onClick={() => { this.fixLineChartData(element) }}>{infoString}</Button>
                </centerstyle>
            );
        })
        const ExamLab = this.CellRender(SelectTitle)

        return (
            <div>
                <Modal
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1200}
                    footer={null}>
                    {charttableview}
                </Modal>
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "grid", gridTemplateColumns: "120px 1fr", alignItems: "center" }}>
                    <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>累積報告</div>
                    <div style={{ display: "grid", gridTemplateColumns: "100px 1fr", gridColumnGap: "5px" }}>
                        <t2 style={{ display: "flex", alignItems: "center" }}>檢驗類別：</t2>
                        <div className="Select-Dropdown" style={{ display: "flex", alignItems: "center" }}>
                            <Select getPopupContainer={triggerNode => triggerNode.parentNode} defaultValue={this.state.Selectus} style={{ width: "100%" }} onChange={this.handleChange}>
                                {SelectList}
                            </Select>
                        </div>
                    </div>
                </div>
                <div style={{ backgroundColor: "rgba(226, 237, 243,.5)", height: "30px", display: "grid", gridTemplateColumns: "110px 1fr", alignItems: "center" }}>
                    <centerstyle>
                        <t2 style={{ display: "flex", justifyItems: "center" }}>檢驗時間</t2>
                    </centerstyle>
                    <div style={{ display: "flex" }} onClick={this.titleSearch}>
                        {SelectTitleRender}
                    </div>
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: "calc(100vh - 305px)", maxHeight: "calc(100vh - 305px)", overflowY: "auto" }}>
                        {ExamLab}
                    </div>
                </div>
            </div>
        );
    }
}

export default Labview;
