import React, { Component } from 'react';
import { Checkbox } from 'antd';
import * as ApiProvider from '../API/ApiProvider'

class Wardinfousercheckbox extends Component {
    state = {
        checkboxselect: this.props.selectstate
    }

    updateOnePatientTodo() {
        if (this.state.checkboxselect) {
            ApiProvider.createTodo(this.props.item.caseid, this.props.item.context, this.props.item.time, this.props.item.transinno, '已完成')
                .then(data => {
                    console.log('updateOnePatientTodo ok ' + JSON.stringify(data))
                })
                .catch(err => {
                    console.log('updateOnePatientTodo ng ' + JSON.stringify(err))
                })
        } else {
            ApiProvider.createTodo(this.props.item.caseid, this.props.item.context, this.props.item.time, this.props.item.transinno, '未完成')
                .then(data => {
                    console.log('updateOnePatientTodo ok ' + JSON.stringify(data))
                })
                .catch(err => {
                    console.log('updateOnePatientTodo ng ' + JSON.stringify(err))
                })
        }
    }
    selectchange() {
        this.setState({
            checkboxselect: !this.state.checkboxselect
        }, this.updateOnePatientTodo)
    }

    render() {
        const selectunstyle = {
            fontSize: "1rem"
        }
        const selectstyle = {
            textDecoration: "line-through", fontSize: "1rem"
        }
        return (
            <div style={{ height: "50px", display: "flex", alignItems: "center" }}>
                <Checkbox onChange={() => this.selectchange()} checked={this.state.checkboxselect} style={this.state.checkboxselect ? selectstyle : selectunstyle}>{this.props.item.context}</Checkbox>
            </div>
        )
    }

}

export default Wardinfousercheckbox;
