import React, { Component } from 'react';
import IOpage from '../../Module/iopage'
import moment from 'moment';


class CrossectionIo extends Component {
    render() {
        const { caseinfo, title, PickerDate } = this.props
        const titlearray = ['白班', '小夜', '大夜', '全日']
        const titlelab = titlearray.map(function (info, index) {
            return (
                <centerstyle key={index}>
                    <t2 style={{ fontWeight: index === 3 ? '600' : "" }}>{info}</t2>
                </centerstyle>
            );
        })
        return (
            <div style={{ border: '1px solid rgba(238, 238, 238, 1)' }}>
                <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t1 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{title}</t1>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr", height: "40px" }}>
                    <div style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}>{moment(PickerDate).format('M/DD')}</div>
                    {titlelab}
                </div>
                <IOpage PickerDate={PickerDate} caseinfo={caseinfo} Status={"Crossection"} IntervalStatus={0}></IOpage>
            </div>
        );
    }
}

export default CrossectionIo
