import React, { Component } from 'react';
import { Month_format } from '../Commonfunction'

class Bedinformation extends Component {
    state = {
        tableselect: false
    }

    SelectTruechange() {
        this.setState({
            tableselect: true
        })
    }

    SelectFalsechange() {
        this.setState({
            tableselect: false
        })
    }

    sendReturnSelect = (id) => {
        const { selectkey, idkey, localtime } = this.props
        const select = this.Selectswitch(idkey, selectkey)
        if (select) {
            const data = { "idkey": null, "time": localtime }
            this.props.parentCallback(data);
        }
        else {
            this.props.parentCallback(id);
        }
    }

    Selectswitch(id, select) {
        if (id === select) {
            return true
        }
        else return false
    }
    render() {
        function weekdatformat(weekday) {
            switch (weekday) {
                case 0:
                    return "日"
                case 1:
                    return "一"
                case 2:
                    return "二"
                case 3:
                    return "三"
                case 4:
                    return "四"
                case 5:
                    return "五"
                case 6:
                    return "六"
                default:
                    return null
            }
        }
        const { selectkey, idkey, localtime, data } = this.props
        const day = Month_format(new Date(localtime).getMonth()) + "/" + new Date(localtime).getDate()
        const weekday = new Date(localtime).getDay()
        const Unselect_tablestyle = { height: "50px", display: "flex", alignItems: "center", justifyContent: "center", border: "1px solid rgba(238,238,238,1)", borderLeftWidth: "0px" }
        const select = this.Selectswitch(idkey, selectkey)
        if (data === null) {
            return (
                <div>
                    <div style={select ? { border: "1px solid rgba(59, 151, 225, 1)", backgroundColor: "#ffe1c499", marginTop: '-2px' } : { cursor: "pointer" }} onMouseUp={() => this.sendReturnSelect({ "idkey": idkey, "time": localtime })}>
                        <div style={Unselect_tablestyle}>
                            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                                <t2 style={{ textAlign: "center" }}>{day}</t2>
                                <t2 style={{ textAlign: "center" }}>{weekdatformat(weekday)}</t2>
                            </div>
                        </div>
                        <div style={Unselect_tablestyle}>
                            <t2 style={{ textAlign: "center" }}>0(12)</t2>
                        </div>
                        <t2 style={Unselect_tablestyle}>0</t2>
                        <t2 style={Unselect_tablestyle}>0</t2>
                        <t2 style={Unselect_tablestyle}>0</t2>
                        <t2 style={Unselect_tablestyle}>0</t2>
                        <t2 style={Unselect_tablestyle}>0</t2>
                    </div>
                    <div style={{ display: select ? "flex" : "none", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ width: 0, height: 0, borderStyle: "solid", borderWidth: "17.3px 10px 0 10px", borderColor: "#ffbe59 transparent transparent transparent" }}></div>
                    </div>
                </div>
            );

        }
        return (
            <div>
                <div style={select ? { border: "1px solid rgba(59, 151, 225, 1)", backgroundColor: "#ffe1c499", marginTop: '-2px' } : { cursor: "pointer" }} onMouseUp={() => this.sendReturnSelect({ "idkey": idkey, "time": localtime })}>
                    <div style={Unselect_tablestyle}>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                            <t2 style={{ textAlign: "center" }}>{day}</t2>
                            <t2 style={{ textAlign: "center" }}>{weekdatformat(weekday)}</t2>
                        </div>
                    </div>
                    <div style={Unselect_tablestyle}>
                        <t2 style={{ textAlign: "center" }}>{data.hbedcount}({parseInt(parseInt(data.hbedcount) * 100 / 12)}%)</t2>
                    </div>
                    <t2 style={Unselect_tablestyle}>{data.transoutcount}</t2>
                    <t2 style={Unselect_tablestyle}>{data.transincount}</t2>
                    <t2 style={Unselect_tablestyle}>{data.rtcount}</t2>
                    <t2 style={Unselect_tablestyle}>{data.nonrtcount}</t2>
                    <t2 style={Unselect_tablestyle}>{data.tpncount}</t2>
                </div>
                <div style={{ display: select ? "flex" : "none", justifyContent: "center", alignItems: "center" }}>
                    <div style={{ width: 0, height: 0, borderStyle: "solid", borderWidth: "17.3px 10px 0 10px", borderColor: "rgba(255,190,5,1) transparent transparent transparent" }}></div>
                </div>
            </div>
        );
    }
}

export default Bedinformation
