import React, { Component } from 'react';
import calenderlogo from '../../Image/svg/calender.svg'
import calenderwhitelogo from '../../Image/svg/calenderwhite.svg'
import pingcheck from '../../Image/svg/ping.svg'
import pingwhite from '../../Image/svg/pingwhite.svg'
import './Calendercell.scss'
import { mmddformat, weekdateformatcalender } from '../../Function/Timeformat'
import { Col, Row } from "antd"
class Calendercell extends Component {
    constructor(props) {
        super(props);
        this.state = {
            status: false,
        }

    }


    sendcallback(timestamp, clickindex) {
        this.props.parentCallback(timestamp, clickindex);
    }
    onClickCheck(timestamp, clickindex) {
        this.sendcallback(timestamp, clickindex)
        this.onClick1()
    }
    onClick1 = () => {
        if (this.state.status === false) {
            this.setState({ status: true }, () => {
                this.props.CancelModule(true)
            })

        } else {
            this.setState({ status: false }, () => {
                this.props.CancelModule(false)
            })

        }
    }


    render() {
        const { clickindex, state, newDate, timestamp, clickdate, data, today } = this.props;
        function disableday(state) {
            const scheduleArray = data.scheduleList.filter(item => item.type === "排程");
            if (state) {
                return (
                    <div className='calendersubcontexticon'>
                        <img src={calenderlogo} alt='calenderlogo' />
                        <t1>{scheduleArray.length}</t1>
                    </div>
                );
            }
            else {
                return (
                    <div className='calendersubcontexticon'>
                        <img src={calenderwhitelogo} alt='calenderlogo' />
                        <t1 className='displayinfo'>{scheduleArray.length}</t1>
                    </div>
                );
            }
        }
        function locl(state) {
            if (timestamp === today) return '當日'
            else return null
        }
        function stateswitch() {

            if (mmddformat(clickdate) === mmddformat(timestamp)) {
                return true
            }
            return false
        }
        return (
            <Row >
                <Row type="flex" justify="center" >
                    <Col span={24}>
                        <div className={stateswitch() ? 'calendersubcontextclick' : 'calendersubcontext'}
                            onClick={() =>
                                this.onClickCheck(timestamp, clickindex)
                            }>
                            <div className={stateswitch() ? 'calendersubcontexttitleclick' : 'calendersubcontexttitle'}>
                                <t2 className='calendersubcontexttitletoday'>{mmddformat(timestamp)}({weekdateformatcalender(timestamp)}){locl(newDate)}</t2>

                            </div>
                            {disableday(state)}
                        </div>
                    </Col></Row>
                <Row type="flex" justify="center" >
                    <Col >
                        {stateswitch() && this.state.status ?
                            <div style={{ width: 0, height: 0, borderStyle: "solid", borderWidth: "17.3px 10px 0 10px", borderColor: "#ffbe59 transparent transparent transparent" }}></div>
                            : ""}</Col>
                </Row>
            </Row>
        );
    }
}

export default Calendercell
