import React, { Component } from 'react';
import * as d3 from "d3";
import './Wardcardvitalsign.scss';
import { mmddformat } from '../Function/Timeformat'
import * as ApiProvider from '../API/ApiProvider'
import moment from 'moment';
import X2JS from 'x2js';
import * as WardState from '../Function/Wardstate'



class WardcardvitalsignChart extends Component {
    state = {
        sourcedata: [],
        preHID: null,
        BT: []
    }
    componentDidUpdate() {
        const { HISID } = this.props
        const HID = HISID.toString().trim()
        const preHID = this.state.preHID
        if (HID !== preHID) {
            this.setState({ preHID: HISID.toString().trim() })
            this.getCenterMonitor()
        }

    }
    componentDidMount() {
        const { HISID } = this.props
        this.setState({ preHID: HISID.toString().trim() })
        this.getCenterMonitor()
    }

    getCenterMonitor() {
        function selectType(type) {
            switch (type) {
                case "div_hr":
                    return "hr"
                case "div_bp":
                    return "nbp"
                case "div_rr":
                    return "rr"
                case "div_spo":
                    return "spo2"
                case "div_abp":
                    return "abp"
                case "div_bt":
                    return "bt"
                default:
                    break;
            }
        }
        // const simdata = vitalData.simdata
        const endTime = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").format("YYYYMMDDHHmmss");
        const startTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf() - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
        const { id_key, HISID, CID } = this.props
        const HID = HISID.toString().trim()
        const type = selectType(id_key)
        const et = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").format("YYYYMMDDHHmm")
        const st = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf() - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        if (id_key === "div_abp") {
            ApiProvider.getPatientwardvalueBoxPlot(HID, startTime, endTime, type, 0)
                .then(data => {
                    this.setState({
                        sourcedata: data.data
                    })
                    this.drawChart()
                })
                .catch(err => { console.log(err) })
        }
        if (id_key === "div_hr" || id_key === "div_rr" || id_key === "div_spo") {

            ApiProvider.getPatientwardvalueBoxPlot(HID, startTime, endTime, type, 0)
                .then(data => {
                    this.setState({
                        sourcedata: data.data
                    })
                    this.drawChart()
                })
                .catch(err => { console.log(err) })
        }
        if (id_key === "div_bp") {

            ApiProvider.getVitalSignData(HID, startTime, endTime, type)
                .then(data => {
                    this.setState({
                        sourcedata: data.data
                    })
                    this.drawChart()
                })
                .catch(err => {
                    console.log(err);
                })
        }
        if (id_key === "div_bt") {
            console.log(CID, st, et)
            ApiProvider.getQDVS(CID, st, et)
                .then(data => {
                    const BTDATA = WardState.XM2JSON(data.data)
                    console.log(BTDATA)
                    if (Array.isArray(BTDATA)) {
                        const BT_Data = BTDATA.filter(function (value, index, arr) {
                            return value.temperature !== undefined && Number(value.temperature) !== NaN && value.temperature.__text === undefined
                        })
                        const mapBTData = BT_Data.map(function (info, index, arr) {
                            const time = moment(info.rec_dat.toString() + info.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                            return {
                                Data: info.temperature,
                                Time: time
                            }
                        })
                        console.log(mapBTData)
                        this.setState({ BT: mapBTData })
                        // d3.select("#svg_bt").remove()
                        this.drawChart([]);
                    }
                })
                .catch(err => { console.log(err) })
        }
    }
    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 145
            }
        }
        else {
            if (axisBot) {
                return 100
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 20
            }
            else {
                return 10
            }
        }
    }

    drawChart() {
        function multiFormat(date, i, n) {
            if ((n.length - 1) == i) {
                return "現在"
            }
            if (formatHour(date) % 4 === 0) {
                return formatHour(date)
            }
            else return null
        }
        function multibotformat(date) {
            if (new Date(date).getHours() === 0) {
                return mmddformat(date)
            }
            else return null
        }
        const { svg_key, circlrcolor, axisTop, id_key, axisBot, MAX, MIN, Upper, Lower } = this.props
        const width = window.screen.availWidth * 0.25, height = 70, transformmargin = this.cal_svg_transform(), margin = 20
        // const borderwidth = width / 27
        const time_now = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf()
        const startTime = time_now - (24 * 60 * 60 * 1000);

        var time_array = []
        var time_axis_array = []
        for (let index = 0; index < 25; index++) {
            time_array.push({ key: "time", x_axis_time: time_now - index * 60 * 60 * 1000 })
            time_axis_array.push(time_now - index * 60 * 60 * 1000)
        }
        var time_array_reverse = time_array.reverse()
        var time_axis_array_reverse = time_axis_array.reverse()
        var formatHour = d3.timeFormat("%H")

        // const endTime = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf();


        var x = d3.scaleLinear()
            .domain([time_array_reverse[0].x_axis_time, time_array_reverse[24].x_axis_time])
            .range([0, width - 40]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([MIN, MAX])
            .range([height, 0])

        var yAxis = d3.axisLeft(y)
            .tickValues([MIN, MAX])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisTop = d3.axisTop(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(margin + 15)
            .tickFormat(function (d) { return multibotformat(d); });

        var xAxis = d3.axisTop(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(margin)
            .tickFormat(function (d, i, n) { return multiFormat(d, i, n); });

        d3.select("#" + svg_key).remove()
        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width, this.cal_svg_hight()])
            .append("g")
            .attr("transform", "translate(" + 30 + "," + transformmargin + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");


        if (axisTop) {
            svg.append("g")
                .call(xAxis)
                .attr("class", "axis");

            svg.append("g")
                .call(xAxisTop)
                .attr("class", "axis");
        }

        // if (axisBot) {
        //     svg.append("g")
        //         .call(xAxisBottom)
        //         .attr("class", "axis");
        // }

        //上下虛線
        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(MAX))
            .attr('x2', width)
            .attr('y2', y(MAX))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(MIN))
            .attr('x2', width)
            .attr('y2', y(MIN))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);


        for (let index = 0; index < time_array_reverse.length; index++) {
            svg.append('line')
                .attr('x1', x(time_array_reverse[index].x_axis_time))
                .attr('y1', 0)
                .attr('x2', x(time_array_reverse[index].x_axis_time))
                .attr('y2', height)
                .style('stroke', '#E0E0E0')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }



        drawshadow(startTime, 0, Lower, Upper)
        function drawshadow(startTime, interval, lower, upper) {
            const LimitBoxTimer = formatBox(startTime, interval, width)
            for (let index = 0; index < LimitBoxTimer.length; index++) {
                const boxwidth = formatBoxWidth(width, interval)
                const element = LimitBoxTimer[index];
                svg.append('line')
                    .attr('x1', x(element))
                    .attr('y1', y(lower))
                    .attr('x2', x(element))
                    .attr('y2', y(upper))
                    .style('stroke', circlrcolor)
                    .style('opacity', 0.7)
                    .style('stroke-width', boxwidth)

                if (id_key === "div_bp" || id_key === "div_abp") {
                    svg.append('line')
                        .attr('x1', x(element))
                        .attr('y1', y(35))
                        .attr('x2', x(element))
                        .attr('y2', y(55))
                        .style('stroke', circlrcolor)
                        .style('opacity', 0.3)
                        .style('stroke-width', boxwidth)
                }
            }
        }
        function formatBox(startTime, interval) {
            function interFormat(startTime, interval) {
                var TimeArray = []
                for (let index = 0; index < interval; index++) {
                    if (interval === 24) {
                        const boxTime = startTime + (30 * 60 * 1000) + (index * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else if (interval === 18) {
                        const boxTime = startTime + (2 * 60 * 60 * 60 * 1000) + (index * 4 * 60 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else if (interval === 21) {
                        const boxTime = startTime + (4 * 60 * 60 * 60 * 1000) + (index * 8 * 60 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                    else {
                        const boxTime = startTime + (12 * 60 * 60 * 60 * 1000) + (index * 24 * 60 * 60 * 60 * 1000)
                        TimeArray.push(boxTime)
                    }
                }
                return TimeArray
            }
            if (interval === 0) {
                return interFormat(startTime, 24)
            }
            if (interval === 1) {
                return interFormat(startTime, 18)
            }
            if (interval === 2) {
                return interFormat(startTime, 21)
            }
            return interFormat(startTime, 14)
        }
        function formatBoxWidth(viewwidth, interval) {
            if (interval === 0) {
                return viewwidth / 26.5
            }
            if (interval === 1) {
                return viewwidth / 18
            }
            if (interval === 2) {
                return viewwidth / 21
            }
            return viewwidth / 14
        }
        // var sim_data = this.state.sourcedata
        const BoxPlotData = this.state.sourcedata
        console.log(id_key, BoxPlotData)
        if (id_key === "div_bt") {
            const BT_Row = this.state.BT
            for (let index = 0; index < BT_Row.length; index++) {
                const element = BT_Row[index];
                console.log(element)
                svg.append('circle')
                    .attr('cx', x(element.Time))
                    .attr('cy', y(element.Data))
                    .attr('r', 3)
                    .style('fill', circlrcolor)

            }
        }
        if (BoxPlotData.length === 0) {
            return null
        }

        if (id_key === "div_abp") {
            const dataAbps = BoxPlotData.abps
            const dataAbpd = BoxPlotData.abpd
            const boxwidth = 10
            for (let index = 0; index < dataAbpd.length; index++) {
                const element = dataAbpd[index];
                const q1 = element.Q1
                const q2 = element.Q2
                const q3 = element.Q3
                const q4 = element.Q4
                const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf()
                svg.append("line")
                    .attr("x1", x(Xtime))
                    .attr("x2", x(Xtime))
                    .attr("y1", y(q1))
                    .attr("y2", y(q4))
                    .attr("stroke", circlrcolor)
                    .style('opacity', 0.5)
                    // .attr("stroke", "blcak")
                    .style('stroke-width', 1)

                svg.append("rect")
                    .attr("x", x(Xtime) - boxwidth / 2)
                    .attr("y", y(q3))
                    .attr("height", (y(q2) - y(q3)))
                    .attr("width", boxwidth)
                    .attr("stroke", circlrcolor)
                    .style('opacity', 0.5)
                    .style("fill", "rgba(255,255,255,1)")
                    .style('stroke-width', 1);

                svg
                    .selectAll("toto")
                    .data([q1, q4])
                    .enter()
                    .append("line")
                    .attr("x1", x(Xtime) - boxwidth / 2)
                    .attr("x2", x(Xtime) + boxwidth / 2)
                    .attr("y1", function (d) { return (y(d)) })
                    .attr("y2", function (d) { return (y(d)) })
                    .attr("stroke", circlrcolor)
                    .style('opacity', 0.5)
                    .style('stroke-width', 1);
            }
            for (let index = 0; index < dataAbps.length; index++) {
                const element = dataAbps[index];
                const q1 = element.Q1
                const q2 = element.Q2
                const q3 = element.Q3
                const q4 = element.Q4
                const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf()
                svg.append("line")
                    .attr("x1", x(Xtime))
                    .attr("x2", x(Xtime))
                    .attr("y1", y(q1))
                    .attr("y2", y(q4))
                    .attr("stroke", circlrcolor)
                    // .attr("stroke", "blcak")
                    .style('stroke-width', 1)

                svg.append("rect")
                    .attr("x", x(Xtime) - boxwidth / 2)
                    .attr("y", y(q3))
                    .attr("height", (y(q2) - y(q3)))
                    .attr("width", boxwidth)
                    .attr("stroke", circlrcolor)
                    .style("fill", "rgba(255,255,255,1)")
                    .style('stroke-width', 1);

                svg
                    .selectAll("toto")
                    .data([q1, q4])
                    .enter()
                    .append("line")
                    .attr("x1", x(Xtime) - boxwidth / 2)
                    .attr("x2", x(Xtime) + boxwidth / 2)
                    .attr("y1", function (d) { return (y(d)) })
                    .attr("y2", function (d) { return (y(d)) })
                    .attr("stroke", circlrcolor)
                    .style('stroke-width', 1);
            }
        }
        if (id_key === "div_bp") {
            for (let index = 0; index < BoxPlotData.length; index++) {
                const element = BoxPlotData[index];
                const Xtime = moment(element.Time, "YYYY-MM-DD HH:mm:ss").valueOf()
                const SBP = element.SBP
                const DBP = element.DBP
                if (SBP === 0 || DBP === 0) {
                    continue
                }
                svg.append('circle')
                    .attr('cx', x(Xtime))
                    .attr('cy', y(SBP))
                    .attr('r', 3)
                    .style('fill', circlrcolor)


                svg.append('circle')
                    .attr('cx', x(Xtime))
                    .attr('cy', y(DBP))
                    .attr('r', 3)
                    .style('opacity', 0.5)
                    .style('fill', circlrcolor)
            }
        }

        if (id_key === "div_hr" || id_key === "div_rr" || id_key === "div_spo") {
            // for (let index = 0; index < BoxPlotData.length; index++) {
            //     const element = BoxPlotData[index];
            //     const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf() + (30 * 60 * 1000)
            //     svg.append('line')
            //         .attr('x1', x(Xtime))
            //         .attr('y1', y(Lower))
            //         .attr('x2', x(Xtime))
            //         .attr('y2', y(Upper))
            //         .style('stroke', circlrcolor)
            //         .style('opacity', 0.5)
            //         // .style('stroke', 'red')
            //         .style('stroke-width', borderwidth)

            // }

            for (let index = 0; index < BoxPlotData.length; index++) {
                const element = BoxPlotData[index];
                const q1 = element.Q1
                const q2 = element.Q2
                const q3 = element.Q3
                const q4 = element.Q4
                const Xtime = moment(element.time, "YYYY-MM-DD HH:mm:ss").valueOf() + (30 * 60 * 1000)
                const boxwidth = 10
                svg.append("line")
                    .attr("x1", x(Xtime))
                    .attr("x2", x(Xtime))
                    .attr("y1", y(q1))
                    .attr("y2", y(q4))
                    .attr("stroke", circlrcolor)
                    .style('stroke-width', 1)

                svg.append("rect")
                    .attr("x", x(Xtime) - boxwidth / 2)
                    .attr("y", y(q3))
                    .attr("height", (y(q2) - y(q3)))
                    .attr("width", boxwidth)
                    .attr("stroke", circlrcolor)
                    .style("fill", "rgba(255,255,255,1)")
                    .style('stroke-width', 1);

                svg
                    .selectAll("toto")
                    .data([q1, q4])
                    .enter()
                    .append("line")
                    .attr("x1", x(Xtime) - boxwidth / 2)
                    .attr("x2", x(Xtime) + boxwidth / 2)
                    .attr("y1", function (d) { return (y(d)) })
                    .attr("y2", function (d) { return (y(d)) })
                    .attr("stroke", circlrcolor)
                    .style('stroke-width', 1);

            }
        }

    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default WardcardvitalsignChart;
