import React, { Component } from 'react';
import './ExamNonlabtablecell.scss'
// import { Nonlablist } from '../../Data/Wardviewdata'
import moment from 'moment'
import NonlabviewtableCell from '../CrossSectionView/NonlabviewtableCell'
// import { Modal } from 'antd';
// import InsReportModal from '../ExamReportModal'

class Examnonlabviewtable extends Component {
    state = {
        visible: false
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            const resultKey = mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key
            allResult[resultKey] = []
        });
        allResult["全部類別"] = []
        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "" && (mapping.sample ? input.ORSPCN1.trim() === mapping.sample : true)) {
                    const resultKey = mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key
                    allResult[resultKey].push(input)
                }
            });

            allResult["全部類別"].push(input)
        });

        return allResult;
    }

    render() {
        function listforstatus(ordlist, ressect, pickerdaterange, StateMode, InputMode) {
            if (StateMode !== 0) {
                /*
                預設 
                全部 StateMode == 0
                已開單 StateMode == 1, data.ORSTATUS == 30
                已簽收 StateMode == 2, data.ORSTATUS == 31
                已發報告 StateMode == 3, data.ORSTATUS == 32
                */
                const changeState = { '1': '30', '2': '31', '3': '32' }
                ordlist = ordlist.filter(data => {
                    return data.ORSTATUS === changeState[StateMode.toString()]
                })
            }

            if (InputMode !== null) {
                const regex = new RegExp(InputMode, "i");
                ordlist = ordlist.filter(data => {
                    return data.ORPROCED.match(regex) !== null
                })
            }

            let status_report = []
            const et = pickerdaterange[1].valueOf() + (24 * 60 * 60 * 1000)
            const st = pickerdaterange[0].valueOf()

            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    const time = element.ORBGNDT.toString().trim() + element.ORBGNTM.toString().trim()
                    const timeStamp = moment(time, 'YYYY-MM-DDHHmmss').valueOf()
                    if (timeStamp > st && timeStamp < et) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDCLNM === element.ORDCLNM
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }

            // console.log(status_report.length)

            status_report = status_report.sort((a, b) => {
                let tempA = a.ORBGNDT.toString().trim() + a.ORBGNTM.toString().trim()
                let tempTimeA = moment(tempA, 'YYYY-MM-DDHHmmss').valueOf()
                let tempB = b.ORBGNDT.toString().trim() + b.ORBGNTM.toString().trim()
                let tempTimeB = moment(tempB, 'YYYY-MM-DDHHmmss').valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })

            return status_report
        }
        function formatPickerDateRangeToORDLIST(ORDLIST, PickerDateRange) {
            const start = PickerDateRange[0]
            const end = PickerDateRange[1]
            if (Array.isArray(ORDLIST)) {
                const filterORDLIST = ORDLIST.filter(function (info, index, arr) {
                    const BGtime = moment(info.ORBGNDT.toString().trim() + info.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    return start <= BGtime && BGtime <= end
                })
                return filterORDLIST
            }
            return null

        }
        function formatSelectMode(data, selectMode) {
            //StateMode 0:全部狀態 1:正式報告 2:已簽收 3:待送檢 4:其他
            if (Array.isArray(data)) {
                if (selectMode === 1) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.ORSTATUS) === 64
                    })
                    return filterData
                }
                if (selectMode === 2) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.ORSTATUS) === 38
                    })
                    return filterData
                }
                if (selectMode === 3) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.ORSTATUS) === 31
                    })
                    return filterData
                }
                if (selectMode === 4) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.ORSTATUS) !== 31 && Number(info.ORSTATUS) !== 64 && Number(info.ORSTATUS) !== 38
                    })
                    return filterData
                }
                return data
            }
            return null

        }
        function formatInputMode(data, InputMode) {
            if (Array.isArray(data)) {
                if (InputMode === null || InputMode === "" || InputMode === undefined) {
                    return data
                }
                const regex = new RegExp(InputMode, "i");
                const filterInputMode = data.filter(function (info, index, arr) {
                    return info.ORPROCED.toString().toLowerCase().match(regex) !== null
                })
                return filterInputMode
            }
            return null
        }
        const { PickerDateRange, ORDLIST, StateMode, InputMode, selectedClass } = this.props

        const filterTimer = formatPickerDateRangeToORDLIST(ORDLIST, PickerDateRange)
        const filterStateMode = formatSelectMode(filterTimer, StateMode)
        const filterInput = formatInputMode(filterStateMode, InputMode)

        // console.log(filterTimer)
        // console.log(filterStateMode)
        console.log(ORDLIST)
        let clusteredData = null
        if (this.props.parser !== undefined && this.props.parser.mappings.length !== 0) {
            // console.log(this.props.parser.mappings.length)
            clusteredData = this.parse(filterInput, this.props.parser)
        }

        // console.log(clusteredData, this.props.selectedClass)
        if (clusteredData === null) {
            return null
        }
        const filteredData = clusteredData[this.props.selectedClass];
        const sortfilteredData = filteredData.sort(function (a, b) {
            const aBGtime = moment(a.ORBGNDT.toString().trim() + a.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
            const bBGtime = moment(b.ORBGNDT.toString().trim() + b.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
            return aBGtime < bBGtime ? 1 : -1;
        });
        // const list = listforstatus(filteredData, RESSECT, PickerDateRange, StateMode, InputMode)
        const showModal = this.props.showModal
        const nonlabcell = sortfilteredData.map(function (value, index, array) {
            function ORSTATUSToString(ORSTATUS) {
                if (ORSTATUS === 64) {
                    return 0
                }
                if (ORSTATUS === 38) {
                    return 1
                }
                if (ORSTATUS === 31) {
                    return 2
                }
                return ORSTATUS
            }
            // console.log(value)
            const time = moment(value.ORBGNDT.toString().trim() + value.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
            const come = value.ORSPCN1
            const name = value.ORPROCED
            const status = ORSTATUSToString(Number(value.ORSTATUS))

            let isSameDay = false;
            if (index < filteredData.length - 1) {
                const day = moment(time).format('YYYY/MM/DD');
                const compareDay = moment(filteredData[index + 1].ORBGNDT.toString().trim() + value.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").format('YYYY/MM/DD');
                isSameDay = moment(day).isSame(compareDay, 'day');
            }
            if (index === filteredData.length - 1) {
                isSameDay = true;
            }

            return (
                <NonlabviewtableCell key={"Examnonlabviewtable" + index} come={come} time={time} name={name} status={status} hisId={value.ORHISTNO} caseId={value.ORDSEQCN} month={value.ORMM} seq={value.ORDSEQNO} showModal={showModal} isSameDay={isSameDay}></NonlabviewtableCell>
            );
        })

        const title = ["時間", "檢驗項目", "檢體", "狀態", "報告"]
        const titlecell = title.map(function (value, index, arr) {
            // const tempStyle = index == 1
            return (
                <t3 className='nonlabtebletitlecell' style={{ justifyContent: "center" }}>{value}</t3>
            );
        })

        return (
            <div >
                <div style={{ height: "30px", backgroundColor: "rgba(226, 237, 243,.5)", display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                    {titlecell}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: "calc(100vh - 305px)", maxHeight: "calc(100vh - 305px)", overflowY: "auto" }}>
                        {JSON.stringify(filteredData) !== '[]' ?
                            nonlabcell
                            :
                            <div style={{ display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                                <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                    無資料
                                </div>
                            </div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default Examnonlabviewtable
