import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './RTbreathtablecell.scss'
// import * as FiliterReg from '../../Function/FiliterReg'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

class RTbreathetablecell extends Component { 
    state = {
        onCell: false,
        onEdit: false,
        remark: '',
        powerLogin: 0
    }
    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }
    Mouse_move() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: true
                }
            )
        }
    }

    Mouse_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: false
                }
            )
        }
    }

    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue)
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    }

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    seachRemark = () => {
        const { caseinfo, value } = this.props
        const id = `Breath${moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()}`
        ApiProvider.getOneRemark(caseinfo.TRANSINID, id, "RTbreath").then(result => {
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    render() {
        const { value, caseinfo, isSameDay } = this.props
        const id = `Breath${moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()}`
        const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").format("MM/DD HH:mm")
        const mode = value.Mode.toString().trim()
        const setting = value.Setting

        console.log(setting)

        const remarkValue = {
            "createTime": time,
            "name": setting,
            "remarkId": id,
            "remark": this.state.remark,
            "transinId": caseinfo.TRANSINID,
            "type": "RTbreath",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <div onMouseMove={() => this.Mouse_move()} onMouseLeave={() => this.Mouse_leave()} className="RTtablecell" style={{ minheight: this.state.remark === '' ? '40px' : '60px', borderBottom:  isSameDay ? "rgba(238, 238, 238, 1) 1px solid" : '#989898 1px solid'  }}>
                <t3 className='RTBreathDateCell' style={{ minheight: this.state.remark === '' ? '40px' : '60px' }}>{time}</t3>
                <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === '' ? "1fr" : "1fr 20px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 3fr" }}>
                        <div style={{ display: "flex", alignItems: "center", justifyContent: "center", borderCollapse: "collapse", borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>
                            <t2 style={{ fontWeight: 600, color: 'black' }}>
                                {mode}
                            </t2>
                        </div>
                        <div style={{ display: "flex", alignItems: "center", borderCollapse: "collapse", borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>
                            <t2 style={{ fontWeight: 600, color: 'black' }}>
                                {setting}
                            </t2>
                        </div>
                    </div>
                    <t3 style={{ fontWeight: 600, alignItems: "center", borderCollapse: "collapse", color: "rgb(0,120,0)", overflow: "hidden", height: "20px", display: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "none" : ""], borderLeft: "rgba(238, 238, 238, 1) 1px solid", borderTop: "rgba(238, 238, 238, 1) 1px solid" }}>{this.state.remark}</t3>
                    <div style={this.state.onCell ?
                        { display: this.state.powerLogin == 1 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: this.state.remark === '' ? "100%" : "100%", background: "transparent", width: "77px" }
                        :
                        { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: this.state.remark === '' ? '30px' : '40px', height: this.state.remark === '' ? '30px' : '40px', borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}

export default RTbreathetablecell
