import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtionfeedingcell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider';

class Nurtionfeedingcell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        powerLogin: 0,
        remark: '',
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { caseinfo, data } = this.props
        ApiProvider.getOneRemark(caseinfo.TRANSINID, data.ORDSEQNO, "nurtionfeeding").then(result => {
            console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    };
    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    Moduledisplay(PName, ST) {
        const remarkValue = {
            "createTime": ST,
            "name": PName,
            "remarkId": this.props.data.ORDSEQNO,
            "remark": this.state.remark,
            "transinId": this.props.caseinfo.TRANSINID,
            "type": "nurtionfeeding",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
        );
    }

    render() {
        const { data } = this.props
        const startTime = moment(data.ORBGNDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const endTime = moment(data.ORENDDT.toString().trim(), "YYYY-MM-DD").format("MM/DD")
        const name = data.ORPROCED.toString().trim()

        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} className="remark">
                <div className='nurtionfeedingcell' style={{ gridTemplateRows: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "1fr" : "1fr 1fr"] }}>
                    <div className='cellforstate'>
                        <div className='celllayout'>
                            <t3>{startTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{endTime}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{name}</t3>
                        </div>
                    </div>
                    <div className='cellforremark' style={{ display: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "none" : "flex"] }}>
                        <t3>{this.state.remark}</t3>
                    </div>
                </div>
                <div style={this.state.EditMode ? {
                    display: this.state.powerLogin == 1 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", top: 0, right: 0, height: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "50px" : "100px"], background: "white", width: "77px", border: "rgba(232, 232, 232, 1) 1px solid"
                } : { display: "none" }}>
                    < div onClick={() => this.showModal()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }} >
                        <img src={chooseedit} alt='chooseedit'></img>
                    </div>
                </div>
                {this.Moduledisplay(name, startTime)}
            </div >
        );
    }
}

export default Nurtionfeedingcell
