import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Prescriptionordertablecell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

class Prescriptionordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false,
        remark: '',
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { caseinfo, value } = this.props
        ApiProvider.getOneRemark(caseinfo.TRANSINID, value.UDORDSEQ, "prescription").then(result => {
            console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    };

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    checkStatus = () => {
        const { value } = this.props
        const valueStatus = Number(value.UDSTATUS)
        if (valueStatus === 80) {
            return "已取消"
        }
        if (valueStatus === 30) {
            return "生效中"
        }
        if (valueStatus === 60) {
            return "已完成"
        }
        return valueStatus
    }

    render() {
        const { caseinfo, value } = this.props
        const Name = value.UDGNAME.toString()
        const PName = value.UDRPNAME.toString()
        const Unit = value.UDDOSAGE.toString().trim() + " " + value.UDDUNIT.toString().trim() + "-" + value.UDFREQN.toString().trim() + "-" + value.UDROUTE.toString().trim()
        const ST = moment(value.UDBGNDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
        const ET = moment(value.UDENDDT, "YYYY-MM-DD-HH:mm:ss.000000").format('MM/DD HH:mm')
        const Status = this.checkStatus()

        const remarkValue = {
            "createTime": ST,
            "name": PName,
            "remarkId": value.UDORDSEQ,
            "remark": this.state.remark,
            "transinId": caseinfo.TRANSINID,
            "type": "prescription",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className='prescriptiontablecell' style={{ minheight: this.state.remark === '' ? '40px' : '60px', borderTop: '#989898 1px solid', borderBottom: "#989898 1px solid", borderCollapse: "collapse" }}>
                <t3 className='prescriptiontablecellcontexttext' style={{ minheight: this.state.remark === '' ? '40px' : '1fr' }}>{ST} - {ET}</t3>
                <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "1fr" : "1fr 20px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 140px 70px", minHeight: "40px" }}>
                        <div className='prescriptiontablecellcontext'>
                            <t2 className='prescriptiontablecellcontexttext' style={{ color: "black", fontWeight: 600 }}>{Name}</t2>
                        </div>
                        <div className='prescriptiontablecellcontext'>
                            <t3 className='prescriptiontablecellcontexttext'>{Unit}</t3>
                        </div>
                        <div className='prescriptiontablecellcontext'>
                            <t3 className='prescriptiontablecellcontexttext'>{Status}</t3>
                        </div>
                    </div>
                    <t3 style={{ fontWeight: 600, alignItems: "center", borderTop: "rgba(238,238,238,1) 1px solid", borderLeft: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px", color: "rgb(0,120,0)", overflow: "hidden", height: "20px", display: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "none" : ""] }}>{this.state.remark}</t3>
                    <div style={this.state.onMove ?
                        { display: this.state.powerLogin == 1 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: [this.state.remark === undefined || this.state.remark === "" ? "40px" : "60px"], background: "transparent", width: "77px" }
                        :
                        { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: this.state.remark === '' ? '30px' : '40px', height: this.state.remark === '' ? '30px' : '40px', borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}

export default Prescriptionordertablecell
