import React, { Component } from 'react';
import { wardstatestringformat } from '../Function/Wardstate'
import './Wardcardstatus.scss'
import * as ApiProvider from '../API/ApiProvider'
import * as ApiGithub from '../API/ApiGithub'
// import moment from 'moment';
// import * as RegularData from '../Data/RegularData'

class Wardcarstatus extends Component {
    state = {
        TRTMNTQ1: [],
        UDORDER0: [],
        FlagReg: [],

        preCID: null,
        preHID: null
    }
    componentDidMount() {
        const { PCASENO, PHISTNUM } = this.props
        this.setState({ preCID: PCASENO, preHID: PHISTNUM })
        this.ApiGet(PCASENO, PHISTNUM)

    }
    componentDidUpdate() {
        const { PCASENO, PHISTNUM } = this.props
        const { preCID, preHID } = this.state
        if (preCID !== PCASENO || preHID !== PHISTNUM) {
            this.setState({ preCID: PCASENO, preHID: PHISTNUM })
            this.ApiGet(PCASENO, PHISTNUM)
        }
    }
    ApiGet(PCASENO, PHISTNUM) {
        const TRQ1 = ApiProvider.getStoredProcedureTRTMNTQ1(PHISTNUM, PCASENO)
            .then(data => {
                const rowList = data.data
                const filterStatusList = rowList.filter(function (info, index, arr) {
                    return Number(info.ORSTATUS) === 30 || Number(info.ORSTATUS) === 90
                })
                return filterStatusList
            })
            .catch(err => {

            })
        const UDORDER = ApiProvider.getStoredProcedureUDORDER0(PHISTNUM, PCASENO, "123")
            .then(data => {
                var List = []
                const dataList = data.data
                if (Array.isArray(dataList)) {
                    for (let index = 0; index < dataList.length; index++) {
                        const status = Number(dataList[index].UDSTATUS)
                        if (status === 30) {
                            List.push(dataList[index])
                        }
                    }
                }
                return List
            })
            .catch(err => {

            })
        const Reg = ApiGithub.getGitHubJson("FlagReg.json")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log(err)
            })
        Promise.all([TRQ1, UDORDER, Reg])
            .then(result => {
                this.setState({
                    TRTMNTQ1: result[0],
                    UDORDER0: result[1],
                    FlagReg: result[2]
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    render() {
        function RespiratorStatusParse(InvasiveRespiratorStatus, NonInvasiveRespiratorStatus) {
            console.log(InvasiveRespiratorStatus, NonInvasiveRespiratorStatus)
            if (InvasiveRespiratorStatus === false && NonInvasiveRespiratorStatus === false) {
                return "false"
            }
            if (InvasiveRespiratorStatus === true && NonInvasiveRespiratorStatus === true) {
                return "false"
            }
            if (InvasiveRespiratorStatus === true) {
                return '呼(侵入)'
            }
            if (NonInvasiveRespiratorStatus === true) {
                return "呼(非侵入)"
            }
            return "--"
        }

        function parseFlagData(TRTMNTQ1, UDORDER, FlagReg, type) {
            //filter  status 生效中
            if (FlagReg === undefined || FlagReg === null) { return null }
            const Flag = FlagReg.filter(function (value, index, arr) {
                return value.item === type
            })
            // if (type === "InvasiveRespirator") {
            //     console.log(Flag)

            // }
            // if (type === "NonInvasiveRespirator") {
            //     console.log(Flag)

            // }
            if (Flag.length === 0) {
                return null
            }
            const StatusUDORDER = UDORDER.some(function (value, index, arr) {
                const UDDRGNAM = value.UDDRGNAM.toLowerCase()
                const UDGNAME = value.UDGNAME.toLowerCase()
                const findFlag = Flag[0].ParseValueUD.some(function (value, index, arr) {
                    return UDDRGNAM.indexOf(value) >= 0 || UDGNAME.indexOf(value)
                })
                return findFlag
            })
            const StatusTRTMNTQ1 = TRTMNTQ1.some(function (value, index, arr) {
                const ORPROCED = value.ORPROCED.toLowerCase()
                const findFlag = Flag[0].ParseValueTRT.some(function (value, index, arr) {
                    return ORPROCED.match(value) !== null
                    // return value
                })
                return findFlag
            })
            if (StatusUDORDER === false && StatusTRTMNTQ1 === false) {
                return false
            }
            return true
        }

        function statusspan(source) {
            if (source.item === 'Respirator') {
                return 'gridtwocoltwo'
            }
            else return 'gridtwocolone'
        }
        function style_change(source) {
            if (source.item === 'Respirator') {
                if (source.data === '呼(侵入)') {
                    return 'statustrue'
                }
                if (source.data === '呼(非侵入)') {
                    return 'statustrue'
                }
                if (source.data === 'true') {
                    return 'statustrue'
                }
                return 'statusfalse'

            }
            else {
                if (source.data) {
                    return 'statustrue'
                }
                else {
                    return 'statusfalse'
                }
            }
        }

        const UDORDER = this.state.UDORDER0
        const TRTMNTQ1 = this.state.TRTMNTQ1


        const Flag = this.state.FlagReg


        const AntiStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "Anti")
        const TPNStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "TPN")
        const NP0Status = parseFlagData(TRTMNTQ1, UDORDER, Flag, "NPO")
        const FoleyStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "Foley")
        const DrainStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "Drain")
        const InvasiveRespiratorStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "InvasiveRespirator")
        const NonInvasiveRespiratorStatus = parseFlagData(TRTMNTQ1, UDORDER, Flag, "NonInvasiveRespirator")
        const RespiratorStatus = RespiratorStatusParse(InvasiveRespiratorStatus, NonInvasiveRespiratorStatus)

        const StatusArray = [
            { "item": "Respirator", "data": RespiratorStatus },
            { "item": "CVC", "data": FoleyStatus },
            { "item": "NPO", "data": NP0Status },
            { "item": "TPN", "data": TPNStatus },
            { "item": "Abx", "data": AntiStatus },
            { "item": "Xanth", "data": [] },
            { "item": "照光", "data": [] },
            { "item": "DNR", "data": DrainStatus }
        ]

        const statuslist = StatusArray.map(function (info, index, arr) {
            return (
                <div key={index} className={statusspan(info)}>
                    <t4 className={style_change(info)}>{wardstatestringformat(info)}</t4>
                </div>
            );
        })
        const source = this.props.source
        return (
            <div className={source ? "warscardstatusgrid" : "wardinfouserstatusgrid"}>
                {statuslist}
            </div >
        );
    }
}

export default Wardcarstatus
