import React, { Component } from 'react';
import * as d3 from "d3";
import { growdata } from '../jsonResponse'
import moment from 'moment';

//40週前
class WardlifeChart extends Component {
    state = {
        preWeight: null,
        preWeek: null,
        preday: null
    }
    componentDidMount() {

        const { weightList, svg_key, grewinfo } = this.props
        console.log(grewinfo)
        if (grewinfo !== undefined) {
            if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined) {
                if (weightList !== undefined) {
                    this.setState({ preWeight: weightList.length, preWeek: grewinfo.WEEK_BORN, preday: grewinfo.DAY_BORN })
                    d3.select("#" + svg_key).remove()
                    this.drawChart();
                }
            }
        }
    }
    componentDidUpdate() {
        const { weightList, svg_key, grewinfo } = this.props
        const { preWeight, preWeek, preday } = this.state
        console.log(grewinfo)
        if (grewinfo !== undefined) {
            if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined) {
                if (weightList !== undefined) {
                    if (weightList.length !== preWeight || grewinfo.WEEK_BORN !== preWeek || grewinfo.DAY_BORN !== preday) {
                        this.setState({ preWeight: weightList.length, preWeek: grewinfo.WEEK_BORN, preday: grewinfo.DAY_BORN })
                        d3.select("#" + svg_key).remove()
                        this.drawChart();
                    }
                }
            }
        }

    }

    drawChart() {
        const { gender, svg_key, weightList, grewinfo, width, height, id_key } = this.props
        if (grewinfo === null || grewinfo === undefined) {
            return null
        }
        console.log(grewinfo)
        const bornweeks = Number(grewinfo.WEEK_BORN)
        const borndays = Number(grewinfo.DAY_BORN)
        const Birthday = grewinfo.BIRTHDAY
        const pregencyTimestamp = moment(Birthday, "YYYYMMDD").valueOf() - ((bornweeks * 7) + borndays) * 24 * 60 * 60 * 1000


        function tickValues(growdata, gender) {
            if (gender === 'M' || gender === '1') {
                const Row = growdata.Male_befor
                return [Row[Row.length - 1].min, Row[Row.length - 1].max]
            }
            const Row = growdata.FeMale_before
            return [Row[Row.length - 1].min, Row[Row.length - 1].max]
        }

        const tick = tickValues(growdata, gender)

        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([0, 5500])
            .range([height, 0])

        var x = d3.scaleLinear()
            .domain([22, 44])
            .range([0, width])


        var yAxis = d3.axisLeft(y)
            .ticks(10)
            .tickSize(0, 0)
            .tickPadding(10)
            .tickFormat(function (d, index) { return index === 11 ? d + "(g)" : d });


        var xAxis = d3.axisTop(x)
            .ticks(14)
            .tickSize(0, 0)
            .tickPadding(-15)
            .tickFormat(function (d, index) { return d + 'W'; });

        var yRightAxis = d3.axisLeft(y)
            .tickValues(tick)
            .tickSize(0, 0)
            .tickPadding(-40)
            .tickFormat(function (d, index) {
                if (index === 0) {
                    return "10th%"
                }
                if (index === 1) {
                    return "90th%"
                }
                return null
            });
        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .style("height", "435px")
            .style("width", "100%")
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 30 + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");



        svg.append("g")
            .call(xAxis)
            .attr("class", "axis")
            .attr("transform", "translate(" + 0 + ", " + height + ")");

        svg.append("g")
            .call(yRightAxis)
            .attr("class", "axis")
            .attr("transform", "translate(" + width + ", " + 0 + ")");

        for (let index = 0; index < 13; index++) {
            const number = index * 500
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(number))
                .attr('x2', width)
                .attr('y2', y(number))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);

        }

        // for (let index = 0; index < 13; index++) {
        //     const number = index * 500
        //     svg.append('line')
        //         .attr('x1', 0)
        //         .attr('y1', y(number))
        //         .attr('x2', width)
        //         .attr('y2', y(number))
        //         .style('stroke', 'rgba(187, 187, 187, 1)')
        //         .style('stroke-width', 1)
        //         .style('stroke-dasharray', 5.5);
        // }

        for (let index = 22; index < 45; index++) {
            if (index % 2 == 0) {
                svg.append('line')
                    .attr('x1', x(index))
                    .attr('y1', 0)
                    .attr('x2', x(index))
                    .attr('y2', height)
                    .style('stroke', 'rgba(187, 187, 187, 1)')
                    .style('stroke-width', 1)
                    .style('stroke-dasharray', 5.5);
            }
        }

        if (gender === 'M' || gender === '1') {
            for (let index = 0; index < growdata.Male_befor.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_befor[index].y))
                    .attr('y1', y(growdata.Male_befor[index].min))
                    .attr('x2', x(growdata.Male_befor[index + 1].y))
                    .attr('y2', y(growdata.Male_befor[index + 1].min))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }

            for (let index = 0; index < growdata.Male_befor.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_befor[index].y))
                    .attr('y1', y(growdata.Male_befor[index].max))
                    .attr('x2', x(growdata.Male_befor[index + 1].y))
                    .attr('y2', y(growdata.Male_befor[index + 1].max))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }
        }
        else {
            for (let index = 0; index < growdata.FeMale_before.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_before[index].y))
                    .attr('y1', y(growdata.FeMale_before[index].min))
                    .attr('x2', x(growdata.FeMale_before[index + 1].y))
                    .attr('y2', y(growdata.FeMale_before[index + 1].min))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }

            for (let index = 0; index < growdata.FeMale_before.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_before[index].y))
                    .attr('y1', y(growdata.FeMale_before[index].max))
                    .attr('x2', x(growdata.FeMale_before[index + 1].y))
                    .attr('y2', y(growdata.FeMale_before[index + 1].max))
                    .style('stroke', '#001a6dbb')
                    .style('stroke-width', 3);
            }
        }
        const datasource = weightList
        //x 週數 y體重 NIS
        for (let index = 0; index < datasource.length; index++) {
            const element = datasource[index];
            const BornDay = element.rec_dat.toString().trim()
            const BornTime = element.rec_tim.toString().trim()
            const time = BornDay + BornTime
            const weighttimestamp = moment(time, "YYYYMMDDHHmm").valueOf() //體重時間
            const weeks = (weighttimestamp - pregencyTimestamp) / (7 * 24 * 60 * 60 * 1000) //體重週數
            // console.log(weeks)
            if (weeks < 22) {
                continue
            }
            if (weeks > 44) {
                continue
            }
            svg.append('circle')
                .attr('cx', x(weeks))
                .attr('cy', y(Number(element.value2) * 1000))
                .attr('r', 1.5)
                .style('fill', 'green')
        }


    }

    render() {
        const { id_key, svg_key } = this.props
        return (
            <div id={id_key}>

            </div>
        );
    }
}

export default WardlifeChart;
