import React, { Component } from 'react'
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'
import * as ApiPronider from '../../API/ApiProvider'
import Chronictablecell from './ChronicTableCell'
import * as RSData from '../../Resource/RESSECT'

class Tableonchronic extends Component {
    state = {
        LabParse: [],
        SummaryParse: [{ apply_condition: "", mappings: [] }],
        RESSECT: [],
        preHID: null
    }    

    ApiGet(HID, monthly) {
        ApiGithub.getGitHubJson("Chronic_Lab.json")
            .then(data => {
                this.setState({
                    LabParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
        ApiGithub.getGitHubJson("Summary.json")
            .then(data => {
                this.setState({
                    SummaryParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
        ApiPronider.getStoredProcedureRESSECT(HID, "1", "ALL", "DOC3924B")
            .then(data => {
                this.setState({ RESSECT: data.data })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        // this.setState({ RESSECT: RSData.data })
    }
    componentDidMount() {
        const { caseinfo, PickerDate, chronic_state } = this.props
        const HID = caseinfo.HISID
        const monthly = this.fotmatTimerMonthly(PickerDate)
        this.setState({ preHID: caseinfo.HISID, prePickerDate: PickerDate, preInterval: chronic_state })
        this.ApiGet(HID, monthly)
    }
    componentDidUpdate() {
        const { caseinfo, PickerDate, chronic_state } = this.props
        const HID = caseinfo.HISID
        const { preHID, prePickerDate, preInterval } = this.state
        const monthly = this.fotmatTimerMonthly(PickerDate)
        if (HID !== preHID || prePickerDate !== PickerDate || preInterval !== chronic_state) {
            this.setState({ preHID: caseinfo.HISID, prePickerDate: PickerDate, preInterval: chronic_state })
            this.ApiGet(HID, monthly)
        }
    }

    fotmatTimerMonthly(pickerdate) {
        const timestampInterval = moment().valueOf() - pickerdate
        const timestampMonth = 30 * 24 * 60 * 60 * 1000
        const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
        return MonthInt
    }
    switch_state_title() {
        return "140px"
    }
    render() {
        function DataForTimer(dataList, interval, pickerdate) {
            function starttime(interval, entTimeStamp) {
                if (interval === 0) {
                    return entTimeStamp - (24 * 60 * 60 * 1000)
                }
                if (interval === 1) {
                    return entTimeStamp - (3 * 24 * 60 * 60 * 1000)
                }
                if (interval === 2) {
                    return entTimeStamp - (7 * 24 * 60 * 60 * 1000)
                }
                return entTimeStamp - (14 * 24 * 60 * 60 * 1000)
            }

            function fotmatTimerForTimer(interval, st, list, title, grid) {
                var TimerForList = []
                for (let index = 0; index < interval; index++) {
                    const StartTime = st + (index * grid * 60 * 60 * 1000)
                    const EndTime = st + ((index + 1) * grid * 60 * 60 * 1000)
                    const filterTimerForDataList = list.filter(function (element, index, arr) {
                        const time = element.ORRCPDT.toString().trim() + element.ORRCPTM.toString().trim()
                        const timestamp = moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf()
                        return StartTime <= timestamp && timestamp < EndTime
                    })
                    TimerForList.push({
                        StartTime: StartTime,
                        EndTime: EndTime,
                        element: filterTimerForDataList,
                        title: title
                    })
                }
                return TimerForList
            }
            const et = pickerdate + (31 * 60 * 60 * 1000)
            const st = starttime(interval, et)
            const slectInt = TimeInterval(interval)
            const DataTransForTimer = fotmatTimerForTimer(slectInt.interval, st, dataList.array, dataList.title, slectInt.grid)
            return DataTransForTimer
        }



        function LabFormat(ressect, parsrList) {
            var arrayBiochemical = []
            var arrayBlood = []
            var arrayCultivate = []
            var arrayGAS = []
            // var arrayGlu = []
            var arrayRadiation = []
            var arrayOther = []

            for (let index = 0; index < ressect.length; index++) {
                const element = ressect[index];
                const element_ORPROCED = element.ORPROCED
                const BioFind = parsrList.Biochemical.some(function (value, index, arr) {
                    return element_ORPROCED.match(value) !== null
                })
                const BloFind = parsrList.Blood.some(function (value, index, arr) {
                    return element_ORPROCED.match(value) !== null
                })
                const CulFind = parsrList.Cultivate.some(function (value, index, arr) {
                    return element_ORPROCED.match(value) !== null
                })
                const GasFind = parsrList.GAS.some(function (value, index, arr) {
                    return element_ORPROCED.match(value) !== null
                })
                // const GluFind = parsrList.GAS.some(function (value, index, arr) {
                //     return element_ORPROCED.match(value) !== null
                // })
                const RadFind = parsrList.Radiation.some(function (value, index, arr) {
                    return element_ORPROCED.match(value) !== null
                })
                if (BioFind) {
                    arrayBiochemical.push(element)
                }
                if (BloFind) {
                    arrayBlood.push(element)
                }
                if (CulFind) {
                    arrayCultivate.push(element)
                }
                if (GasFind) {
                    arrayGAS.push(element)
                }
                // if (GluFind) {
                //     arrayGlu.push(element)
                // }
                if (RadFind) {
                    arrayRadiation.push(element)
                }
                if (BioFind === false && BloFind === false && CulFind === false && GasFind === false && RadFind === false) {
                    arrayOther.push(element)
                }
            }
            const arrayList = []
            arrayList.push({
                title: "生化",
                array: arrayBiochemical,
            })
            arrayList.push({
                title: "血液",
                array: arrayBlood,
            })
            arrayList.push({
                title: "培養",
                array: arrayCultivate,
            })
            arrayList.push({
                title: "GAS",
                array: arrayGAS,
            })
            // arrayList.push({
            //     title: "Glu",
            //     array: arrayGlu,
            // })
            arrayList.push({
                title: "放射",
                array: arrayRadiation,
            })
            arrayList.push({
                title: "其他",
                array: arrayOther,
            })
            return arrayList
        }
        function TimeLine(interval, index) {
            const lineStyle = {
                width: '',
                color: '',
            }
            if (interval === 0) {
                if (index === 0 || (index % 24) === 0) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                } else if (index % 8 === 0) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                }
                else {
                    lineStyle.width = '1px';
                    lineStyle.color = '#eeeeee';
                    return lineStyle;
                }
            }
            if (interval === 1) {
                if (index === 0 || (index % 6) === 0) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                } else if (index % 2 === 0) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                }
                else {
                    lineStyle.width = '1px';
                    lineStyle.color = '#eeeeee';
                    return lineStyle;
                }
            }
            if (interval === 2) {
                if (index === 0 || (index % 3) === 0) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                } else {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                }
            }
            if (interval === 3) {
                lineStyle.width = '2px';
                lineStyle.color = 'rgba(0,0,0,0.2)';
                return lineStyle;
            }
        }
        function TimeInterval(interval) {
            if (interval === 0) {
                return { interval: 24, grid: 1 }
            }
            if (interval === 1) {
                return { interval: 18, grid: 4 }
            }
            if (interval === 2) {
                return { interval: 21, grid: 8 }
            }
            return { interval: 14, grid: 24 }
        }
        const { chronic_state, caseinfo, PickerDate, title, openTimeline } = this.props
        const { LabParse, RESSECT, SummaryParse } = this.state
        if (LabParse === null || LabParse === undefined || LabParse.length === 0) {
            return null
        }
        if (RESSECT === null || RESSECT === undefined || RESSECT.length === 0) {
            return null
        }
        const data = LabFormat(RESSECT, LabParse)
        const statestatus = TimeInterval(chronic_state)
        const title_list = title.map((info, index) =>
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "40px", border: "1px solid rgba(238,238,238,1)", borderCollapse: "collapse", backgroundColor: "#eeeeee" }} key={index}>
                <t2>{info.title}</t2>
            </div>
        )
        const cell_list = data.map(function (value, index, arr) {
            const DataList = DataForTimer(value, chronic_state, PickerDate)
            return DataList
        })
        const cellRender = cell_list.map(function (value, index, array) {
            const valuecell = value.map(function (element, index, arr) {
                const toolTipID = "ChronicTableTootip" + index
                return (
                    <Chronictablecell lineColor={openTimeline ? TimeLine(chronic_state, index) : null} key={toolTipID} element={element} caseinfo={caseinfo} summaryParse={SummaryParse}></Chronictablecell>
                );
            })
            return valuecell
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {title_list}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + statestatus.interval + ",1fr)", gridTemplateRows: "repeat(6,1fr)" }}>
                    {cellRender}
                </div>
            </div>
        );
    }
}

export default Tableonchronic
