import moment from 'moment';

function HISAPI_stringtodate(datestring) {
    function addzero(value) {
        if (value < 10) {
            return "0" + value
        }
        return value
    }
    const datenumber = Number(datestring)
    const dateformat = parseInt(datenumber / 10000) + '-' + parseInt(parseInt(datenumber / 100) % 100) + '-' + addzero(parseInt(datenumber % 100))
    return dateformat
}

function yyyymmddformat(timestamp) {
    function Monthformat(month) {
        const formatmonth = month + 1
        if (formatmonth < 10) {
            return "0" + formatmonth
        }
        else return formatmonth
    }
    function Dateformat(date) {
        if (date < 10) {
            return "0" + date
        }
        else return date
    }
    return new Date(timestamp).getFullYear() + "/" + Monthformat(new Date(timestamp).getMonth()) + "/" + Dateformat(new Date(timestamp).getDate())
}

