function weekdateformat(timestamp) {
    const param = new Date(timestamp).getDay()
    const weekarray = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
    return weekarray[param]
}

function weekdateformatcalender(timestamp) {
    const param = new Date(timestamp).getDay()
    const weekarray = ['日', '一', '二', '三', '四', '五', '六']
    return weekarray[param]
}

function hhmmssformat(timestamp) {
    function addZero(i) {
        if (i < 10) {
            i = "0" + i;
        }
        return i;
    }
    return addZero(new Date(timestamp).getHours()) + ':' + addZero(new Date(timestamp).getMinutes())
}

function hhmmssformattohalf(timestamp) {
    function addZero(i) {
        if (i < 10) {
            i = "0" + i;
        }
        return i;
    }
    return addZero(new Date(timestamp).getHours()) + ':30:00'
}


function yyyymmddHHmmfromat(timestamp) {
    function Monthformat(month) {
        const formatmonth = month + 1
        if (formatmonth < 10) {
            return "0" + formatmonth
        }
        else return formatmonth
    }
    function Dateformat(date) {
        if (date < 10) {
            return "0" + date
        }
        else return date
    }
    function addZero(i) {
        if (i < 10) {
            i = "0" + i;
        }
        return i;
    }
    return new Date(timestamp).getFullYear().toString() + Monthformat(new Date(timestamp).getMonth()).toString() + Dateformat(new Date(timestamp).getDate()).toString() + addZero(new Date(timestamp).getHours()).toString() + addZero(new Date(timestamp).getMinutes()).toString()
}

function MMDDhhmmfromat(timestamp) {
    function Monthformat(month) {
        const formatmonth = month + 1
        if (formatmonth < 10) {
            return "0" + formatmonth
        }
        else return formatmonth
    }
    function Dateformat(date) {
        if (date < 10) {
            return "0" + date
        }
        else return date
    }
    function addZero(i) {
        if (i < 10) {
            i = "0" + i;
        }
        return i;
    }
    return Monthformat(new Date(timestamp).getMonth()) + '/' + Dateformat(new Date(timestamp).getDate()) + ' ' + addZero(new Date(timestamp).getHours()) + ':' + addZero(new Date(timestamp).getMinutes())
}


function daysdiff(newtime, oldtime) {
    const timestampdiff = new Date(newtime).getTime() - new Date(oldtime).getTime()
    const dissdays = timestampdiff / (24 * 60 * 60 * 1000)
    return parseInt(dissdays)
}


function disabledDate(current) {
    // Can not select days before today and today
    return current && current < moment().endOf('day');
}

function compareday(date) {
    if (new Date(date).getTime() > new Date().getTime()) {
        return true
    }
    else {
        return false
    }
}


function formatxArrayhalf(time, interval) {
    var xAxisarray = []
    if (interval === 24) {
        for (let index = 0; index < 144; index++) {
            xAxisarray.push(time + (index * 10 * 60 * 1000))
        }
    }
    if (interval === 18) {
        for (let index = 0; index < 18; index++) {
            xAxisarray.push(time + (60 * 60 * 1000 * 2) + (index * 60 * 60 * 1000 * 4))
        }
    }

    if (interval === 21) {
        for (let index = 0; index < 21; index++) {
            xAxisarray.push(time + (60 * 60 * 1000 * 4) + (index * 60 * 60 * 1000 * 8))
        }
    }
    if (interval === 14) {
        for (let index = 0; index < 14; index++) {
            xAxisarray.push(time + (60 * 60 * 1000 * 12) + (index * 60 * 60 * 1000 * 24))
        }
    }
    return xAxisarray
}


function formatendtime(time) {
    const endtime = new Date(yyyymmddformat(time)).getTime() + (31 * 60 * 60 * 1000)
    return endtime
}

function formatintervaldays(days, interval) {
    //false:一天 true:三天
    if (interval === 1) {
        return days - (24 * 60 * 60 * 1000) * 1

    }
    if (interval === 3) {
        return days - (24 * 60 * 60 * 1000) * 3

    }
    else {
        return null
    }
}

