import React, { Component } from 'react';
import { Button, DatePicker, Checkbox } from 'antd';
import moment from 'moment';
import displaylogo from '../Image/svg/filter.svg'
import { daysdiff } from '../Function/Timeformat'
import { pregancyweeksanddays } from '../Function/Wardbedfunction'
import './Controlbar.scss'
// import * as ApiProvider from '../API/ApiProvider';
import * as WardState from '../Function/Wardstate'
import locale from 'antd/es/date-picker/locale/zh_TW';


class Controlbar extends Component {
    state = {
        display_Mode: false,
        item: this.props.item_array,
        scrollstate: 0,
    }
    sendData = (itwm_array) => {
        this.props.parentCallback(itwm_array);
    }


    sendDateData = (PickerDate) => {
        this.props.parentDateCallback(PickerDate);
    }
    //快速滑動
    scrollToAnchor = (anchorName) => {
        if (anchorName) {
            let anchorElement = document.getElementById(anchorName);
            if (anchorElement) { anchorElement.scrollIntoView(); }
        }
    }
    onChange = (checkedValues) => {
        this.sendData(checkedValues)

    }
    onDateChange = (date, dateString) => {
        this.sendDateData(date.valueOf())
    }
    open_display() {
        this.setState({
            display_Mode: !this.state.display_Mode
        })
    }

    trainDay = (e) => {
        const { caseinfo, PickerDate } = this.props
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = moment(moment(bTime).format('YYYYMMDD')).valueOf()
                bronDate <= bTemp && this.sendDateData(bTime)
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD')).valueOf()
                this.sendDateData(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const atemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= atemp && this.sendDateData(aTime)
                break;
            default:
                break;
        }
    }

    render() {

        const btn_style = {
            unclick_style: { borderRadius: "4px", color: "#001a6d88", border: "1px solid #001a6d88", padding: "0px 4px", height: "30px" }
        }

        function Monthformat(month) {
            return month + 1
        }
        function timeformat(timestring) {
            return new Date(timestring).getFullYear() + "/" + Monthformat(new Date(timestring).getMonth()) + "/" + new Date(timestring).getDate()
        }
        function valueFormat(value) {
            if (value === "" || value === null || value === undefined) {
                return "--"
            }
            return value.toString()
        }
        function valueBornWeight(value) {
            if (value === "" || value === null || value === undefined || value === 0) {
                return ""
            }
            return value.toString() + "g → "
        }
        const display_style = {
            displye_mode: { position: "absolute", right: '0px', top: "50px", width: "200px", background: "white", boxShadow: "3px 3px 12px", zIndex: "100", borderRadius: "4px" },
            non_display_mode: { display: "none" },
            check_area: { display: "flex", justifyContent: "center", alignItems: "center" },
            check_text: { padding: "4px", width: "140px", borderRadius: "4px", borderColor: 'rgba(0, 0, 0, 0.15)', borderStyle: "solid", borderWidth: "1px", marginLeft: "10px", borderRadius: "4px" }
        }

        function disabledDate(current) {
            // Can not select days before today and today
            const endvalue = moment(caseinfo.PBASINGNINFO.PBIRTHDT, 'YYYYMMDD')
            return endvalue.endOf('day') > current || current > moment().endOf('day')
        }

        const { caseinfo, btnlist, PickerDate, status } = this.props
        // console.log(AgeStatus)
        const weightList = caseinfo.WEIGHT
        const Birthday = caseinfo.PBASINGNINFO.PBIRTHDT
        const btn = btnlist.map(
            (item, index) => <Button onClick={this.scrollToAnchor.bind(this, item.scrollpoint)} key={index} style={btn_style.unclick_style}>
                <t3>{item.name}</t3>
            </Button>
        )
        return (
            // <div>
            <div className='controlbarlayout'>
                <div style={{ display: "grid", gridTemplateColumns: "auto auto auto", gridColumnGap: "30px" }}>
                    <DatePicker getCalendarContainer={triggerNode => triggerNode.parentNode} locale={locale} width={140} onChange={this.onDateChange} value={moment(PickerDate)} allowClear={false} showToday={false} disabledDate={(current) => { return current > moment().startOf("day") || current < moment(Birthday, "YYYYMMDD").add(-1, "days").endOf("day") }} />
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "2px" }}>
                        <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                        <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                        <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                    </div>
                    <div style={{ display: 'grid', gridTemplateColumns: "repeat(3,auto)", gridColumnGap: "30px", alignContent: "center" }}>
                        {/* <t2>矯正週數：{pregancyweeksanddays(caseinfo.WEEK_BORN, caseinfo.DAY_BORN, moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD'))}</t2>
                        <t2>體重：{valueFormat(DayWeight)}g</t2>
                        <t2>住院第{daysdiff(moment(PickerDate).format('YYYY/MM/DD'), moment(caseinfo.TRANSINDT, 'YYYYMMDD').format('YYYY/MM/DD'))}天</t2> */}
                    </div>
                </div>
                <div style={{ fontSize: "16px" }}>
                    當日年齡: {WardState.bornstatusFormat(status.week,status.day)} <spam style={{ fontWeight: "600", color: "black" }}>{WardState.CalculateAgeFromPickerDate(status, PickerDate)}</spam>
                </div>
                <div style={{ fontSize: "16px" }}>
                    當日體重: {WardState.valueFormat(status.BornWeight)}<spam style={{ fontWeight: "600", color: "black" }}>{WardState.WeightListFilter(weightList,PickerDate)}g</spam>
                </div>
                <div style={{ display: "flex", alignItems: 'center', position: "relative" }}>
                    <t2>快速切換：</t2>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(5,auto)", gridColumnGap: "5px" }}>{btn}</div>
                    <div onClick={() => this.open_display()} style={{ cursor: "pointer", marginLeft: "5px" }}>
                        <img src={displaylogo} alt='displaylogo' style={{ width: '15px', height: '15px' }}></img>
                    </div>
                    <div style={this.state.display_Mode ? display_style.displye_mode : display_style.non_display_mode}>
                        <t2 className='controlmoduletitle'>顯示切換</t2>
                        <div style={{ marginTop: "10px", marginBottom: "20px" }}>
                            <Checkbox.Group defaultValue={this.state.item} id={"checkbox_group"} style={{ width: '100%', display: "grid", gridTemplateRows: "repeat(5,1fr)", gridRowGap: "5px" }} onChange={this.onChange}>
                                <div style={display_style.check_area}>
                                    <Checkbox value={0}></Checkbox>
                                    <t3 style={display_style.check_text}>生命徵象</t3>
                                </div>
                                <div style={display_style.check_area}>
                                    <Checkbox value={1} ></Checkbox>
                                    <t3 style={display_style.check_text}>檢驗</t3>
                                </div>
                                <div style={display_style.check_area}>
                                    <Checkbox value={2}></Checkbox>
                                    <t3 style={display_style.check_text}>呼吸評估</t3>
                                </div>
                                <div style={display_style.check_area}>
                                    <Checkbox value={3}></Checkbox>
                                    <t3 style={display_style.check_text}>醫藥囑</t3>
                                </div>
                                <div style={display_style.check_area}>
                                    <Checkbox value={4}></Checkbox>
                                    <t3 style={display_style.check_text}>行事曆及註記</t3>
                                </div>
                                {/* <div style={display_style.check_area}>
                                        <Checkbox value={5}></Checkbox>
                                        <t3 style={display_style.check_text}>To Do List</t3>
                                    </div> */}
                            </Checkbox.Group>
                        </div>
                    </div>
                </div>
            </div>
            //     <div className='boxshadow'></div>
            // </div >

        );
    }
}

export default Controlbar;
