import React, { Component } from 'react';
import { Select, Modal, Tabs, Icon, Button } from 'antd';
import Crosssectionvitalsignchart from './Crossectionvitalsignchart'
import Warningsetting from './Warningsettinglist'
// import Addwarningsetting from './Addnewwaring'
import './Vitalsign.scss'
// import { formatxArrayhalf, formatendtime } from '../../Function/Timeformat'
// import { simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import '../../Wardview/Wardchart.scss'
import { statecolor } from '../../Function/Wardstate'
import * as Wardstate from '../../Function/Wardstate'


class Vitalsign extends Component {

    state = {
        vital_days: 0, //false:一天 true:三天
        source: "CM", //false:中央站 true:NIS
        visible: false,
        visibleConflict: false,
        chackstate_one: 0,
        chackstate_three: 0,
        datasource: null,
        addNewWarningDateStr: null,
        addNewWarningHourStr: null,

        CenterMonitorValue: null,
        preHISID: null,
        prePickerDate: null,
        checkStateNumber: null,

        pbStatus: true
    }
    componentDidUpdate() {
        const { PickerDate } = this.props
        const { prePickerDate } = this.state
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        const preHISID = this.state.preHISID
        const Time = moment(this.timeCheck(PickerDate, this.state.vital_days).time).format("YYYYMMDDHHmmss")
        if (Phistnum !== preHISID || PickerDate !== prePickerDate) {
            this.setState({ preHISID: Phistnum, prePickerDate: PickerDate, checkStateNumber: this.timeCheck(PickerDate, this.state.vital_days).checkNum })
            this.getVitalSignData(Time.time, Phistnum)
        }
    }
    componentDidMount() {
        //選取現在時間
        const { PickerDate } = this.props
        const Time = moment(this.timeCheck(PickerDate, this.state.vital_days).time).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ preHISID: Phistnum, prePickerDate: PickerDate, checkStateNumber: this.timeCheck(PickerDate, this.state.vital_days).checkNum })
        this.getVitalSignData(Time, Phistnum)
    }

    changePb = () => {
        this.setState({
            pbStatus: false
        })
    }

    decidePB = (hr, nbpd, abpd) => {
        const status = this.state.pbStatus
        if (status) {
            hr > 160 || hr < 100 && this.changePb()
            nbpd > 75 || nbpd < 55 && this.changePb()
            abpd > 75 || abpd < 55 && this.changePb()
        }
    }

    timeCheck(PickerDate, interval) {
        function sevenFormat(nowDaySeven) {
            const now = moment().valueOf()
            if (now < nowDaySeven) {
                return nowDaySeven - (24 * 60 * 60 * 1000)
            }
            return nowDaySeven
        }
        const nowDaySeven = moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf() + (7 * 60 * 60 * 1000)
        const time = sevenFormat(nowDaySeven)
        const PickerDateSeven = PickerDate + (7 * 60 * 60 * 1000)
        const hourFormat = moment().get('hours')
        if (PickerDateSeven === time) {
            //選取到今天
            const EndTime = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf()
            if (interval === 0) {
                return {
                    time: EndTime - (60 * 60 * 1000),
                    checkNum: hourFormat - 7
                }
            }
            const format = parseInt(hourFormat % 4) + 1
            return {
                time: EndTime - (format * 60 * 60 * 1000),
                checkNum: 18 - parseInt((moment(time + (24 * 60 * 60 * 1000)).diff(moment(EndTime - (format * 60 * 60 * 1000)), "hours") / 4))
            }

        }
        const EndTime = moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf() + (31 * 60 * 60 * 1000)
        if (interval === 0) {
            return {
                time: EndTime - (60 * 60 * 1000),
                checkNum: 24
            }
        }
        return {
            time: EndTime - (4 * 60 * 60 * 1000),
            checkNum: 18
        }

    }

    getVitalSignData(Time, Phistnum) {
        const Interval = this.formatvital_daysToInterval()
        ApiProvider.getPatientwarMidValue(Phistnum, Time, Interval)
            .then(data => { console.log(data.data); this.setState({ CenterMonitorValue: data.data }) })
            .catch(err => { console.log(err) })
    }

    onVitalCheck(interval) {
        var gridarray = []
        if (interval === 1) {
            for (let index = 0; index < 18; index++) {
                const checknumber = index + 1
                if (checknumber === this.state.checkStateNumber) {
                    gridarray.push(<div key={index} style={{ height: 370, width: "100%", border: "rgba(245, 166, 35, 1) 1px solid" }} ></div>)
                }
                else gridarray.push(<div key={index} style={{ height: 370, width: "100%", cursor: "pointer" }} onClick={() => this.onClickCheckgrid(checknumber, interval)}></div>)
            }
        }
        else {
            for (let index = 0; index < 24; index++) {
                const checknumber = index + 1
                if (checknumber === this.state.checkStateNumber) {
                    gridarray.push(<div key={index} style={{ height: 370, width: "100%", border: "rgba(245, 166, 35, 1) 1px solid" }} ></div>)
                }
                else gridarray.push(<div key={index} style={{ height: 370, width: "100%", cursor: "pointer" }} onClick={() => this.onClickCheckgrid(checknumber, interval)}></div>)
            }
        }
        return gridarray;
    }

    formatvital_daysToInterval() {
        const vitaldays = this.state.vital_days
        if (vitaldays === 0) {
            return 0
        }
        return 1;
    }

    onClickCheckgrid(number, Interval) {
        function startTime(PickerDate, Interval) {
            const End = PickerDate + (31 * 60 * 60 * 1000)
            if (Interval === 0) {
                return End - (24 * 60 * 60 * 1000)
            }
            return End - (3 * 24 * 60 * 60 * 1000)
        }
        function intervalforhour(Interval) {
            if (Interval === 0) {
                return 1;
            }
            return 4;
        }
        const { PickerDate } = this.props
        const time = moment(startTime(PickerDate, Interval) + (intervalforhour(Interval) * (number - 1) * 60 * 60 * 1000)).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ checkStateNumber: number })
        this.getVitalSignData(time, Phistnum)
    }

    render() {
        function valueFormat(value) {
            if (value === null || value === undefined || value === "") {
                return "--"
            }
            return value
        }
        function centervalueNull(value) {
            if (value === undefined || value === null || value === 0) {
                return "--"
            }
            return value
        }
        function centervaluetoNull(value) {
            function FilterList(DataList, type) {
                const filterData = DataList.filter(function (value, index, arr) {
                    return value.type === type
                })
                if (filterData.length === 0) {
                    return null
                }
                return filterData[0].Mid
            }
            if (value === undefined || value === null) {
                return {
                    hr: null,
                    rr: null,
                    bt: null,
                    spo2: null,
                    abpd: null,
                    abpm: null,
                    abps: null,
                    nbpm: null,
                    nbps: null,
                    nbpd: null
                }
            }
            return {
                hr: FilterList(value, "hr"),
                rr: FilterList(value, "rr"),
                bt: FilterList(value, "bt"),
                spo2: FilterList(value, "spo2"),
                abpd: FilterList(value, "abpd"),
                abpm: FilterList(value, "abpm"),
                abps: FilterList(value, "abps"),
                nbpd: FilterList(value, "nbpd"),
                nbps: FilterList(value, "nbps"),
                nbpm: FilterList(value, "nbpm"),
            }
        }

        function limitValue(value, upper, lower) {
            if (Number(value) > 0) {
                if (value > upper || value < lower) {
                    return false
                }
                return true
            }
            return true
        }

        function vitaldaysBool(vitaldays) {
            if (vitaldays === 3) {
                return true
            }
            return false
        }

        function formatPickerTimer(pickerdate, interval) {
            if (interval === 0) {
                return {
                    startTime: pickerdate + (7 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }
            }
            else {
                return {
                    startTime: pickerdate + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000),
                    endTime: pickerdate + (31 * 60 * 60 * 1000)
                }

            }
        }
        const width = window.screen.availWidth * 0.25
        const centervalue = centervaluetoNull(this.state.CenterMonitorValue)
        const pickerTimer = formatPickerTimer(this.props.PickerDate, this.state.vital_days)
        const PickerDate = this.props.PickerDate

        this.decidePB(centervalue.hr, centervalue.nbpd, centervalue.abpd)
        const status = this.state.pbStatus
        return (
            <div style={{ border: '1px solid rgba(238, 238, 238, 1)' }}>
                <div style={{ display: 'flex', alignItems: 'center', height: "36px", backgroundColor: "#71b0e46e", padding: "0px 16px", borderRadius: "4px 4px 0 0" }}>
                    <div style={{ fontSize: "16px" }}>生命徵象</div>
                    <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridGap: "5px", marginLeft: "20px" }}>
                        <div className={this.state.source === "CM" ? "vitalBtnSource" : "vitalBtnSelectSource"} style={{ marginRight: "1px" }} onClick={() => { this.setState({ source: "CM", checkStateNumber: null }) }}>
                            中央站資訊
                        </div>
                        <div className={this.state.source === "NIS" ? "vitalBtnSource" : "vitalBtnSelectSource"} onClick={() => { this.setState({ source: "NIS", checkStateNumber: null }) }}>
                            護理系統資訊
                        </div>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridGap: "2px", marginLeft: "15px" }}>
                        <div className={this.state.vital_days === 0 ? "vitalBtnInterval" : "vitalBtnSelectInterval"} style={{ marginRight: "1px" }} onClick={() => { this.setState({ vital_days: 0, checkStateNumber: this.timeCheck(PickerDate, 0).checkNum }) }}>
                            顯示一天
                        </div>
                        <div className={this.state.vital_days === 1 ? "vitalBtnInterval" : "vitalBtnSelectInterval"} onClick={() => { this.setState({ vital_days: 1, checkStateNumber: this.timeCheck(PickerDate, 1).checkNum }) }}>
                            顯示三天
                        </div>
                    </div>
                </div>
                <div className='Wardchartlayout'>
                    <div className="halfwardchart">
                        <div className='singlewardchart' style={{ display: 'grid', gridTemplateRows: '22px 122px 122px 123px' }}>
                            <div></div>
                            <div className="rowTitle" >
                                <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: Wardstate.limitValue(centervalue.hr, 160, 100) ? "white" : statecolor('HR') }}>
                                    <t3 style={{ color: Wardstate.limitValue(centervalue.hr, 160, 100) ? statecolor('HR') : "white" }}>HR</t3>
                                    <div style={{ fontWeight: "600", fontSize: "40px", lineHeight: "44px", paddingRight: "3px", color: status ? statecolor('HR') : "white" }}>{centervalueNull(centervalue.hr)}</div>
                                </div>
                            </div>
                            {
                                this.state.source === 'CM' ? (
                                    <div className="rowTitle">
                                        <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: Wardstate.bplimitValue(centervalue.nbpd, centervalue.nbps, 55, 35, 75, 55) ? "white" : statecolor('BP') }}>
                                            <div style={{ color: Wardstate.bplimitValue(centervalue.nbpd, centervalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>NBP(mmHg)</div>
                                            <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "34px", color: Wardstate.bplimitValue(centervalue.nbpd, centervalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>{centervalueNull(centervalue.nbps)}/{centervalueNull(centervalue.nbpd)}</div>
                                            <div style={{ fontWeight: "600", fontSize: "20px", lineHeight: "22px", color: Wardstate.bplimitValue(centervalue.nbpd, centervalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>({centervalueNull(centervalue.nbpm)})</div>
                                        </div>
                                    </div>
                                ) : null
                            }
                            <div className="rowTitle">
                                <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: Wardstate.bplimitValue(centervalue.abpd, centervalue.abps, 55, 35, 75, 55) ? "white" : statecolor('BP') }}>
                                    <div style={{ color: Wardstate.bplimitValue(centervalue.nbpd, centervalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>{this.state.source === 'CM' ? 'ABP' : 'BP'}(mmHg)</div>
                                    <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "34px", color: Wardstate.bplimitValue(centervalue.abpd, centervalue.abps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>{centervalueNull(centervalue.abps)}/{centervalueNull(centervalue.abpd)}</div>
                                    <div style={{ fontWeight: "600", fontSize: "20px", lineHeight: "22px", color: Wardstate.bplimitValue(centervalue.abpd, centervalue.abps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>({centervalueNull(centervalue.abpm)})</div>
                                </div>
                            </div>
                        </div>
                        <div style={{ position: "relative" }}>
                            {
                                this.state.source === 'CM' ? (
                                    <div style={{ position: "absolute", top: 20, left: 50, width: width - 50, height: 330 }}>
                                        <div style={{ display: "grid", gridTemplateColumns: this.state.vital_days === 1 ? "repeat(18, 1fr)" : "repeat(24,1fr)" }}>
                                            {this.onVitalCheck(this.state.vital_days)}
                                        </div>
                                    </div>
                                ) : null
                            }
                            <Crosssectionvitalsignchart
                                comein={'line'}
                                CID={this.props.caseinfo.CASEID}
                                HID={this.props.caseinfo.HISID}
                                PickerDate={PickerDate}
                                svg_id={"HR_svg"}
                                id_key={"HR_div"}
                                type={"hr"}
                                source={this.state.source}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={this.state.vital_days}
                                lower={100}
                                upper={160}
                                axisBot={true}
                                axisTop={true}
                                circlrcolor="#779F50"
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={200}>
                            </Crosssectionvitalsignchart>
                            {
                                this.state.source === 'CM' ? (
                                    <Crosssectionvitalsignchart
                                        comein={'line'}
                                        CID={this.props.caseinfo.CASEID}
                                        HID={this.props.caseinfo.HISID}
                                        PickerDate={PickerDate}
                                        svg_id={"NBP_svg"}
                                        id_key={"NBP_div"}
                                        type={"nbp"}
                                        source={this.state.source}
                                        startTime={pickerTimer.startTime}
                                        endTime={pickerTimer.endTime}
                                        height={100}
                                        width={width}
                                        interval={this.state.vital_days}
                                        lower={55}
                                        upper={75}
                                        axisBot={false}
                                        axisTop={false}
                                        circlrcolor={"#C12528"}
                                        colorshadow={"rgba(126, 211, 33, 0.5)"}
                                        min={0}
                                        max={100}>
                                    </Crosssectionvitalsignchart>
                                ) : null
                            }
                            <Crosssectionvitalsignchart
                                comein={'line'}
                                CID={this.props.caseinfo.CASEID}
                                HID={this.props.caseinfo.HISID}
                                PickerDate={PickerDate}
                                svg_id={"ABP_svg"}
                                id_key={"ABP_div"}
                                type={"abp"}
                                source={this.state.source}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={this.state.vital_days}
                                lower={55}
                                upper={75}
                                axisBot={false}
                                axisTop={false}
                                circlrcolor={"#C12528"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}>
                            </Crosssectionvitalsignchart>
                        </div>
                    </div>
                    <div className="halfwardchart">
                        <div className='singlewardchart' style={{ display: 'grid', gridTemplateRows: '22px 122px 122px 123px' }}>
                            <div></div>
                            <div className="rowTitle" >
                                <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: limitValue(centervalue.spo2, 100, 90) ? "white" : statecolor('SpO2') }}>
                                    <t3 style={{ color: limitValue(centervalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>
                                        SpO
                                        <sub >2</sub>
                                        (%)
                                    </t3>
                                    <div style={{ fontWeight: "600", fontSize: "40px", lineHeight: "44px", paddingRight: "3px", color: limitValue(centervalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>{centervalueNull(centervalue.spo2)}%</div>
                                </div>
                            </div>
                            <div className="rowTitle" >
                                <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: limitValue(centervalue.bt, 38, 36) ? "white" : statecolor('BT') }}>
                                    <div style={{ color: limitValue(centervalue.bt, 38, 36) ? statecolor('BT') : "white" }}>BT</div>
                                    <div style={{ fontWeight: "600", fontSize: "40px", lineHeight: "44px", paddingRight: "3px", color: limitValue(centervalue.bt, 38, 36) ? statecolor('BT') : "white" }}>{valueFormat(centervalue.bt)}</div>
                                </div>
                            </div>
                            <div className="rowTitle">
                                <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "100px", height: "100px", backgroundColor: limitValue(centervalue.rr, 60, 20) ? "white" : statecolor('RR') }}>
                                    <div style={{ color: limitValue(centervalue.rr, 60, 20) ? statecolor('RR') : "white" }}>RR(min)</div>
                                    <div style={{ fontWeight: "600", fontSize: "40px", lineHeight: "44px", paddingRight: "3px", color: limitValue(centervalue.rr, 60, 20) ? statecolor('RR') : "white" }}>{centervalueNull(centervalue.rr)}</div>
                                </div>
                            </div>
                        </div>
                        <div style={{ position: "relative" }}>
                            {
                                this.state.source === 'CM' ? (
                                    <div style={{ position: "absolute", top: 20, left: 50, width: width - 50, height: 330 }}>
                                        <div style={{ display: "grid", gridTemplateColumns: this.state.vital_days === 1 ? "repeat(18, 1fr)" : "repeat(24,1fr)" }}>
                                            {this.onVitalCheck(this.state.vital_days)}
                                        </div>
                                    </div>
                                ) : null
                            }

                            <Crosssectionvitalsignchart
                                comein={'line'}
                                CID={this.props.caseinfo.CASEID}
                                HID={this.props.caseinfo.HISID}
                                PickerDate={PickerDate}
                                svg_id={"SpO2_svg"}
                                id_key={"SpO2_div"}
                                type={"spo2"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={this.state.vital_days}
                                lower={90}
                                upper={100}
                                axisBot={true}
                                axisTop={true}
                                circlrcolor={"#4773B8"}
                                colorshadow={"#4773B8"}
                                min={60}
                                max={100}
                                source={this.state.source}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                comein={'line'}
                                CID={this.props.caseinfo.CASEID}
                                HID={this.props.caseinfo.HISID}
                                PickerDate={PickerDate}
                                svg_id={"BT_svg"}
                                id_key={"BT_div"}
                                type={"bt"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                source={this.state.source}
                                height={100}
                                width={width}
                                interval={this.state.vital_days}
                                lower={36}
                                upper={38}
                                axisBot={false}
                                axisTop={false}
                                circlrcolor={"#7F3DB5"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={34}
                                max={41}>
                            </Crosssectionvitalsignchart>
                            <Crosssectionvitalsignchart
                                comein={'line'}
                                CID={this.props.caseinfo.CASEID}
                                HID={this.props.caseinfo.HISID}
                                PickerDate={PickerDate}
                                svg_id={"RR_svg"}
                                id_key={"RR_div"}
                                type={"rr"}
                                startTime={pickerTimer.startTime}
                                endTime={pickerTimer.endTime}
                                height={100}
                                width={width}
                                interval={this.state.vital_days}
                                lower={20}
                                upper={60}
                                axisBot={false}
                                axisTop={false}
                                circlrcolor={"#DE7531"}
                                colorshadow={"rgba(126, 211, 33, 0.5)"}
                                min={0}
                                max={100}
                                source={this.state.source}>
                            </Crosssectionvitalsignchart>
                        </div>
                    </div>
                </div>
            </div >
        );
    }

}

export default Vitalsign;
