import axios from 'axios';

const githubApiUrl = "https://raw.githubusercontent.com/gerrykiki/JsonRegObject/master/";

//create new instance of github API
var axiosGithub = axios.create({
    baseURL: githubApiUrl,
    timeout: 1000,
    //headers: {'X-Custom-Header': 'foobar'}
});

export function getGitHubJson(string) {
    return axiosGithub.get(string);
}

