import React, { Component } from 'react';
import * as ApiProvider from '../../API/ApiProvider';
import Wardinfousercheckbox from '../../Wardview/Wardunfousercheckbox';

class CrosssectionTodo extends Component {
    state = {
        todoList: []
    }

    componentDidMount() {
        this.queryTodo();
    }

    queryTodo() {
        ApiProvider.getOneTodo(this.props.caseinfo.TRANSINID, this.props.caseinfo.CASEID)
            .then(result => {
                console.log("getOneTodo OK " + JSON.stringify(result.data));
                this.setState({
                    todoList: result.data
                })
            })
            .catch(err => {
                console.log("getOneTodo NG " + JSON.stringify(err));
            });
    }

    renderTodoItem() {
        if (this.state.todoList.length === 0) {
            return (
                // <div>沒有待辦事項</div>
                <div style={{ height: "50px", display: "flex", alignItems: "center" }}>
                    沒有待辦事項
                </div>
            )
        } else {
            return this.state.todoList.map((item, index) => {
                if (item.type === "未完成") {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={false}></Wardinfousercheckbox>);
                } else {
                    return (<Wardinfousercheckbox key={index} item={item} selectstate={true}></Wardinfousercheckbox>);
                }

            });
        }
    }

    render() {
        const renderTodoItem = this.renderTodoItem.bind(this);
        return (
            <div>
                <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>待辦事項</t2>
                </div>
                <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse", padding: "0px 16px" }}>
                    {renderTodoItem()}
                </div>
            </div>
        );
    }
}

export default CrosssectionTodo
