import React, { Component } from 'react';
import { Modal } from 'antd'
import NonLabReport from '../CrossSectionView/NonLabReport'

export default class ChronicTableCellToolTipReport extends Component {
    state = {
        visible: false
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    render() {
        const { HID, CID, ORMM, ORDSEQNO, status } = this.props
        console.log(status)
        return (
            <div style={{ cursor: "pointer", }}>
                <div>
                    <div style={{ textDecoration: "underline", cursor: "pointer" }} onClick={this.showModal}>{"顯示"}</div>
                    <Modal
                        title="檢驗結果"
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        width={1000}
                        footer={null}>
                        <NonLabReport CID={CID} HID={HID} month={ORMM} seq={ORDSEQNO}></NonLabReport>
                    </Modal>
                </div>
            </div>
        );
    }
}