import React, { Component } from 'react';
import './Flootsheetio.scss'
import * as RegFilter from '../../Function/FiliterReg'
import moment from 'moment';
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider'
import * as QDCR from '../../Resource/QDC'
import * as FlootSheetDataFormat from './FlootSheetDataFormat'
import { QDC } from '../../Data/RegularData';

const generateDateColumn1 = () => {
    const dateArr = Array.from(Array(24).keys()).map(d => (d + 7) % 24);
    return (
        <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
            <div style={{ borderRight: '5px solid transparent', borderBottom: "1px solid transparent", borderTop: "1px solid transparent", borderCollapse: 'collapse', display: "flex", alignItems: "center", justifyContent: 'flex-end', fontSize: "12px" }}>時間</div>
            {dateArr.map((time, index) => (
                <div key={index} style={{ borderRight: (index + 1) % 8 === 0 && index !== 0 && index !== dateArr.length - 1 ? '5px solid transparent' : '1px solid transparent', borderBottom: "1px solid transparent", borderTop: "1px solid transparent", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{time}</div>
            ))}
        </div>
    )
}


class Flootsheetio extends Component {
    state = {
        itemNumber: "day",
        preCID: null,
        prePickerDate: null,
        QDC: []
    }
    componentDidMount() {
        const { caseinfo, PickerDate } = this.props
        const CID = caseinfo.CASEID
        this.setState({ preCID: CID, prePickerDate: PickerDate })
        this.getQDC(CID, PickerDate)
    }
    componentDidUpdate() {
        const { caseinfo, PickerDate } = this.props
        const CID = caseinfo.CASEID
        const preCID = this.state.preCID
        const prePickerDate = this.state.prePickerDate
        if (CID !== preCID || PickerDate !== prePickerDate) {
            this.setState({ preCID: CID, prePickerDate: PickerDate })
            this.getQDC(CID, PickerDate)
        }
    }

    ToolTipRender(elementList) {
        console.log(elementList)
    }

    getQDC(CID, PickerDate) {
        const xm2 = new X2JS()
        const st = moment(PickerDate + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(PickerDate + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        ApiProvider.getQDC(CID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                this.setState({
                    QDC: parsedata
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        // this.setState({ QDC: QDCR.data.outmsg.resconts.rescont })
    }
    render() {
        function nullvalue(value) {
            if (value === undefined || value === null || value === 0) {
                return ""
            }
            return value.toString().trim()
        }
        function formatDayData(valueStatus, title) {
            if (!valueStatus) {
                var cell = []
                for (let index = 0; index < 24; index++) {
                    cell.push({
                        value: 0
                    })
                }
                const cellMap = cell.map(function (info, index, arr) {
                    if (title === "無輸血紀錄") {
                        return (
                            <div key={index} style={{ borderRight: (index + 1) % 8 == 0 && index != 0 && index != cell.length - 1 ? 'rgb(210, 175, 165) 5px solid' : 'rgb(160, 200, 200) 1px solid', borderBottom: "rgb(160, 200, 200) 1px solid", borderTop: "rgb(160, 200, 200) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
                        );
                    }
                    return (
                        <div key={index} style={{ borderRight: (index + 1) % 8 == 0 && index != 0 && index != cell.length - 1 ? 'rgb(160, 200, 200) 5px solid' : 'rgb(160, 200, 200) 1px solid', borderBottom: "rgb(160, 200, 200) 1px solid", borderTop: "rgb(160, 200, 200) 1px solid", borderCollapse: 'collapse', display: "flex", justifyContent: "center", alignItems: "center" }}>{nullvalue(info.value)}</div>
                    );
                })
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                        <div style={{ borderRight: 'rgb(160, 200, 200) 5px solid', borderBottom: "rgb(160, 200, 200) 1px solid", borderTop: "rgb(160, 200, 200) 1px solid", borderCollapse: 'collapse', backgroundColor: "rgb(195, 225, 225)", display: "flex", alignItems: "center", fontSize: "12px" }}>{title}</div>
                        {cellMap}
                    </div>
                );
            }
            return null
        }

        const { PickerDate } = this.props
        const { QDC } = this.state
        const Infusion = FlootSheetDataFormat.formatIOData(QDC, "輸液")
        const IV = FlootSheetDataFormat.formatIV(Infusion)
        console.log(IV)
        const ALINE = FlootSheetDataFormat.formatALUA(Infusion)
        const PICC = FlootSheetDataFormat.formatCVCPICC(Infusion)
        const filterInfusion = FlootSheetDataFormat.formatData(Infusion, PickerDate, "輸液")
        const groupFilterInfusion = FlootSheetDataFormat.groupArray(filterInfusion)
        const mapInfusion = FlootSheetDataFormat.mapCellrender(groupFilterInfusion, PickerDate, "輸液")
        // console.log(groupFilterInfusion)
        const Blood = FlootSheetDataFormat.formatIOData(QDC, "血品")
        const filterBlood = FlootSheetDataFormat.formatData(Blood, PickerDate, "血品")
        const groupFilterBlood = FlootSheetDataFormat.groupArray(filterBlood)
        const mapBlood = FlootSheetDataFormat.mapCellrender(groupFilterBlood, PickerDate, "血品")
        // console.log(mapBlood)
        const Eat = FlootSheetDataFormat.formatIOData(QDC, "進食")
        const filterEat = FlootSheetDataFormat.formatData(Eat, PickerDate, "進食")
        const groupFilterEat = FlootSheetDataFormat.groupArray(filterEat)
        const mapEat = FlootSheetDataFormat.mapCellrender(groupFilterEat, PickerDate, "進食")


        const RV = FlootSheetDataFormat.formatIOData(QDC, "進食")
        const filterRV = FlootSheetDataFormat.formatData(RV, PickerDate, "進食")
        const filterRVData = FlootSheetDataFormat.formatRVData(filterRV)
        const mapRV = FlootSheetDataFormat.mapCellrender(filterRVData, PickerDate, "RV")
        console.log(filterRVData)

        //Fourth
        const BloodLoss = FlootSheetDataFormat.formatIOData(QDC, "失血")
        const filterBloodLoss = FlootSheetDataFormat.formatData(BloodLoss, PickerDate, "失血")
        const groupFilterBloodLoss = FlootSheetDataFormat.groupArray(filterBloodLoss)
        const mapBloodLoss = FlootSheetDataFormat.mapCellrender(groupFilterBloodLoss, PickerDate, "失血")

        const Exudate = FlootSheetDataFormat.formatIOData(QDC, "滲液")
        const filterExudate = FlootSheetDataFormat.formatData(Exudate, PickerDate, "滲液")
        const groupFilterExudate = FlootSheetDataFormat.groupArray(filterExudate)
        const mapExudate = FlootSheetDataFormat.mapCellrender(groupFilterExudate, PickerDate, "滲液")

        const Vomit = FlootSheetDataFormat.formatIOData(QDC, "嘔吐")
        const filterVomit = FlootSheetDataFormat.formatData(Vomit, PickerDate, "嘔吐")
        const groupFilterVomit = FlootSheetDataFormat.groupArray(filterVomit)
        const mapVomit = FlootSheetDataFormat.mapCellrender(groupFilterVomit, PickerDate, "嘔吐")

        const Drainage = FlootSheetDataFormat.formatIOData(QDC, "引流")
        const filterDrainage = FlootSheetDataFormat.formatData(Drainage, PickerDate, "引流")
        const groupFilterDrainage = FlootSheetDataFormat.groupArray(filterDrainage)
        const mapDrainage = FlootSheetDataFormat.mapCellrender(groupFilterDrainage, PickerDate, "引流")


        const Puncture = FlootSheetDataFormat.formatIOData(QDC, "穿刺")
        const filterPunctureOut = FlootSheetDataFormat.formatData(Puncture, PickerDate, "穿刺出")
        const groupFilterPunctureOut = FlootSheetDataFormat.groupArray(filterPunctureOut)
        const mapPunctureOut = FlootSheetDataFormat.mapCellrender(groupFilterPunctureOut, PickerDate, "穿刺出")

        const filterPunctureIn = FlootSheetDataFormat.formatData(Puncture, PickerDate, "穿刺入")
        const groupFilterPunctureIn = FlootSheetDataFormat.groupArray(filterPunctureIn)
        const mapPunctureIn = FlootSheetDataFormat.mapCellrender(groupFilterPunctureIn, PickerDate, "穿刺入")


        const Dialysis = FlootSheetDataFormat.formatIOData(QDC, "透析")
        const filterDialysisIn = FlootSheetDataFormat.formatData(Dialysis, PickerDate, "透析入")
        const groupFilterDialysisIn = FlootSheetDataFormat.groupArray(filterDialysisIn)
        const mapDialysisIn = FlootSheetDataFormat.mapCellrender(groupFilterDialysisIn, PickerDate, "透析入")

        const filterDialysisOut = FlootSheetDataFormat.formatData(Dialysis, PickerDate, "透析出")
        const groupFilterDialysisOut = FlootSheetDataFormat.groupArray(filterDialysisOut)
        const mapDialysisOut = FlootSheetDataFormat.mapCellrender(groupFilterDialysisOut, PickerDate, "透析出")

        const filterDialysisTotal = FlootSheetDataFormat.formatData(Dialysis, PickerDate, "透析總")
        const groupFilterDialysisTotal = FlootSheetDataFormat.groupArray(filterDialysisTotal)
        const mapDialysisTotal = FlootSheetDataFormat.mapCellrender(groupFilterDialysisTotal, PickerDate, "透析總")



        const Rinse = FlootSheetDataFormat.formatIOData(QDC, "沖洗")
        const filterRinseIn = FlootSheetDataFormat.formatData(Rinse, PickerDate, "沖洗入")
        const groupFilterRinseIn = FlootSheetDataFormat.groupArray(filterRinseIn)
        const mapRinseIn = FlootSheetDataFormat.mapCellrender(groupFilterRinseIn, PickerDate, "沖洗入")


        const filterRinseOut = FlootSheetDataFormat.formatData(Rinse, PickerDate, "沖洗出")
        const groupFilterRinseOut = FlootSheetDataFormat.groupArray(filterRinseOut)
        const mapRinseOut = FlootSheetDataFormat.mapCellrender(groupFilterRinseOut, PickerDate, "沖洗出")


        const Stool = FlootSheetDataFormat.formatIOData(QDC, "排便")
        const filterStool = FlootSheetDataFormat.formatData(Stool, PickerDate, "Stool")
        const groupFilterStool = FlootSheetDataFormat.groupArray(filterStool)
        const mapStool = FlootSheetDataFormat.mapCellrender(groupFilterStool, PickerDate, "Stool")

        const filterStimulation = FlootSheetDataFormat.formatData(Stool, PickerDate, "Stimulation")
        const groupFilterStimulation = FlootSheetDataFormat.groupArray(filterStimulation)
        const mapStimulation = FlootSheetDataFormat.mapCellrender(groupFilterStimulation, PickerDate, "Stimulation")


        const Urine = FlootSheetDataFormat.formatIOData(QDC, "排尿")
        const filterUrine = FlootSheetDataFormat.formatData(Urine, PickerDate, "排尿")
        const groupFilterUrine = FlootSheetDataFormat.groupArray(filterUrine)
        const mapUrine = FlootSheetDataFormat.mapCellrender(groupFilterUrine, PickerDate, "排尿")


        return (

            <div>

                {/* <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>I/O</div>
                </div> */}

                {/* <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>I/O</t2>
                </div> */}
                <div style={{ margin: "0 auto", display: "flex", flexDirection: "column", justifyContent: "center", maxWidth: "1360px" }}>
                    {generateDateColumn1()}
                    {Infusion.length != 0 ? '輸液' : null}
                    <div style={{ backgroundColor: "rgb(195, 225, 225)", display: "block", border: "5px solid rgb(160, 200, 200)", borderCollapse: "collapse" }}>
                        {formatDayData(IV, "IV")}
                        {formatDayData(ALINE, "A-LINE/UVA")}
                        {formatDayData(PICC, "CVC/PICC/UVC")}
                        {mapInfusion}
                    </div>
                    {'血品'}
                    <div style={{ backgroundColor: "rgb(220, 205, 200)", display: "block", border: "5px solid rgb(210, 175, 165)", borderCollapse: "collapse" }}>
                        {mapBlood}
                    </div>
                    {'進食'}
                    <div style={{ backgroundColor: "rgb(230, 217, 154)", display: "block", border: "5px solid rgb(179, 173, 50)", borderCollapse: "collapse" }}>
                        {mapEat}
                        {mapRV}
                    </div>
                    {'排泄引流'}
                    <div style={{ backgroundColor: "rgb(212, 202, 221)", display: "block", border: "5px solid rgb(140, 118, 162)", borderCollapse: "collapse" }}>
                        {mapBloodLoss}
                        {mapExudate}
                        {mapVomit}
                        {mapDrainage}
                        {mapPunctureOut}
                        {mapPunctureIn}
                        {mapDialysisIn}
                        {mapDialysisOut}
                        {mapDialysisTotal}
                        {mapRinseIn}
                        {mapRinseOut}
                        {mapStool}
                        {mapStimulation}
                        {mapUrine}
                    </div>
                </div>
            </div>
        );
    }
}

export default Flootsheetio;
