import React, { Component } from 'react';
import './Patientviewinfo.scss'
import { Icon } from 'antd'
import * as Wardstate from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
class Patientinfo extends Component {

    state = { preCID: null, QENT: [] };
    componentDidMount() {
        const { caseinfo } = this.props
        const CID = caseinfo.CASEID
        this.setState({ preCID: CID })
        this.getQENT(CID)
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const { preCID } = this.state
        const CID = caseinfo.CASEID
        if (CID !== preCID) {
            this.setState({ preCID: CID })
            this.getQENT(CID)

        }
    }
    getQENT(CID) {
        ApiProvider.getQENT(CID)
            .then(data => {
                const parsedata = Wardstate.QMNCXM2JSON(data.data)
                this.setState({ QENT: parsedata })
            })
            .catch(err => {
                console.log("Andy test getQENT NG " + JSON.stringify(err));
            });
    }
    render() {
        function fixpatientDate(data) {
            const transdata = {
                'cause': '住院原因',
                'car_pho': '主要照顧者電話',
                'care': '主要照顧者',
                'con_1': '聯絡人1',
                'con_p11': '聯絡人1電話',
                'con_2': '聯絡人2',
                'con_p21': '聯絡人2電話',
                'dvs': '住院時生命徵象',
                'habit': '飲食狀況',
                'oxy': '住院時氧合評估',
                'h_val': '住院時頭圍',
                'b_val': '住院時胸圍',
                'tall': '住院時身高',
                'weight': '住院時體重'
            }

            const patientDate = []

            for (let index in transdata) {
                if (data[index] !== "") {
                    patientDate.push([transdata[index], data[index]])
                }
            }

            return patientDate
        }

        const patientDetail = fixpatientDate(this.state.QENT)
        const patientData = patientDetail.map(element =>
            <div className='context'>
                <div className='title'>{element[0]} ：</div>
                <div className='item'>{element[1]}</div>
            </div>
        )
        if (this.state.preCID === null || patientDetail.length === 0) {
            return (
                <div style={{ display: "flex", justifyContent: "center", alignItems: 'center' }}>
                    <Icon type="loading" />
                </div>
            );
        }
        return (
            <div className='patientinfolayout'>
                {patientData}
            </div>
        );
    }
}

export default Patientinfo
