import React, { Component } from 'react';
import Notecell from './Notecell'
import editlogo from '../Image/svg/Edit.svg'
import { Input, Button } from 'antd';
import * as ApiProvider from '../API/ApiProvider';
import moment from 'moment';
class Note extends Component {
    state = {
        powerLogin: 0,
        EditMode: false,
        inputContent: '',
        noteData: [],
        PickerDate: moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf()
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.queryNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.HISID, this.state.PickerDate)
    }
    componentDidUpdate() {
        const propDate = this.props.PickerDate
        const stateDate = this.state.PickerDate
        if (propDate !== stateDate) {
            this.setState({ PickerDate: propDate })
            this.queryNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.HISID, propDate)
        }
    }
    queryNotice(TRANSID, HISID, pickerdate) {
        const st = moment(moment(pickerdate).format("YYYYMMDD"), "YYYYMMDD").valueOf()
        const et = moment(moment(pickerdate).format("YYYYMMDD"), "YYYYMMDD").valueOf() + (24 * 60 * 60 * 1000)
        ApiProvider.getOnePatientNotice(TRANSID, HISID, st, et)
            .then(result => {
                console.log("getOnePatientNotice OK " + JSON.stringify(result));
                this.setState({
                    noteData: result.data
                })
            })
            .catch(err => {
                console.log("getOnePatientNotice NG " + JSON.stringify(err));
            });
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }
    // queryOnePatientNotice() {
    //     ApiProvider.getOnePatientNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.CASEID)
    //         .then(data => {
    //             this.setState({ noteData: data.data })
    //         }).catch(err => { })
    // }
    onClickEnter() {
        const timeNowStr = new Date().toISOString()
        const doc = sessionStorage.getItem('user')
        ApiProvider.createNotice(this.props.caseinfo.HISID, this.state.inputContent, timeNowStr, this.props.caseinfo.TRANSINID, doc)
            .then(data => {
                this.queryNotice(this.props.caseinfo.TRANSINID, this.props.caseinfo.CASEID, this.props.PickerDate)
            })
            .catch(err => {
                console.log(" Err " + JSON.stringify(err))
            })
        this.setState(
            {
                EditMode: false,
                inputContent: undefined
            }
        )
    }
    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    EditControl() {
        const edit = this.props.editstatus
        if (edit) {
            return (
                <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", cursor: "pointer" }}>
                    <img src={editlogo} alt="editlogo" />
                </div>
            );
        }
        return null
    }
    render() {
        let dataList = this.state.noteData

        if (Object.keys(dataList).length > 1) {
            dataList = dataList.sort((a, b) => {
                let tempTimeA = moment(a.time, "YYYY-MM-DD[T]HH:mm:ss.000+0000").valueOf()
                let tempTimeB = moment(b.time, "YYYY-MM-DD[T]HH:mm:ss.000+0000").valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }

        const notelist = dataList.map((info, index) => {
            const time = moment(info.time, "YYYY-MM-DD[T]HH:mm:ss.000+0000").format('YYYY-MM-DD HH:mm:ss')
            const localTime = moment.utc(time).toDate()
            const infoObj = {
                updatedictor: info.doc,
                updatetime: moment(localTime).format("YYYY/MM/DD HH:mm:ss"),
                data: info.context
            }
            return <Notecell key={index} info={infoObj}></Notecell>
        })
        const EditButton = this.EditControl()
        return (
            <div >
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center", justifyContent: 'space-between' }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{this.props.title}</t2>
                    {EditButton}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse", padding: "0px 16px" }}>
                    <div style={{ maxHeight: "300px", minHeight: "120px", overflowY: "auto" }}>
                        {JSON.stringify(dataList) !== '[]' ? <div style={{ minHeight: "120px", maxHeight: "400px", overflow: "auto" }}>
                            {notelist}
                        </div> :
                            <div style={{ height: "50px", display: "flex", alignItems: "center" }}>
                                當日無評論
                        </div>}
                    </div>
                    <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                        <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <Input placeholder="輸入當日內容" value={this.state.inputContent} onChange={this.onInputChange.bind(this)}></Input>
                        </div>
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                                <Button onClick={() => this.onClickCancel()}>取消</Button>
                                <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}

export default Note
