import React, { Component } from 'react';
import WardcardvitalsignChart from './Wardcardvitalsign'
import { statecolor } from '../Function/Wardstate'
// import * as ApiProvider from '../API/ApiProvider'
import './Wardchart.scss'
// import moment from 'moment';



class WardChart extends Component {
    state = {
        data: [],
        pbStatus: true
    }

    changePb = () => {
        this.setState({
            pbStatus: false
        })
    }

    decidePB = (hr, nbpd, abpd) => {
        const status = this.state.pbStatus
        if (status) {
            hr > 160 || hr < 100 && this.changePb()
            nbpd > 75 || nbpd < 55 && this.changePb()
            abpd > 75 || abpd < 55 && this.changePb()
        }
    }

    render() {
        function valueFormat(value) {
            if (value === null || value === undefined || value === "" || Number(value) === 0) {
                return "--"
            }
            return value
        }
        const data = this.state.data
        const { CMvalue, HISID, CID } = this.props
        const vitalvalue = CMvalue
        if (vitalvalue === undefined || vitalvalue === null) {
            return null
        }

        this.decidePB(vitalvalue.hr, vitalvalue.nbpd, vitalvalue.abpd)

        function bplimitValue(bpd, bps, upper1, lower1, upper2, lower2) {
            if (Number(bpd) > 0 || Number(bps) > 0) {
                if (bpd > upper1 || bpd < lower1) {
                    return false
                }
                if (bps > upper2 || bps < lower2) {
                    return false
                }
                return true
            }
            return true

        }

        function limitValue(value, upper, lower) {
            if (Number(value) > 0) {
                if (value > upper || value < lower) {
                    return false
                }
                return true
            }
            return true

        }

        const status = this.state.pbStatus
        console.log(CID)
        return (
            <div className='Wardchartlayout'>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: limitValue(vitalvalue.hr, 160, 100) ? "white" : statecolor('HR') }}>
                                <centerstyle>
                                    <t3 style={{ color: limitValue(vitalvalue.hr, 160, 100) ? statecolor('HR') : "white" }}>HR(min)</t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "44px", paddingRight: "3px", color: limitValue(vitalvalue.hr, 160, 100) ? statecolor('HR') : "white" }}>{valueFormat(vitalvalue.hr)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div className='secondRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: bplimitValue(vitalvalue.nbpd, vitalvalue.nbps, 55, 35, 75, 55) ? "white" : statecolor('BP') }}>
                                <centerstyle>
                                    <t3 style={{ color: bplimitValue(vitalvalue.nbpd, vitalvalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>NBP(mmHg)</t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "20px", lineHeight: "34px", color: bplimitValue(vitalvalue.nbpd, vitalvalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>{valueFormat(vitalvalue.nbps)}/{valueFormat(vitalvalue.nbpd)}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "15px", lineHeight: "22px", color: bplimitValue(vitalvalue.nbpd, vitalvalue.nbps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>({valueFormat(vitalvalue.nbpm)})</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div className='thirdRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: bplimitValue(vitalvalue.abpd, vitalvalue.abps, 55, 35, 75, 55) ? "white" : statecolor('BP') }}>
                                <centerstyle>
                                    <t3 style={{ color: bplimitValue(vitalvalue.abpd, vitalvalue.abps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>ABP(mmHg)</t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "20px", lineHeight: "34px", color: bplimitValue(vitalvalue.abpd, vitalvalue.abps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>{valueFormat(vitalvalue.abps)}/{valueFormat(vitalvalue.abpd)}</div>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "15px", lineHeight: "22px", color: bplimitValue(vitalvalue.abpd, vitalvalue.abps, 55, 35, 75, 55) ? statecolor('BP') : "white" }}>({valueFormat(vitalvalue.abpm)})</div>
                                </centerstyle>
                            </div>
                        </div>
                    </div>
                    <div style={{ position: "relative" }}>
                        <WardcardvitalsignChart CID={CID} MAX={200} MIN={0} Upper={160} Lower={100} HISID={HISID} svg_key={"svg_hr"} id_key={"div_hr"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(126, 211, 33, 0.1)"} circlrcolor={"#779F50"}></WardcardvitalsignChart>
                        <WardcardvitalsignChart CID={CID} MAX={100} MIN={0} Upper={75} Lower={55} HISID={HISID} svg_key={"svg_bp"} id_key={"div_bp"} data={data} data2={true} axisBot={false} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                        <WardcardvitalsignChart CID={CID} MAX={100} MIN={0} Upper={75} Lower={55} HISID={HISID} svg_key={"svg_abp"} id_key={"div_abp"} data={data} data2={true} axisBot={true} axisTop={false} colorshadow={"rgba(240, 144, 144, 0.1)"} circlrcolor={"#C12528"} data_2_circlrcolor={"rgba(240, 144, 144, 0.5)"}></WardcardvitalsignChart>
                    </div>
                </div>
                <div className='halfwardchart'>
                    <div className='singlewardchart'>
                        <div className='firstRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: limitValue(vitalvalue.spo2, 100, 90) ? "white" : statecolor('SpO2') }}>
                                <centerstyle>
                                    <t3 style={{ color: limitValue(vitalvalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>
                                        SpO
                                        <sub >2</sub>
                                        (%)
                                    </t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "44px", paddingRight: "3px", color: limitValue(vitalvalue.spo2, 100, 90) ? statecolor('SpO2') : "white" }}>{valueFormat(vitalvalue.spo2)}%</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div className='secondRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: limitValue(vitalvalue.bt, 38, 36) ? "white" : statecolor('BT') }}>
                                <centerstyle>
                                    <t3 style={{ color: limitValue(vitalvalue.bt, 38, 36) ? statecolor('BT') : "white" }}>BT(<sup>&ordm;</sup>C)</t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "44px", paddingRight: "3px", color: limitValue(vitalvalue.bt, 38, 36) ? statecolor('BT') : "white" }}>{valueFormat(vitalvalue.bt)}</div>
                                </centerstyle>
                            </div>
                        </div>
                        <div className='thirdRowTitle'>
                            <div style={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "90px", height: "80px", padding: "0px 2px", backgroundColor: limitValue(vitalvalue.rr, 60, 20) ? "white" : statecolor('RR') }}>
                                <centerstyle>
                                    <t3 style={{ color: limitValue(vitalvalue.rr, 60, 20) ? statecolor('RR') : "white" }}>RR(min)</t3>
                                </centerstyle>
                                <centerstyle>
                                    <div style={{ fontWeight: "600", fontSize: "30px", lineHeight: "44px", paddingRight: "3px", color: limitValue(vitalvalue.rr, 60, 20) ? statecolor('RR') : "white" }}>{valueFormat(vitalvalue.rr)}</div>
                                </centerstyle>
                            </div>
                        </div>
                    </div>
                    <div style={{ position: "relative" }}>
                        <WardcardvitalsignChart CID={CID} MAX={100} MIN={60} Upper={100} Lower={90} HISID={HISID} svg_key={"svg_spo"} id_key={"div_spo"} data={data} axisBot={false} axisTop={true} colorshadow={"rgba(0, 183, 255, 0.1)"} circlrcolor={"#4773B8"}></WardcardvitalsignChart>
                        <WardcardvitalsignChart CID={CID} MAX={41} MIN={34} Upper={38} Lower={36} HISID={HISID} svg_key={"svg_bt"} id_key={"div_bt"} data={data} axisBot={false} axisTop={false} colorshadow={"rgba(199, 162, 244,0.1)"} circlrcolor={"#7F3DB5"}></WardcardvitalsignChart>
                        <WardcardvitalsignChart CID={CID} MAX={100} MIN={0} Upper={60} Lower={20} HISID={HISID} svg_key={"svg_rr"} id_key={"div_rr"} data={data} axisBot={true} axisTop={false} colorshadow={"rgba(255, 152, 0, 0.1)"} circlrcolor={"#DE7531"}></WardcardvitalsignChart>
                    </div>
                </div>
            </div>
        );
    }
}

export default WardChart;
