import React, { Component } from 'react';
import Schedule_event_list from '../Module/Schedule_event'
import moment from 'moment';

class Wardschedule extends Component {

    render() {
        return (
            <Schedule_event_list  PickerDate={moment(moment().format("YYYYMMDD"),"YYYYMMDD").valueOf()}  source={this.props.source} title="今日排程" data={this.props.scheduleData} editstatus={this.props.editstatus} />
        );
    }
}


export default Wardschedule
