import React, { Component } from 'react';
import Treatmentordertable from './Treatmentordertable'
import { Select, Input } from 'antd';
import * as ApiGithub from '../../API/ApiGithub'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'

class Treatmentorderlayout extends Component {
    state = {
        // PickerDateRange: [moment().subtract(6, 'days'), moment()],
        parser: { "mappings": [] },
        DateRangeMode: {},
        class: "全部類別",
        SelectMode: 0,
        InputMode: '',
        preHID: null,
        preCID: null,
        TRTMNTQ1: []
    }

    componentDidMount() {
        this.getGithub()
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        this.setState({ preHID: HID, preCID: CID })
        this.getTreatmentQ1(HID, CID)
    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preHID, preCID } = this.state
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        if (HID !== preHID || CID !== preCID) {
            this.setState({ preHID: HID, preCID: CID })
            this.getTreatmentQ1(HID, CID)
        }


    }
    getGithub() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Treatment");
                parser.mappings = [{ "key": "全部類別", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Treatment OK " + JSON.stringify(parser))
                this.setState({
                    parser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    getTreatmentQ1(HID, CID) {
        ApiProvider.getStoredProcedureTRTMNTQ1(HID, CID)
            .then(data => { this.setState({ TRTMNTQ1: data.data }) })
            .catch(err => { console.log("getStoredProcedureTRTMNTQ1 NG " + JSON.stringify(err)) });
    }

    onChangeClass = (value) => {
        this.setState({ class: value })
    }

    handleChange = (value) => {
        this.setState({ SelectMode: value })
    }

    onChangeinputKey = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    Search_bar = () => {
        // const { RangePicker } = DatePicker;
        const { Option } = Select;

        return (
            <div style={{ display: "grid", gridTemplateColumns: "120px 100px auto", gridColumnGap: "10px" }}>
                <Select defaultValue={this.state.class} style={{ width: 120 }} onChange={this.onChangeClass}>
                    {this.state.parser.mappings.map((mapping, index) => {
                        return <Option key={index} value={mapping.key}>{mapping.key}</Option>
                    })}
                </Select>
                <Select defaultValue={0} style={{ width: 100 }} onChange={this.handleChange}>
                    <Option value={0}>全部狀態</Option>
                    <Option value={1}>生效中</Option>
                    <Option value={2}>已完成</Option>
                    <Option value={3}>已結束</Option>
                </Select>
                <Input placeholder="輸入藥囑關鍵字" value={this.state.InputMode} onChange={this.onChangeinputKey} />
            </div>
        );

    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部類別"] = []

        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部類別"].push(input)
        });
        return allResult;
    }

    onClickSearch = () => {
        function formatTRMINQtatus(TRTMNT01, Status) {
            if (Status === 0) {
                return TRTMNT01
            }
            const filterTRTMNT01 = TRTMNT01.filter(function (info, index, arr) {
                if (Status === 1) {
                    return Number(info.ORSTATUS) === 30 || Number(info.ORSTATUS) === 90
                }
                if (Status === 2) {
                    return Number(info.ORSTATUS) === 60
                }
                return Number(info.ORSTATUS) === 80

            })
            return filterTRTMNT01
        }
        const { TRTMNT01 } = this.props
        const { SelectMode } = this.state
        const parse = this.parse;

        const activeList = formatTRMINQtatus(TRTMNT01, SelectMode)
        console.log("activeList " + JSON.stringify(activeList));

        let clusteredData = {}
        if (this.state.parser !== undefined) {
            clusteredData = parse(activeList, this.state.parser)
        }

        //filter data by selected class
        let filteredData = clusteredData[this.state.class];

        const ListData = this.SortDataList(filteredData)

        return ListData
    }

    SortDataList = (DataList) => {
        const { PickerDateRange, searchbar } = this.props
        const { InputMode } = this.state

        if (InputMode !== '') {
            const regex = new RegExp(InputMode, "i");
            DataList = DataList.filter(data => {
                return data.ORPROCED.match(regex) !== null
            })
        }

        const st = searchbar ? PickerDateRange[0] : PickerDateRange[0]
        const et = searchbar ? PickerDateRange[1] + (24 * 60 * 60 * 1000) : PickerDateRange[1]

        let filterDate = DataList.filter(function (element, index, arr) {
            const startTime = moment(element.ORBGNDT.toString().trim(), 'YYYY-MM-DD').valueOf()
            const endTime = moment(element.ORENDDT.toString().trim(), 'YYYY-MM-DD').valueOf()
            return !(st > endTime || et < startTime)
        })

        if (Object.keys(filterDate).length > 1) {
            filterDate = filterDate.sort((a, b) => {
                let tempTimeA = moment(a.ORBGNDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                let tempTimeB = moment(b.ORBGNDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }
        return filterDate
    }

    render() {
        const { searchbar, caseinfo } = this.props

        const filterData = this.onClickSearch()

        return (
            <div>
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "grid", gridTemplateColumns: searchbar ? "2fr 3fr" : "1fr", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{searchbar ? "治療處置" : "當日處置"}</t2>
                    {searchbar ? this.Search_bar() : null}
                </div>
                <Treatmentordertable caseinfo={caseinfo} TRTMNT01={filterData} searchbar={searchbar}></Treatmentordertable>
            </div>
        );
    }
}

export default Treatmentorderlayout
