import React, { Component } from 'react';
import { DatePicker, Button, Select, Checkbox } from 'antd';
import filterlogo from '../../Image/svg/filter.svg'
import Inoutonchronic from './Chronic_io'
import Chroniclab from './ChronicLab'
import Chronicrt from './ChronicRT'
import Orderonchronic from './ChronicOrder'
import Weightonchronic from './ChronicWeight'
import Vitalxaxis from '../../Module/Vitalxaxis'
import Vitalxaxisbot from '../../Module/Vitalxaxisbot'
import moment from 'moment'
import './Chroniclelayout.scss'
import Vitalonchronic from './Chronic_vital'
import locale from 'antd/es/date-picker/locale/zh_TW';
import * as WardState from '../../Function/Wardstate'

class Chroniclelayout extends Component {
    state = {
        display_Mode: false,
        Cross_item: [0, 1, 2, 4],
        scrollstate: 0,
        PickerDate: moment(moment().format('YYYYMMDD'), "YYYYMMDD").valueOf(), //選擇日期
        select_interval: 0, //選擇區間
        statuschange: 0,
    }
    onChangeCross = (checkedValues) => {
        this.setState({ Cross_item: checkedValues })
    }

    open_display() {
        this.setState({
            display_Mode: !this.state.display_Mode
        })
    }
    scrollToAnchor = (anchorName) => {
        if (anchorName) {
            let anchorElement = document.getElementById(anchorName);
            if (anchorElement) { anchorElement.scrollIntoView(); }
        }
    }
    scroll_btn_style(index) {
        if (index === this.state.scrollstate) {
            return true
        }
        else return false
    }
    onChange = (date, dateString) => {

        this.setState(
            {
                PickerDate: date.valueOf()
            }
        )
    }
    onSelectchange = (value) => {this.setState({select_interval: value})}

    trainDay = (e) => {
        const { caseinfo } = this.props
        const { PickerDate } = this.state
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                const bTemp = moment(moment(bTime).format('YYYYMMDD')).valueOf()
                bronDate <= bTemp && this.onChange(moment(bTime))
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.onChange(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.onChange(moment(aTime))
                break;
            default:
                break;
        }
    }

    display_item(number) {
        const item = this.state.Cross_item
        var filter = item.some(function (item, index, array) {
            return item === number
        });
        return filter
    }

    render() {
        const { Option } = Select;
        // const dateFormat = 'YYYY/MM/DD';
        const { caseinfo, status } = this.props
        // const { PickerDate } = this.state
        const crosssectioncontrollist =
            [{ "name": "生命徵象", "scrollpoint": "vital_sign_chronic" },
            { "name": "輸出入", "scrollpoint": "input_output_chronic" },
            { "name": "檢驗", "scrollpoint": "lab_chronic" },
            { "name": "藥物處方", "scrollpoint": "order_chronic" }]

        const btn_style = {
            unclick_style: { borderRadius: "4px", color: "#001a6d88", border: "1px solid #001a6d88", padding: "0px 4px", height: "30px" }
        }

        let button = crosssectioncontrollist.map(
            (item, index) => <Button onClick={this.scrollToAnchor.bind(this, item.scrollpoint)} key={index} style={btn_style.unclick_style}>
                <t3>{item.name}</t3>
            </Button>);

        const display_style = {
            displye_mode: { position: "absolute", right: '0px', top: "50px", width: "200px", background: "white", boxShadow: "3px 3px 12px", zIndex: "100", borderRadius: "4px" },
            non_display_mode: { display: "none" },
            check_area: { display: "flex", justifyContent: "center", alignItems: "center" },
            check_text: { padding: "4px", width: "140px", borderRadius: "4px", borderColor: 'rgba(0, 0, 0, 0.15)', borderStyle: "solid", borderWidth: "1px", marginLeft: "10px", borderRadius: "4px" }
        }
        return (
            <div style={{ paddingLeft: "20px", paddingRight: "20px" }}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", height: "40px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "140px 120px", gridColumnGap: "5px" }}>
                        <DatePicker getCalendarContainer={triggerNode => triggerNode.parentNode} locale={locale} disabledDate={(current) => { return current > moment().startOf("day") || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }} onChange={this.onChange} value={moment(this.state.PickerDate)} allowClear={false} />
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px", marginLeft: "10px" }}>
                            <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                            <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                            <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                        </div>
                    </div>
                    <div style={{ fontSize: "16px" }}>
                        當日年齡: {WardState.bornstatusFormat(status.week, status.day)} <spam style={{ fontWeight: "600", color: "black" }}>{WardState.CalculateAgeFromPickerDate(status, this.state.PickerDate)}</spam>
                    </div>
                    <div style={{ fontSize: "16px" }}>
                        當日體重: {WardState.valueFormat(status.BornWeight)}<spam style={{ fontWeight: "600", color: "black" }}>{WardState.WeightListFilter(caseinfo.WEIGHT, this.state.PickerDate)}g</spam>
                    </div>
                    <div style={{ display: "flex", alignItems: "center", position: "relative" }}>
                        <t2 >快速切換：</t2>
                        <div style={{ display: "grid", gridTemplateColumns: "repeat(6,auto)", gridColumnGap: "5px" }}>{button}</div>
                        <div onClick={() => this.open_display()} style={{ cursor: "pointer", marginLeft: "5px" }}>
                            <img src={filterlogo} alt='filterlogo' style={{ width: '15px', height: '15px' }}></img>
                        </div>
                        <div style={this.state.display_Mode ? display_style.displye_mode : display_style.non_display_mode}>
                            <t2 className='controlmoduletitle'>顯示切換</t2>
                            <div style={{ marginTop: "10px", marginBottom: "20px" }}>
                                <Checkbox.Group defaultValue={this.state.Cross_item} id={"checkbox_group"} style={{ width: '100%', display: "grid", gridTemplateRows: "repeat(4,1fr)", gridRowGap: "5px" }} onChange={this.onChangeCross}>
                                    <div style={display_style.check_area}>
                                        <Checkbox value={0}></Checkbox>
                                        <t3 style={display_style.check_text}>生命徵象</t3>
                                    </div>
                                    {/* <div style={display_style.check_area}>
                                        <Checkbox value={3}></Checkbox>
                                        <t3 style={display_style.check_text}>呼吸治療設定</t3>
                                    </div> */}
                                    <div style={display_style.check_area}>
                                        <Checkbox value={1} ></Checkbox>
                                        <t3 style={display_style.check_text}>輸出入</t3>
                                    </div>
                                    {/* <div style={display_style.check_area}>
                                        <Checkbox value={5}></Checkbox>
                                        <t3 style={display_style.check_text}>體重</t3>
                                    </div> */}
                                    <div style={display_style.check_area}>
                                        <Checkbox value={2}></Checkbox>
                                        <t3 style={display_style.check_text}>檢驗</t3>
                                    </div>
                                    <div style={display_style.check_area}>
                                        <Checkbox value={4}></Checkbox>
                                        <t3 style={display_style.check_text}>藥物處方</t3>
                                    </div>
                                </Checkbox.Group>
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "150px 1fr", height: "10px", marginTop: "10px" }}>
                    <div></div>
                    <Vitalxaxisbot max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_bot_chronic" svg_id="xaxis_bot_svg_chronic"></Vitalxaxisbot>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px", margin: "10px 0px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_chronic" svg_id="xaxis_svg_chronic"></Vitalxaxis>
                </div>
                <div className='boxshadow'></div>
                <div className='chroniclayout'>
                    {/* Vital sign */}
                    <div id="vital_sign_chronic" style={{ display: this.display_item(0) ? "block" : "none" }}>
                        <Vitalonchronic source={"Chronic"} PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="Vital" onSelectchange={this.onSelectchange}></Vitalonchronic>
                        <Chronicrt PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo}></Chronicrt>
                    </div>
                    {/* RT */}
                    {/* <div id="rt_chronic" style={{ display: this.display_item(3) ? "block" : "none" }}>
                        <Chronicrt PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo}></Chronicrt>
                    </div> */}
                    {/* I/O */}
                    <div id="input_output_chronic" style={{ display: this.display_item(1) ? "block" : "none" }}>
                        <Inoutonchronic PickerDate={this.state.PickerDate} Status={"Chronic"} IntervalStatus={this.state.select_interval} caseinfo={caseinfo} title="輸出入"></Inoutonchronic>
                        <Weightonchronic PickerDate={this.state.PickerDate} QTWPER={this.state.QTWPER} select_interval={this.state.select_interval} caseinfo={caseinfo} title=""></Weightonchronic>
                    </div>
                    {/* 體重 */}
                    {/* <div id="infection_chronic" style={{ display: this.display_item(5) ? "block" : "none" }}>
                    </div> */}
                    {/* Lab */}
                    <div id="lab_chronic" style={{ display: this.display_item(2) ? "block" : "none" }}>
                        <Chroniclab PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="檢驗"></Chroniclab>
                    </div>
                    {/* Order */}
                    <div id="order_chronic" style={{ display: this.display_item(4) ? "block" : "none" }}>
                        <Orderonchronic PickerDate={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="藥物處方"></Orderonchronic>
                    </div>
                </div>
            </div>
        );
    }
}

export default Chroniclelayout;
