import React, { Component, cloneElement } from 'react';
import { Button, Icon } from 'antd';
import { Redirect } from 'react-router-dom'
import { Input } from 'antd'
import * as ApiProvider from '../API/ApiProvider';
import logo from '../Image/Logo/1200px-Taipei_Veterans_General_Hospital_Emblem.svg.png'


class Loginview extends Component {

    constructor(props) {
        super(props);
        this.state = {
            redirect: false,
            permission: true,
            statusstring: null
        }
        this.setRedirect = this.setRedirect.bind(this);   //把元件中的this繫結到handleClick方法中，這樣就會保持this一致          
    };

    componentDidMount() {
        const account = document.querySelector('#account')
        const password = document.querySelector('#password')
        account.addEventListener('focus', () => {
            account.addEventListener('keyup', e => {
                if (e.keyCode === 13) {
                    account.blur();
                    password.focus();
                }
            })
        })

        password.addEventListener('focus', () => {
            password.addEventListener('keyup', e => {
                e.keyCode === 13 && this.setRedirect()
            })
        })
    }

    setRedirect = () => {
        const account = document.getElementById("account").value;
        const password = document.getElementById("password").value;
        ApiProvider.login(account, password)
            .then(result => {
                console.log("login OK " + JSON.stringify(result));
                const userData = result.data
                const user = account
                const role = userData[0].role
                sessionStorage.setItem('user', user);
                sessionStorage.setItem('role', role);
                this.setState({
                    redirect: true
                })
            })
            .catch(err => {
                console.log("login NG " + JSON.stringify(err));
                this.setState({
                    permission: false,
                    statusstring: "帳號或密碼錯誤"
                })
            });
    }
    renderRedirect = () => {
        const login = sessionStorage.getItem('user');
        const role = sessionStorage.getItem('role');
        if (login !== null) {
            if (Number(role) === 2) {
                return <Redirect from={"/login"} to={"/nicu/system"} />
            }
            return <Redirect from={"/login"} to={"/nicu/ward/wardindex"} />
        }
    }


    render() {
        return (
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100vh", width: "100vw", background: 'linear-gradient(to bottom, rgba(195, 239, 255, 1), rgba(46, 100, 164, 1))' }}>
                {this.renderRedirect()}
                <div style={{ textAlign: "center" }}>
                    <img style={{ width: "200px" }} src={logo} alt='logo'></img>
                    <div style={{ display: 'flex', justifyContent: "center", alignItems: "center", flexWrap: "wrap" }}>
                        <div style={{ color: "white", fontSize: "20px", textAlign: "center", width: "100%" }}>台北榮民總醫院 NICU智慧醫療系統</div>
                        <div style={{ width: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div style={{ width: "350px", height: "250px", borderRadius: "4px", backgroundColor: "white", textAlign: "center", padding: '25px', position: "relative" }}>
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                    <div style={{ height: "32px", width: "32px", borderRadius: "2px", backgroundColor: "rgba(46,100,164,1)", color: "white", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                        <Icon type="user" style={{ color: "white" }} />
                                    </div>
                                    <Input id="account" style={{ marginLeft: "20px" }} placeholder="請輸入3270主機帳號"></Input>
                                </div>
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "25px" }}>
                                    <div style={{ height: "32px", width: "32px", borderRadius: "2px", backgroundColor: "rgba(46,100,164,1)", color: "white", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                        <Icon type="lock" style={{ color: "white" }} />
                                    </div>
                                    <Input type="password" id="password" style={{ marginLeft: "20px" }} placeholder="請輸入密碼"></Input>
                                </div>
                                <Button style={{ width: "280px", backgroundColor: "rgba(46, 100, 164, 1)", color: "white", marginTop: "70px" }} onClick={this.setRedirect}>登入</Button>
                                <div style={{ display: this.state.permission ? "none" : "block", color: "red", fontSize: "1rem" }}>{this.state.statusstring}</div>
                            </div>
                        </div>
                        <div style={{ width: "100%", justifyContent: "center", alignItems: "center", display: "flex" }}>200310.007-T</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Loginview
