import React, { Component } from 'react';
import Prescriptionordertablecell from './Prescriptionordertablecell'
import './Prescriptionordertable.scss'


class Prescriptionordertable extends Component {
    render() {
        const { UDORDER0, caseinfo, SelectMode, searchbar } = this.props

        const title = ["時間起迄", "學名", "用法", "狀態"]
        const titlecell = title.map(function (value, index, arr) {
            return (
                <t3 className='title'>{value}</t3>
            );
        })
        const Prescriptionordertabletable = UDORDER0.map(
            (value, index, array) =>
                <Prescriptionordertablecell caseinfo={caseinfo} key={index} value={value} SelectMode={SelectMode}></Prescriptionordertablecell>
        )
        return (
            <div>
                <div className='Prescriptionordertablelayout'>
                    {titlecell}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: searchbar ? "calc(100vh - 300px)" : "120px", maxHeight: searchbar ? "calc(100vh - 300px)" : "400px", overflowY: "auto" }}>
                        {JSON.stringify(UDORDER0) !== '[]' ?
                            Prescriptionordertabletable
                            :
                            <div style={{ display: "grid", gridTemplateColumns: "180px 1fr 140px 70px" }}>
                                <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                    {searchbar ? "無資料" : "當日無資料"}
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </div>
        );
    }
}

export default Prescriptionordertable
