import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//Component
import * as Wardstate from '../../Function/Wardstate'
import * as Wardcardvalue from '../WardCardvalue-k'
import { bednumberformat, WardCardgender } from '../../Function/Wardbedfunction'
import './Wardcarddetail.scss'
//image
import editlogo from '../../Image/svg/edit2.svg'
import fileLogo from '../../Image/svg/file1.svg'
import file2Logo from '../../Image/svg/file2.svg'
//antdesign
import { Modal, InputNumber, Button } from 'antd';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment';

var BornChangeWeek = 0
var BornChangeDay = 0
var BornChangeWeight = 0
class Wardcarddetail extends Component {
    state = {
        hoverstate: "none",
        clickstate: false,
        mouse_hover: false,
        edit_hover: false,
        visible: false,
        pbasignfo: null,
        birthdayweight: "N",
        weight: 0,
        bornweek: 0,
        bornday: 0,
        nowweight: 0,
        report: 0,
        nonreport: 0,
        remark: "",
        TRANSINID: "",
        TRANSINDT: "",
        TRANSINTM: "",

        modatstatus: true,
        persionstatus: []
    };

    componentDidMount() {
        const { data } = this.props
        console.log(data)
        const HID = data.PHISTNUM
        const date = moment(data.PBIRHDT, 'YYYYMMDD')
        const TRID = data.TRANSINID
        this.API_DATA(HID, date, TRID)
    }

    API_DATA(HID, date, TRID) {
        ApiProvider.getStoredProcedureORDLIST(HID, date.format('YYYY-MM-DD'), "ALL", "DOC3924B")
            .then(data => {
                const filter = data.data.filter(function (info, index, arr) {
                    const time = moment(info.ORBGNDT.toString().trim() + info.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    return info.ORSTATUS === "31" && time > moment().valueOf() - (24 * 60 * 60 * 1000) && time < moment().valueOf()
                })
                // console.log(filter)
                const Reportfilter = data.data.filter(function (info, index, arr) {
                    const time = moment(info.ORBGNDT.toString().trim() + info.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    return info.ORSTATUS === "64" && time > moment().valueOf() - (24 * 60 * 60 * 1000) && time < moment().valueOf()
                })
                // console.log("HID = " + HID)
                // console.log("64 = " + Reportfilter.length)
                // console.log("31 = " + filter.length)
                this.setState({ nonreport: filter.length, report: Reportfilter.length })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err));
            });
        ApiProvider.getOnePatientRemark(TRID)
            .then(data => {
                this.setState({
                    remark: data.data[0].context
                })
            })
            .catch(err => {
                console.log("Andy test getOnePatientRemark NG " + JSON.stringify(err));
            })
        ApiProvider.getPersionalStatus(HID)
            .then(data => { console.log(data.data); this.setState({ persionstatus: data.data }) })
            .catch(err => { console.log(err) })
    }

    //Module show
    handleOk(HID, name, sex) {
        const week = Number(BornChangeWeek)
        const day = Number(BornChangeDay)
        const weight = parseFloat(Number(BornChangeWeight) / 1000).toFixed(3)
        if (weight === undefined || weight === 0 || weight === null || week === 0 || day === 0 || week === undefined || week === null || day === null || day === undefined) {
            this.setState({ modatstatus: false })
        }
        else if (Number(day) > 6) {
            this.setState({ modatstatus: false })
        }
        else {
            ApiProvider.createPatientInfo(HID, name, sex, weight, week, day)
                .then(data => {
                    console.log(data.data);
                    ApiProvider.getPersionalStatus(HID)
                        .then(data => { console.log(data.data); this.setState({ persionstatus: data.data, visible: false, modatstatus: true }) })
                        .catch(err => { console.log(err) })
                })
                .catch(err => { console.log(err) })
        }
    };

    handleCancel = e => {
        this.setState({
            visible: false,
            modatstatus: true
        });
    };
    showModal = () => { this.setState({ visible: true }); };

    Modelrender(personstatus) {
        function typefrom(from) {
            if (from === 'Custom') {
                return true
            }
            return false
        }
        function BornWeekAndDay(BornWeeks, BornDays) {
            if (BornWeeks === null || BornWeeks === undefined || BornWeeks === "") {
                return []
            }
            return [BornWeeks, BornDays]
        }
        const { data } = this.props
        const source = data
        const HID = source.PHISTNUM.toString().trim()
        const name = source.PNAMEC.toString().trim()
        const sex = source.PSEX.toString().trim()
        const Birthday = source.PBIRHDT
        const BornWeight = personstatus.BornWeight
        const BornWeeks = personstatus.BornWeeks
        const BornWdays = personstatus.BornDays
        const BornStatus = BornWeekAndDay(BornWeeks, BornWdays)
        const viewstatus = this.state.modatstatus
        const dataArray = [
            { item: "病床號碼:", value: bednumberformat(parseInt(source.HBEDNO)) },
            { item: "病歷號碼:", value: source.PHISTNUM.toString().trim() },
            { item: "出生週數:", value: BornStatus },
            { item: "生日:", value: moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD') },
            { item: "出生體重:", value: BornWeight }
        ]
        const dataArrayRender = dataArray.map(function (info, index, arr) {
            if (info.item === "出生體重:") {
                return (
                    <div key={index} >
                        <div key={index} style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                {info.item}
                            </div>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                <InputNumber defaultValue={info.value} onChange={(value) => { BornChangeWeight = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>g</div>
                            </div>
                        </div>
                        <div style={{ display: viewstatus ? "none" : "block", color: "red" }}>輸入錯誤</div>
                    </div>
                );
            }
            if (info.item === "出生週數:") {
                // console.log(info)
                return (
                    <div key={index}>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                {info.item}
                            </div>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                <InputNumber defaultValue={info.value[0]} onChange={(value) => { BornChangeWeek = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>週</div>
                                <InputNumber defaultValue={info.value[1]} onChange={(value) => { BornChangeDay = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>日</div>
                            </div>
                        </div>
                        <div style={{ display: viewstatus ? "none" : "block", color: "red" }}>輸入錯誤</div>
                    </div>
                );
            }
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                    <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                        {info.item}
                    </div>
                    <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                        {info.value}
                    </div>
                </div>
            );
        })
        return (
            <Modal
                width={400}
                title="填寫病床資訊"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={
                    <div style={{ display: "flex", justifyContent: typefrom(source.type) ? 'space-between' : 'flex-end', alignItems: "center" }}>
                        {/* <Button key="delete" style={typefrom(source.type) ? { border: "1px solid rgba(208,2,27,1)", color: "rgba(208,2,27,1)" } : { display: "none" }} onClick={this.handleCancel}>清床</Button> */}
                        <div>
                            <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                            <Button key="ok" type="primary" onClick={() => { this.handleOk(HID, name, sex) }}>儲存</Button>
                        </div>
                    </div>}>
                {dataArrayRender}
            </Modal>
        );
    }
    BornStatusRender(Week, Day) {
        console.log(Week, Day)
        if (Week === null || Week === undefined) {
            return null
        }
        if (Day === null || Day === undefined) {
            return null
        }
        return Week.toString() + "+" + Day.toString() + ">"
    }
    render() {
        function valueFormat(value) {
            if (value === "" || value === null || value === undefined) {
                return "--"
            }
            return value.toString()
        }
        function valueBornWeight(value) {
            if (value === "" || value === null || value === undefined || value === 0) {
                return ""
            }
            return value.toString() + "g > "
        }
        function statusPersion(persionstatus, QMNC, QTWER, Birthday) {
            console.log(persionstatus)
            // console.log(QMNC)
            if (persionstatus.length === 0) {
                console.log(QMNC)
                // console.log("000" + Number(QMNC.weeks))
                if (QMNC.length === 0) {
                    return {
                        BornWeight: undefined,
                        BornWeeks: undefined,
                        BornDays: undefined
                    }
                }
                const BornWeight = Wardstate.BornWeight(QTWER, Birthday)
                const BornWeeks = Number(QMNC.weeks)
                const BornDays = Number(QMNC.days)
                return {
                    BornWeight: BornWeight,
                    BornWeeks: BornWeeks,
                    BornDays: BornDays
                }
            }
            else {
                const Weight = Number(persionstatus[0].bweight) * 1000
                const BornWeeks = Number(persionstatus[0].bweek)
                const BornDays = Number(persionstatus[0].bday)
                return {
                    BornWeight: Weight,
                    BornWeeks: BornWeeks,
                    BornDays: BornDays
                }
            }
        }
        const { data, previewmode, hbedlist } = this.props
        const { persionstatus } = this.state
        const source = data
        const Birthday = source.PBIRHDT
        const QTWER = Wardstate.XM2JSON(source.QTWPER)
        const QMNC = Wardstate.QMNCXM2JSON(source.QMNC)
        const Weight = Wardstate.NewestWeight(QTWER)
        const BornWeight = statusPersion(persionstatus, QMNC, QTWER, Birthday).BornWeight
        const BornWeeks = statusPersion(persionstatus, QMNC, QTWER, Birthday).BornWeeks
        // const BornWeeks = undefined
        const BornDays = statusPersion(persionstatus, QMNC, QTWER, Birthday).BornDays
        // const BornDays = undefined
        const remark = this.state.remark
        if (source === undefined) { return null }
        const status = {
            date: moment(Birthday, "YYYYMMDD").valueOf(),
            week: BornWeeks,
            day: BornDays
        }
        const AgeStatus = Wardstate.CalculateAge(status)
        const role = Number(sessionStorage.getItem('role'))

        return (
            <div>
                <div className='wardcarddetailtitle'>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>NI-{bednumberformat(Number(source.HBEDNO))}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "16px", fontWeight: "600" }}>{source.PNAMEC.toString().trim()}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>{source.PHISTNUM}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{WardCardgender(source.PSEX)}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{moment(Birthday, 'YYYYMMDD').format('MM/DD')}生</t3>
                    {role == 1 && <div onClick={this.showModal} className='wardcarddetileditbutton' style={{ cursor: "pointer" }}>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>}
                </div>
                <div>
                    <div className='wardinfoweeks'>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {this.BornStatusRender(BornWeeks, BornDays)}<span style={{ fontWeight: "900" }}>{AgeStatus}</span>
                        </t3>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {valueBornWeight(BornWeight)}<span style={{ fontWeight: "900" }}>{valueFormat(Weight)}g</span>
                        </t3>
                        <div className='wardinforeportgroup'>
                            <img src={fileLogo} alt='fileLogo'></img>
                            <div style={{ fontSize: "12px" }}>{this.state.report}</div>
                            <img src={file2Logo} alt='file2Logo'></img>
                            <div style={{ fontSize: "12px" }}>{this.state.nonreport}</div>
                        </div>
                    </div>
                    <div style={{ display: previewmode ? "block" : "none", borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <Wardcardvalue.WardCard centerdata={source.centerMonitorData} previewmode={previewmode} PHISTNUM={source.PHISTNUM} PCASENO={source.PCASENO}></Wardcardvalue.WardCard>
                    </div>
                    <div style={{ height: '30px', display: 'grid', justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", position: 'relative', gridTemplateColumns: "2fr 1fr" }}>
                        <div style={{ overflow: "hidden", fontSize: "12px", textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{remark}</div>
                        <Link className='patientviewbutton' to={{ pathname: "/nicu/patient/" + source.PHISTNUM.toString().trim(), state: { "source": hbedlist, "HISID": source.PHISTNUM, "CASEID": source.PCASENO, "BEDNUMBER": source.HBEDNO, "TYPE": true, "TRANSINID": this.state.TRANSINID } }}>
                            <centerstyle>
                                <div style={{ fontSize: "12px" }}>進入病人資料頁面</div>
                            </centerstyle>
                        </Link>
                    </div>
                </div>
                {this.Modelrender(statusPersion(persionstatus, QMNC, QTWER, Birthday))}
            </div>
        );
    }
}

export default Wardcarddetail;
