import React, { Component } from 'react'
import './Infectionlabtable.scss'
// import { chronicladdataoneday, chroniclabdatathird } from '../../Data/Wardviewdata'
// import { arraychange } from '../../Function/Wardstate'

class Infectionlabtable extends Component {
    switch_state() {
        const chronic_state = 2
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 18
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    table_content() {
        function FormatList(Datalist, st) {
            var Render = []
            for (let index = 0; index < 21; index++) {
                const start = st + (index * 8 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 8 * 60 * 60 * 1000)
                const FilterData = Datalist.filter(function (value, index, arr) {
                    return value.Time > start && value.Time < end
                })
                var sum = 0
                for (let index = 0; index < FilterData.length; index++) {
                    const element = FilterData[index];
                    sum = sum + Number(element.Data)
                }
                Render.push(sum)
            }
            return Render
        }
        function CellRander(List) {
            function checkLineColor(lineColor) {
                if (lineColor === null) {
                    return '';
                } else {
                    return `${lineColor.width} solid ${lineColor.color}`;
                }
            }
            function valueFormat(value) {
                if (value === 0 || value === undefined || value === null || value === "") {
                    return ""
                }
                return `${value}`
            }
            function TimeLine(interval, index) {
                const lineStyle = {
                    width: '',
                    color: '',
                }
                if (interval === 0) {
                    if (index === 0 || (index % 24) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else if (index % 8 === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                    else {
                        lineStyle.width = '1px';
                        lineStyle.color = '#eeeeee';
                        return lineStyle;
                    }
                }
                if (interval === 1) {
                    if (index === 0 || (index % 6) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else if (index % 2 === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                    else {
                        lineStyle.width = '1px';
                        lineStyle.color = '#eeeeee';
                        return lineStyle;
                    }
                }
                if (interval === 2) {
                    if (index === 0 || (index % 3) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                }
                if (interval === 3) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                }
            }
            const cellR = List.map(function (info, index) {
                const lineColoe = TimeLine(3, index)
                if (info === 0) {
                    return (
                        <div style={{ height: '40px', display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: checkLineColor(lineColoe) }}></div>
                    )
                }
                return (
                    <div style={{ backgroundColor: 'rgba(0,120,230,.1)', height: '40px', fontSize: "16px", display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', cursor: "pointer", borderLeft: checkLineColor(lineColoe) }}>
                        <t2>{valueFormat(info)}</t2>
                    </div>
                );
            })
            return cellR
        }
        const { PickerDate, WBCList, BandList, SegList, LymList, PCTList, CRPList, title } = this.props
        const st = PickerDate + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
        var WBCRender = FormatList(WBCList, st)
        var BandRender = FormatList(BandList, st)
        var SegRender = FormatList(SegList, st)
        var LymRender = FormatList(LymList, st)
        var PCTRender = FormatList(PCTList, st)
        var CRPRender = FormatList(CRPList, st)
        const WBCcell = CellRander(WBCRender)
        const Bandcell = CellRander(BandRender)
        const Segcell = CellRander(SegRender)
        const Lymcell = CellRander(LymRender)
        const PCTcell = CellRander(PCTRender)
        const CRPcell = CellRander(CRPRender)
        return (
            <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{WBCcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Bandcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Segcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Lymcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{PCTcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{CRPcell}</div>
            </div>
        );
    }
    render() {
        const { userdata, title } = this.props
        const title_list = title.map((info, index) => <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "40px", border: "1px solid rgba(238,238,238,1)", borderCollapse: "collapse", backgroundColor: "#eeeeee" }} key={index}><t2>{info.title}</t2></div>)
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {title_list}
                </div>
                {this.table_content()}
            </div>
        );
    }
}

export default Infectionlabtable
