import React, { Component } from 'react';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

class RTtable extends Component {
    state = {
        RESDBGAS: [],
        PreHID: null,
        PreTransInDate: null,
    }
    componentDidMount() {
        const HID = this.props.caseinfo.HISID
        const TRDT = this.props.caseinfo.TRANSINDT
        this.setState({ PreHID: HID, PreTransInDate: TRDT })
        this.getGasAPI()
    }
    componentDidUpdate() {
        const HID = this.props.caseinfo.HISID
        const TRDT = this.props.caseinfo.TRANSINDT
        const PreHID = this.state.PreHID
        const PreTRDT = this.state.PreTransInDate
        if (HID !== PreHID || TRDT !== PreTRDT) {
            this.setState({ PreHID: HID, PreTransInDate: TRDT })
            this.getGasAPI()
        }
    }
    getGasAPI() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const TransInDate = moment(caseinfo.TRANSINDT, "YYYYMMDD").format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESDBGAS(HID, TransInDate)
            .then(data => { this.setState({ RESDBGAS: data.data }) })
            .catch(err => {
                console.log("getStoredProcedureRESDBGAS NG " + JSON.stringify(err));
            });
    }
    GasListToCell = () => {

        function formatGasData(GasDataList, st, et) {
            let filterTimer = GasDataList.filter(function (element, index, arr) {
                const timeStamp = moment(element.ORRESDT.toString().trim() + element.ORRESTM.toString().trim(), 'YYYY-MM-DDHH.mm.ss').valueOf()
                return st <= timeStamp && timeStamp <= et
            })

            if (Object.keys(filterTimer).length > 1) {
                filterTimer = filterTimer.sort((a, b) => {
                    let tempTimeA = moment(a.ORRESDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                    let tempTimeB = moment(b.ORRESDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                    return tempTimeA < tempTimeB ? 1 : -1
                })
            }

            const decideStyle = {
                "1": { "min": 6.5, "max": 7.7 },
                "2": { "min": 0, "max": 100 },
                "3": { "min": 0, "max": 200 },
                "4": { "min": 0, "max": 40 },
                "5": { "min": -30, "max": 20 },
                "6": { "min": 0, "max": 100 },
                "7": { "min": 0, "max": 100 }
            }

            const cellMap = filterTimer.map(function (element, index, arr) {
                const PHValue = element.SS07
                const PCO2Value = element.SS06
                const PO2Value = element.SS08
                const HCO3Value = element.SS04
                const BEValue = element.SS01
                const O2SatValue = element.SS05
                const FiO2Value = element.SS03
                const timeStamp = moment(element.ORRESDT.toString().trim() + element.ORRESTM.toString().trim(), 'YYYY-MM-DDHH.mm.ss').valueOf()
                const time = moment(timeStamp).format("MM/DD HH:mm")
                const reportSample = 'link'
                const gasDataArray = [time, PHValue, PCO2Value, PO2Value, HCO3Value, BEValue, O2SatValue, FiO2Value, reportSample]
                let isSameDay = false;
                if (index < filterTimer.length - 1) {
                    const day = moment(timeStamp).format('YYYY/MM/DD');
                    const compareDay = moment(filterTimer[index + 1].ORRESDT.toString().trim() + filterTimer[index + 1].ORRESTM.toString().trim(), 'YYYY-MM-DDHH.mm.ss').format('YYYY/MM/DD')
                    isSameDay = moment(day).isSame(compareDay, 'day');

                }
                if (index === filterTimer.length - 1) {
                    isSameDay = true;
                }

                const GasCell = gasDataArray.map(function (value, index, array) {
                    let tempStyle = false
                    const tempValue = parseFloat(value)
                    // console.log(index.toString())
                    if (index != 0 && index != 8 && (tempValue > decideStyle[index.toString()].max || tempValue < decideStyle[index.toString()].min) || isNaN(tempValue) == true) {
                        tempStyle = true;
                    }
                    return (
                        <centerstyle key={index} style={{ borderLeft: index !== 0 ? "rgba(238,238,238,1) 1px solid" : "", borderBottom: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t2 style={{ color: tempStyle ? "red" : index !== 0 ? "black" : "auto", fontWeight: index !== 0 ? 600 : '' }}>{value}</t2>
                        </centerstyle>
                    );
                })

                return (
                    <div key={index} style={{ height: "40px", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)", borderBottom: isSameDay ? "rgba(238, 238, 238, 1) 1px solid" : '#989898 1px solid' }}>{GasCell}</div>
                );
            })
            return cellMap

        }
        const { Timer } = this.props
        const startTime = Timer[0]
        const endTime = Timer[1]
        const GasData = this.state.RESDBGAS
        const CellRander = formatGasData(GasData, startTime, endTime)

        return CellRander
    }

    render() {
        const { searchbar } = this.props
        const GasTitleList = ['時間', 'pH', `PCO2`, 'PO2', 'HCO3', 'BE', 'O2SAT', 'FiO2', '報告']
        const GasTitleCell = GasTitleList.map(function (value, index, array) {
            function formatString(value) {
                // console.log(value)
                if (value === "O2SAT") {
                    return <div style={{ fontSize: "14px" }}>O<sub>2</sub>SaT</div>
                }
                if (value === "PCO2") {
                    return <div style={{ fontSize: "14px" }}>PCO<sub>2</sub></div>
                }
                if (value === "PO2") {
                    return <div style={{ fontSize: "14px" }}>PO<sub>2</sub></div>
                }
                if (value === "HCO3") {
                    return <div style={{ fontSize: "14px" }}>HCO<sub>2</sub></div>
                }
                if (value === "FiO2") {
                    return <div style={{ fontSize: "14px" }}>FiO<sub>2</sub></div>
                }
                return value
            }
            return (
                <centerstyle key={index} style={{ padding: "0px 5px", display: "flex", alignItems: "center", borderCollapse: "collapse" }}>
                    <div style={{ fontSize: "14px" }}>{formatString(value)}</div>
                </centerstyle>
            );
        })
        const GasListToCell = this.GasListToCell()

        return (
            <div>
                <div style={{ justifyItems: "center", height: "30px", backgroundColor: "rgba(226, 237, 243,.5)", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                    {GasTitleCell}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: searchbar ? "calc(100vh - 305px)" : "120px", maxHeight: searchbar ? "calc(100vh - 300px)" : "400px", overflowY: "auto" }}>
                        {GasListToCell.length !== 0 ?
                            GasListToCell
                            :
                            <div style={{ display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                                <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                    無資料
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </div>
        );
    }
}

export default RTtable
