import React, { Component } from 'react';
import detaillogo from '../Image/svg/details.svg'
import editlogo from '../Image/svg/Edit.svg'
import { Tooltip, Input, Button } from 'antd';
import './Schedule_event.scss'
import * as ApiProvider from '../API/ApiProvider';
//資料
// import { eventdata } from '../jsonResponse'
import moment from 'moment';

const parseBedNumber = (bedNumber) => {
    if (Number.isInteger(bedNumber)) {
        return bedNumber > 9 ? bedNumber : `0${bedNumber.toString()}`;
    }

    return '00'
}

class Schedule_event_list extends Component {
    state = {
        detailstate: "none",
        // pressitemstate: "all",
        items_interval: 0,
        powerLogin: 0,
        EditMode: false,
        inputContent: '',
        selSchedule: '排程',

        Scheduledata: [],
        status: false,
        PickerDate: moment().valueOf(),
        TRANSINID: null
    }

    querySchedule(time, ID) {
        const timeDate = moment(time).format("YYYY-MM-DD")
        // const et = moment(moment(time).format("YYYY/MM/DD"), "YYYY/MM/DD").valueOf() + (24 * 60 * 60 * 1000)
        // console.log(ID, st, et)
        ApiProvider.getOnePatientSchedule(ID, timeDate)
            .then(data => {
                console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));
                this.setState({ Scheduledata: data.data })
            })
            .catch(err => {
                console.log("getOnePatientSchedule NG " + JSON.stringify(err));
            });
    }

    queryALLSchedule() {
        // ApiProvider.getAllSchedule()
        //     .then(data => {
        //         console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));
        //         this.setState({ Scheduledata: data.data })
        //     })
        //     .catch(err => {
        //         console.log("getOnePatientSchedule NG " + JSON.stringify(err));
        //     });
        const date = this.props.PickerDate
        const selectdate = moment(date).format("YYYY-MM-DD")
        console.log(selectdate)
        ApiProvider.getAllPatientFromDate(selectdate)
            .then(data => {
                console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));
                this.setState({ Scheduledata: data.data })
            })
            .catch(err => {
                console.log("getAllPatientFromDate NG " + JSON.stringify(err));
            })
    }
    componentDidMount() {
        this.onInputChange = this.onInputChange.bind(this);

        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        const source = this.props.source
        if (source === 1) {
            const ID = this.props.caseinfo.TRANSINID
            const date = this.props.PickerDate
            console.log(this.props.caseinfo)
            if (ID === null) {

            }
            else {
                this.setState({
                    PickerDate: date,
                    TRANSINID: ID
                })
                this.querySchedule(date, ID)
            }
        }
        else {
            //get ALL
            this.queryALLSchedule()
        }

    }

    componentDidUpdate() {
        const source = this.props.source
        if (this.state.status) {
            this.setState({ status: false })
            if (source === 1) {
                const ID = this.props.caseinfo.TRANSINID
                this.querySchedule(this.state.PickerDate, ID)
            }
            else {
                //get ALL
                this.queryALLSchedule()
            }
        }
        const propsDate = this.props.PickerDate
        const stateDate = this.state.PickerDate
        if (propsDate !== stateDate) {
            this.setState({ PickerDate: propsDate })
            if (source === 1) {
                const ID = this.props.caseinfo.TRANSINID
                console.log(moment(propsDate).format("YYYYMMDDHHmmss"))
                console.log(moment(stateDate).format("YYYYMMDDHHmmss"))
                this.querySchedule(propsDate, ID)
            }
            else {
                //get ALL
            }
        }
    }
    selSchedule(value) {
        this.setState({ selSchedule: value })
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }
    onClickEnter() {
        console.log(this.state.PickerDate)
        const timeNowStr = new Date().toISOString()
        console.log(timeNowStr)
        const writter = sessionStorage.getItem("user")
        const name = this.props.caseinfo.PBASINGNINFO.PNAMEC
        const hisid = this.props.caseinfo.HISID
        const date = this.props.PickerDate
        const selectdate = moment(date).format("YYYY-MM-DD")
        console.log(date)
        console.log(selectdate)
        ApiProvider.createPatientSchedule(this.props.caseinfo.PLOCSTART.HBEDNO, this.state.inputContent, timeNowStr, this.props.caseinfo.TRANSINID, this.state.selSchedule, writter, name, hisid, selectdate)
            .then(data => {
                console.log(" OK " + JSON.stringify(data.data))
                this.setState(
                    {
                        EditMode: false,
                        inputContent: undefined,
                        status: true
                    })
            })
            .catch(err => {
                console.log(" Err " + JSON.stringify(err))
            })
    }
    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    timechange(timestamp) {
        const changetime = new Date(timestamp).getHours() + ":" + new Date(timestamp).getMinutes()
        return changetime
    }

    // pressbtn(pressitem) {
    //     this.setState(
    //         {
    //             pressitemstate: pressitem
    //         }
    //     );
    // }
    listevent() {
        let ScheduleList = this.state.Scheduledata
        console.log(ScheduleList)
        if (ScheduleList.length === 0) {
            return <div style={{ height: "50px", display: "flex", alignItems: "center" }}>
                當日無排程
                    </div>
        }
        // const RenderData = scheduleList(eventstate)

        if (ScheduleList.length > 1) {
            ScheduleList = ScheduleList.sort((a, b) => {
                let tempTimeA = moment(moment(a.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss'), "YYYYMMDD").valueOf()
                let tempTimeB = moment(moment(b.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss'), "YYYYMMDD").valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }

        const CellRender = ScheduleList.map(function (info, index, arr) {
            const time = moment(info.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss')
            const localTime = moment.utc(time).toDate()
            const text = <div>{info.writter}醫師於{moment(localTime).format("YYYY/MM/DD HH:mm:ss")}更新</div>
            return (
                <div key={index} className='CellLayout'>
                    <div className='Context'>
                        <div style={{ height: '30px', width: "60px", borderRadius: "4px", backgroundColor: "rgba(124,179,5,1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <t2 style={{ color: 'white' }}>NI-{parseBedNumber(info.bednumber)}</t2>
                            {/* itemchange(info.type) */}
                        </div>
                        <div style={{ fontSize: "16px" }}>
                            {info.name}
                            {/* {moment(localTime).format("HH:mm")}  */}
                            {info.context}
                        </div>
                    </div>
                    <Tooltip placement="right" title={text}>
                        <div>
                            <img src={detaillogo} alt='detaillogo'></img>
                        </div>
                    </Tooltip>
                </div>
            );
        })
        return CellRender
    }


    //測驗項目功能_區間按鈕樣式
    // SwitchTestItemInterval(testiteminterval) {
    //     const stylestate = this.state.pressitemstate
    //     if (testiteminterval === stylestate) {
    //         return 'buttonselect'
    //     }
    //     else return 'buttonUnselect'
    // }
    //測驗項目功能_區間按鈕選擇
    Onchangetimeinterval(changestate) { this.setState({ items_interval: changestate }) }
    EditControl() {
        const edit = this.props.editstatus
        if (edit) {
            return (
                <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", cursor: "pointer" }}>
                    <img src={editlogo} alt="editlogo" />
                </div>
            );
        }
        return null
    }
    render() {
        const { title } = this.props
        const EditButton = this.EditControl()
        return (
            <div className='ScheduleEventLayout'>
                <div className='ScheduleEventTitle'>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{title}</t2>
                    {EditButton}
                </div>
                <div className='ScheduleEventContext'>
                    <div className='ContextList'>
                        {this.listevent()}
                    </div>
                </div>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div value='排程' style={{ display: "grid", width: 150, height: "32px", justifyContent: "center", alignItems: "center", border: "1px solid #d9d9d9", borderRadius: "4px", marginRight: "5px" }}>排程</div>
                        <Input placeholder="輸入當日內容" value={this.state.inputContent} onChange={this.onInputChange}></Input>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Schedule_event_list
