import React, { Component } from 'react';

class ExamReportModal extends Component {
    render() {
        const reportDetail = this.props.report
        const showDetail = reportDetail.map((data, index) => {
            return data.CONTENT !== "" ? <div key={ "ExamReportModal" + index } style={{ whiteSpace: "pre-wrap" }}>{data.CONTENT}</div> : ""
        })
        return (
            <div>{showDetail}</div>
        );
    }
}

export default ExamReportModal
