import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtion_TPN_cell.scss'
import moment from 'moment'
import RemarkModal from '../CrossSectionView/RemarkModal'
import * as ApiProvider from '../../API/ApiProvider';

class Nurtiontpncell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        remark: null,
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { caseinfo, data } = this.props
        ApiProvider.getOneRemark(caseinfo.TRANSINID, data.UDORDSEQ, "nurtiontpn").then(result => {
            console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    onMouseEnter() { this.setState({ EditMode: true }) }

    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    };

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    Moduledisplay(PName, ST) {

        const remarkValue = {
            "createTime": ST,
            "name": PName,
            "remarkId": this.props.data.UDORDSEQ,
            "remark": this.state.remark,
            "transinId": this.props.caseinfo.TRANSINID,
            "type": "nurtiontpn",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            // <RemarkModal caseinfo={caseinfo} type="nurtiontpn" name={title} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
        );
    }
    render() {
        const { data } = this.props
        const TPNList = ["VOL.", "AA", "Glu", "Na", "K", "Ca", "P", "Mg"]
        const TPNListcellRender = TPNList.map(function (info, index) {
            return (
                <centerstyle key={index} style={{ border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                    <t2>{info}</t2>
                </centerstyle>
            );
        })
        function TPNSubDataMap(data, TPNDATA) {
            if (TPNDATA === undefined || TPNDATA === null) {
                return []
            }
            const TPNSubData = [data.UDDOSAGE, data.TPNDATA.AA, data.TPNDATA.Glu, data.TPNDATA.Na, data.TPNDATA.K, data.TPNDATA.Ca, data.TPNDATA.P, data.TPNDATA.Mg]
            const TPNSubDatacellRender = TPNSubData.map(function (info, index) {
                return (
                    <centerstyle key={index} style={{ border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                        <t2>{info}</t2>
                    </centerstyle>
                );
            })
            return TPNSubDatacellRender

        }
        const TPNSubDatacellRender = TPNSubDataMap(data, data.TPNDATA)
        const RemarkData = this.state.remark

        const startTime = moment(data.UDBGNDT.toString().trim(), "YYYY-MM-DD-HH.mm.ss.00.000000").format("MM/DD HH:mm:ss")
        const endTime = moment(data.UDENDDT.toString().trim(), "YYYY-MM-DD-HH.mm.ss.00.000000").format("MM/DD HH:mm:ss")
        const name = data.UDGNAME.toString().trim()
        const status = "生效中"
        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} className='nurtiontpntablelayout'>
                <div style={{ height: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "100px" : "130px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: " 1fr 1fr 60px 450px", height: "100px" }} >
                        <div style={{ padding: "0px 5px", display: "flex", alignItems: "center", border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                            <t3>{startTime}</t3>
                        </div>
                        <div style={{ padding: "0px 5px", display: "flex", alignItems: "center", border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                            <t3>{endTime}</t3>
                        </div>
                        <div style={{ padding: "0px 5px", display: "flex", alignItems: "center", border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                            <t3>{status}</t3>
                        </div>
                        <div style={{ display: "grid", gridTemplateRows: "30px 70px" }}>
                            <div style={{ padding: "0px 5px", display: "flex", alignItems: "center", border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse" }}>
                                <t3>{name}</t3>
                            </div>
                            <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                                <div style={{ display: "grid", gridTemplateColumns: "repeat(8,1fr)" }}>
                                    {TPNListcellRender}
                                </div>
                                <div style={{ display: "grid", gridTemplateColumns: "repeat(8,1fr)" }}>
                                    {TPNSubDatacellRender}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div style={{ fontWeight: "600", color: "rgb(0,120,0)", display: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "none" : "flex"], border: "1px solid rgba(232,232,232,1)", borderCollapse: "collapse", padding: "0px 5px", alignItems: "center", height: "30px", width: "100%" }}>
                        <t3>{RemarkData}</t3>
                    </div>
                </div>
                <div style={this.state.EditMode ? {
                    display: this.state.powerLogin == 1 ? "flex" : "none", alignItems: "center", justifyContent: "center", position: "absolute", top: 0, right: 0, height: [this.state.remark === "" || this.state.remark === undefined || this.state.remark === null ? "100px" : "130px"], background: "white", width: "77px", border: "rgba(232, 232, 232, 1) 1px solid"
                } : { display: "none" }}>
                    < div onClick={() => this.showModal()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }} >
                        <img src={chooseedit} alt='chooseedit'></img>
                    </div>
                </div>
                {this.Moduledisplay(name, startTime)}
            </div >
        );
    }
}

export default Nurtiontpncell
