import React, { Component } from 'react';
import * as ApiProvider from '../../API/ApiProvider'
import { Icon } from 'antd'


export default class NonLabReport extends Component {
    state = {
        RESDISP: null,
        preCID: null,
        preHID: null,
        preSeq: null,
        preMonth: null
    }
    componentDidMount() {
        const { CID, HID, month, seq } = this.props
        this.setState({ preCID: CID, preHID: HID, preMonth: month, preSeq: seq })
        this.APIget(HID, month, CID, seq)
    }

    componentDidUpdate() {
        const { CID, HID, month, seq } = this.props
        const { preCID, preHID, preMonth, preSeq } = this.state
        if (CID !== preCID || HID !== preHID || month !== preMonth || seq !== preSeq) {
            this.setState({ preCID: CID, preHID: HID, preMonth: month, preSeq: seq })
            this.APIget(HID, month, CID, seq)
        }
    }

    APIget(HID, month, CID, seq) {
        ApiProvider.getStoredProcedureRESDISP(HID, month, CID, seq)
            .then(data => { console.log(data.data); this.setState({ RESDISP: data.data }) })
            .catch(err => { console.log(err) })
    }

    render() {

        const RESDISP_Report = this.state.RESDISP
        console.log(RESDISP_Report)
        if (RESDISP_Report === null) {
            return (
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <Icon type="loading" />
                </div>);
        }
        const showDetail = RESDISP_Report.map((data, index) => {
            return data.CONTENT !== "" ? <div key={"ExamReportModal" + index} style={{ whiteSpace: "pre-wrap" }}>{data.CONTENT}</div> : ""
        })
        return (
            <div>{showDetail}</div>
        );
    }
}
