import React, { Component } from 'react';
import Prescriptionordertable from './Prescriptionordertable'
import { Select, Input } from 'antd';
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'
import * as ApiProvider from '../../API/ApiProvider'
import * as UDORDER from '../../Resource/UDORDER0'

class Prescriptionorderlayout extends Component {
    state = {
        classParser: { "mappings": [] },
        class: "全部類別",
        SelectMode: 0,
        InputMode: null,
        preHID: null,
        preCID: null,
        UDORDER0: []
    }
    componentDidMount() {
        this.getGithub()
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        this.setState({ preHID: HID, preCID: CID })
        this.getUDORDER0(HID, CID)
    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preHID, preCID } = this.state
        const HID = caseinfo.HISID
        const CID = caseinfo.CASEID
        if (HID !== preHID || CID !== preCID) {
            this.setState({ preHID: HID, preCID: CID })
            this.getUDORDER0(HID, CID)
        }
    }
    getGithub() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Prescription");
                parser.mappings = [{ "key": "全部類別", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Prescription OK " + JSON.stringify(parser))
                this.setState({
                    classParser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    getUDORDER0(HID, CID, Start, End) {

        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                // const RowData = data.data
                // const SortFromRangeDate = RowData.filter(function (info, index, arr) {
                //     return
                // })
                this.setState({ UDORDER0: data.data })
            })
            .catch(err => { console.log("getStoredProcedureUDORDER0 NG " + JSON.stringify(err)) });
        // this.setState({ UDORDER0: UDORDER.data })
    }


    onChangeClass = (value) => {
        this.setState({ class: value })
    }
    onChangeinputKey = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    ControlBarRender() {
        const { Option } = Select;
        return (
            <div style={{ display: "grid", gridTemplateColumns: "120px 100px auto", gridColumnGap: "10px" }}>
                <Select defaultValue={this.state.class} style={{ width: 120 }} onChange={this.onChangeClass}>
                    {this.state.classParser.mappings.map((mapping, index) => {
                        return <Option value={mapping.key}>{mapping.key}</Option>
                    })}
                </Select>
                <Select defaultValue={this.state.SelectMode} style={{ width: 100 }} onChange={(value) => { console.log(value); this.setState({ SelectMode: value }) }}>
                    <Option value={0}>全部狀態</Option>
                    <Option value={30}>生效中</Option>
                    <Option value={60}>已完成</Option>
                    <Option value={80}>已取消</Option>
                </Select>
                <Input id="prescriptionorderinput" placeholder="輸入藥囑關鍵字" onChange={this.onChangeinputKey} />
            </div>
        );
    }

    parseClass = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["全部類別"] = []

        inputs.forEach(input => {
            let content = input.UDGNAME;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["全部類別"].push(input)
        });
        return allResult;
    }

    onClickSearch = () => {
        function formatUDORDERStatus(UDORDER0, Status) {
            if (Status === 0) {
                return UDORDER0
            }
            const filterUDORDER = UDORDER0.filter(function (info, index, arr) {
                return Number(info.UDSTATUS) === Status
            })
            return filterUDORDER
        }
        const { searchbar } = this.props
        const { UDORDER0 } = this.state
        const parseClass = this.parseClass
        const Status = this.state.SelectMode
        const activeList = formatUDORDERStatus(UDORDER0, Status)
        let ListData = [];
        let clusteredData = {}
        if (this.state.classParser !== undefined) {
            clusteredData = parseClass(activeList, this.state.classParser)
        }
        let filteredData = clusteredData[this.state.class];
        ListData = this.SortDataList(filteredData)
        return ListData
    }

    SortDataList = (DataList) => {
        const { PickerDateRange, searchbar } = this.props
        const { InputMode } = this.state
        if (InputMode !== null) {
            const regex = new RegExp(InputMode, "i");
            DataList = DataList.filter(data => {
                return data.UDGNAME.match(regex) !== null || data.UDRPNAME.match(regex) !== null
            })
        }

        const st = searchbar ? PickerDateRange[0] : PickerDateRange[0]
        const et = searchbar ? PickerDateRange[1] + (24 * 60 * 60 * 1000) : PickerDateRange[1]

        let filterDate = DataList.filter(function (element, index, arr) {
            const startTime = moment(element.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            const endTime = moment(element.UDENDDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            return !(st > endTime || et < startTime)
        })

        if (Object.keys(filterDate).length > 1) {
            filterDate = filterDate.sort((a, b) => {
                console.log()
                let tempTimeA = moment(a.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                let tempTimeB = moment(b.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }
        return filterDate
    }

    render() {
        const { searchbar, caseinfo } = this.props
        const { SelectMode } = this.state

        const filterData = this.onClickSearch()

        return (
            <div >
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "grid", gridTemplateColumns: searchbar ? "2fr 3fr" : "1fr", alignItems: "center" }
                }>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{searchbar ? "藥物處方" : "當日處方"}</t2>
                    {searchbar ? this.ControlBarRender() : null}
                </div>
                <Prescriptionordertable caseinfo={caseinfo} UDORDER0={filterData} SelectMode={SelectMode} searchbar={searchbar}></Prescriptionordertable>
            </div>
        );
    }
}

export default Prescriptionorderlayout
