import React, { Component } from 'react';
import Labview from './ExamLabview'
import { DatePicker, Button, Icon } from 'antd';
import './ExamReport.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import Nonlabview from './NonLabview'
import locale from 'antd/es/date-picker/locale/zh_TW';
import Nonlabviewtable from '../CrossSectionView/Nonlabviewtable'
// import * as RegularData from '../../Data/RegularData'

class Examreport extends Component {
    state = {
        selectdata: 0,
        selectTitle: '',
        PickerDateRange: [moment(moment().subtract(6, 'days').format('YYYYMMDD')),
        moment(moment().format('YYYYMMDD'))],
        RESDGLU1: [], //累積報告
        RESLAB02: [], //累積報告
        RESLAB01: [],  //累積報告
        RESDBGAS: [],  //累積報告
        RESLAB0C: [],//累積報告
        ORDLIST: [],
        RESSECT: [],
        isLoading: false,
    }
    constructor(props) {
        super(props)
        this.PickerDateRangeChange = this.PickerDateRangeChange.bind(this)
    }

    SelectDataList() {
        function vallofnull(value) {
            if (value === null || value === undefined || value === "") {
                return ""
            }
            return value.toString().trim()
        }
        const DateRange = this.state.PickerDateRange
        const selectData = this.state.selectdata
        const st_timestamp = DateRange[0].valueOf()
        const et_timestamp = DateRange[1].valueOf() + (24 * 60 * 60 * 1000)
        // const { PickerDateRange, RESLAB02, RESDGLU1, RESLAB01, RESDBGAS, RESLAB0C, ORDLIST, RESSECT } = this.props
        const RESLAB02 = this.state.RESLAB02
        const RESDGLU1 = this.state.RESDGLU1
        const RESLAB01 = this.state.RESLAB01
        const RESDBGAS = this.state.RESDBGAS
        const RESLAB0C = this.state.RESLAB0C


        var CellData = []
        if (selectData === 0) { // CBC
            for (let index = 0; index < RESLAB02.length; index++) {
                const element = RESLAB02[index];
                const WBC = vallofnull(element.RSV01)
                const Band = vallofnull(element.RSV13)
                const Seq = vallofnull(element.RSV14)
                const Lym = vallofnull(element.RSV15)
                const Hb = vallofnull(element.RSV03)
                const Hct = vallofnull(element.RSV04)
                const Plt = vallofnull(element.RSV09)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [WBC, Band, Seq, Lym, Hb, Hct, Plt]
                    })
                }
            }
            return CellData
        }
        if (selectData === 1) { // 電解質與腎
            for (let index = 0; index < RESLAB01.length; index++) {
                const element = RESLAB01[index];
                const Na = vallofnull(element.RSV13)
                const K = vallofnull(element.RSV14)
                const Cl = vallofnull(element.RSV15)
                const CaFree = "No"
                const CaTotal = vallofnull(element.RSV03)
                const Mg = "No"
                const P = vallofnull(element.RSV17)
                const BUN = vallofnull(element.RSV05)
                const Cr = vallofnull(element.RSV07)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [Na, K, Cl, CaFree, CaTotal, Mg, P, BUN, Cr]
                    })
                }
            }
            return CellData
        }
        if (selectData === 2) { // 肝功能
            for (let index = 0; index < RESLAB01.length; index++) {
                const element = RESLAB01[index];
                const Alt = vallofnull(element.RSV11)
                const AST = vallofnull(element.RSV12)
                const TBil = vallofnull(element.RSV08)
                const DBil = vallofnull(element.RSV25)
                const rGGT = vallofnull(element.RSV19)
                const Alkp = vallofnull(element.RSV09)
                const Alb = vallofnull(element.RSV02)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [Alt, AST, TBil, DBil, rGGT, Alkp, Alb]
                    })
                }
            }
            return CellData
        }
        if (selectData === 3) { // 早產兒
            for (let index = 0; index < RESLAB01.length; index++) {
                const element = RESLAB01[index];
                const Iron = vallofnull(element.RSV26)
                const TIBC = vallofnull(element.RSV27)
                const FeSat = Number(Iron) && Number(TIBC) > 0 ? parseFloat(Iron / TIBC).toFixed(2) : null
                const LDH = vallofnull(element.RSV10)
                const Alkp = vallofnull(element.RSV9)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [Iron, TIBC, FeSat, LDH, Alkp]
                    })
                }
            }
            return CellData
        }
        if (selectData === 4) { // 心臟
            for (let index = 0; index < RESLAB01.length; index++) {
                const element = RESLAB01[index];
                const ck = vallofnull(element.RSV18)
                const ckMB = vallofnull(element.RSV28)
                const BNP = "No"
                const Troponinl = vallofnull(element.RSV43)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [ck, ckMB, BNP, Troponinl]
                    })
                }
            }
            return CellData
        }
        if (selectData === 5) { // 凝血
            for (let index = 0; index < RESLAB02.length; index++) {
                const element = RESLAB02[index];
                const PT = vallofnull(element.RSV10)
                const APTT = vallofnull(element.RSV11)
                const INR = vallofnull(element.RSV12)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [PT, APTT, INR]
                    })
                }
            }
            return CellData
        }
        if (selectData === 6) { // GAS(Acid-Base)
            for (let index = 0; index < RESDBGAS.length; index++) {
                const element = RESDBGAS[index];
                const PH = vallofnull(element.SS07)
                const pCO2 = vallofnull(element.SS06)
                const pO2 = vallofnull(element.SS08)
                const HCO3 = vallofnull(element.SS04)
                const BE = vallofnull(element.SS01)
                const O2Sat = vallofnull(element.SS05)
                const FiO2 = vallofnull(element.SS03)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [PH, pCO2, pO2, HCO3, BE, O2Sat, FiO2]
                    })
                }
            }
            return CellData
        }
        if (selectData === 7) { // GAS(Additionary)
            for (let index = 0; index < RESDBGAS.length; index++) {
                const element = RESDBGAS[index];
                const Hb = vallofnull(element.SS11)
                const Hct = vallofnull(element.SS12)
                const Na = vallofnull(element.SS13)
                const K = vallofnull(element.SS14)
                const CaFree = vallofnull(element.SS15)
                const Bun = vallofnull(element.SS16)
                const Cr = vallofnull(element.SS17)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [Hb, Hct, Na, K, CaFree, Bun, Cr]
                    })
                }
            }
            return CellData
        }
        if (selectData === 8) { // Glucose
            for (let index = 0; index < RESDGLU1.length; index++) {
                const element = RESDGLU1[index]
                const GluBeside = vallofnull(element.GLUFAST)
                const GluLab = vallofnull(element.RSV16)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [GluBeside, GluLab]
                    })
                }
            }
            return CellData
        }
        if (selectData === 9) { // 黃疸
            for (let index = 0; index < RESLAB0C.length; index++) {
                const element = RESLAB0C[index]
                const TCB = vallofnull(element.RSV01)
                const MicroBil = vallofnull(element.RSV02)
                const TBil = vallofnull(element.RSV03)
                const DBil = vallofnull(element.RSV04)
                const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [TCB, MicroBil, TBil, DBil]
                    })
                }
            }
            return CellData
        }
        if (selectData === 10) { // 感染指數
            for (let index = 0; index < RESLAB02.length; index++) {
                const elementOne = RESLAB02[index]
                const WBC = vallofnull(elementOne.RSV01)
                const Band = vallofnull(elementOne.RSV13)
                const Seg = vallofnull(elementOne.RSV14)
                const Lym = vallofnull(elementOne.RSV15)

                const time = moment((elementOne.ORRESDT.toString() + elementOne.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((elementOne.ORRESDT.toString() + elementOne.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [WBC, Band, Seg, Lym, null, null]
                    })
                }
            }
            for (let index = 0; index < RESLAB01.length; index++) {
                const elementTwo = RESLAB01[index]
                const PCT = null
                const CRP = vallofnull(elementTwo.RSV30)
                const time = moment((elementTwo.ORRESDT.toString() + elementTwo.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                if (st_timestamp < time && time < et_timestamp) {
                    CellData.push({
                        Time: moment((elementTwo.ORRESDT.toString() + elementTwo.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").format("YYYY/MM/DD HH:mm:ss"),
                        Data: [null, null, null, null, PCT, CRP]
                    })
                }
            }
            return CellData
        }
        return null

    }

    componentDidMount() {
        const { caseinfo } = this.props
        this.setState({ preHID: caseinfo.HISID })
        this.APIUpdate(caseinfo.TRANSINDT, caseinfo.HISID)
        this.SelectDataList = this.SelectDataList.bind(this)
    }

    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preHID } = this.state
        if (preHID !== caseinfo.HISID) {
            this.setState({ preHID: caseinfo.HISID })
            this.APIUpdate(caseinfo.TRANSINDT, caseinfo.HISID)
            this.SelectDataList = this.SelectDataList.bind(this)
        }
    }

    // ChangeRangePicker(dates, datestrings) {

    // }
    APIUpdate(DateRange, HID) {
        this.setState({ isLoading: true });
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - new Date(PickerDate).getTime()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }

        const st = moment(DateRange, "YYYYMMDD").format("YYYY-MM-DD")
        console.log(DateRange, HID)
        const datePick = moment(DateRange, "YYYYMMDD").format('YYYY/MM/DD')
        const MonthInterval = selectMonthInterval(datePick)
        const RESDGLU1 = ApiProvider.getStoredProcedureRESDGLU1(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB01 = ApiProvider.getStoredProcedureRESLAB01(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESDBGAS = ApiProvider.getStoredProcedureRESDBGAS(HID, st)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB0C = ApiProvider.getStoredProcedureRESLAB0C(HID, st)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const ORDLIST = ApiProvider.getStoredProcedureORDLIST(HID, st, "ALL", "DOC3924B")
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const RESSECT = ApiProvider.getStoredProcedureRESSECT(HID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        Promise.all([RESDGLU1, RESLAB02, RESLAB01, RESDBGAS, RESLAB0C, ORDLIST, RESSECT]).then(result => {
            this.setState({
                RESDGLU1: result[0], //累積報告
                RESLAB02: result[1],  //累積報告
                RESLAB01: result[2],  //累積報告
                RESDBGAS: result[3],  //累積報告
                RESLAB0C: result[4],  //累積報告
                ORDLIST: result[5],
                RESSECT: result[6],
                isLoading: false,
            })
        }).catch(() => { this.setState({ isLoading: false }); });
    }
    callbackDateFunction = (select) => {
        this.setState({
            selectdata: select
        })
    }

    callbackTitleFunction = (select) => {
        this.setState({
            selectTitle: select
        })
    }

    PickerDateRangeChange(date, dateString) {
        this.setState({ PickerDateRange: date })
    }

    trainDay = (e) => {
        const { caseinfo } = this.props
        const { PickerDateRange } = this.state
        const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const st = PickerDateRange[0].valueOf()
        const et = PickerDateRange[1].valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                if (bronDate <= (st - day)) {
                    const bTime = [moment(moment(st - day).format('YYYYMMDD')), moment(moment(et - day).format('YYYYMMDD'))]
                    this.PickerDateRangeChange(bTime)
                }
                break;
            case 'N':
                const nTime = [moment(moment().subtract(6, 'days').format('YYYYMMDD')), moment(moment().format('YYYYMMDD'))]
                this.PickerDateRangeChange(nTime)
                break;
            case 'A':
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                if (aTemp >= (et + day)) {
                    const aTime = [moment(moment(st + day).format('YYYYMMDD')), moment(moment(et + day).format('YYYYMMDD'))]
                    this.PickerDateRangeChange(aTime)
                }
                break;
            default:
                break;
        }
    }

    render() {
        const { caseinfo } = this.props
        const momentDate = this.state.PickerDateRange
        let data = this.SelectDataList()

        data = data.sort((a, b) => {
            let tempTimeA = moment(a.Time, 'YYYY-MM-DDHHmmss').valueOf()
            let tempTimeB = moment(b.Time, 'YYYY-MM-DDHHmmss').valueOf()
            return tempTimeA < tempTimeB ? 1 : -1
        })

        const { RangePicker } = DatePicker
        const PickerDateRange = [momentDate[0].valueOf(), momentDate[1].valueOf() + (24 * 60 * 60 * 1000)]
        console.log(momentDate)
        return (
            <div className='examreportlayout'>
                <div style={{ display: "grid", gridTemplateColumns: "250px 120px", gridColumnGap: "5px", marginBottom: "5px" }}>
                    <RangePicker
                        // defaultValue={this.state.PickerDateRange}
                        getCalendarContainer={triggerNode => triggerNode.parentNode}
                        value={this.state.PickerDateRange}
                        format={"YYYY/MM/DD"}
                        allowClear={false}
                        locale={locale}
                        disabledDate={(current) => { return current > moment().startOf() || current < moment(caseinfo.PBASINGNINFO.PBIRTHDT.toString(), "YYYYMMDD").endOf() }}
                        // toString={(dates, datestrings) => { this.setState({ RangePicker: dates }) }}
                        onChange={this.PickerDateRangeChange}
                    />
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px" }}>
                        <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                        <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                        <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                    </div>
                </div>
                {
                    this.state.isLoading
                        ? (<Icon type="loading" />)
                        : (
                            <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" }}>
                                <Labview selectvalue={this.state.selectdata}
                                    parentDateCallback={this.callbackDateFunction}
                                    DataList={data}
                                    SelectStatus={this.state.selectdata}
                                    PickerDateRange={PickerDateRange}
                                    parentTitleCallback={this.callbackTitleFunction}>
                                </Labview>
                                <Nonlabview caseinfo={caseinfo} PickerDateRange={PickerDateRange}></Nonlabview>
                            </div>
                        )
                }
                {/* {this.charttableview()} */}
                <div style={{ display: "flex", justifyContent: 'center', alignItems: 'center' }}>
                </div>
            </div>
        );
    }
}

export default Examreport;
