import X2JS from "x2js"
import moment from 'moment';


export function InitCenterData(value) {
    if (value === undefined || value === null || value === "" || value === 0) {
        return "--"
    }
    else return value
}


export function bplimitValue(bpd, bps, upper1, lower1, upper2, lower2) {
    if (Number(bpd) > 0 || Number(bps) > 0) {
        if (bpd > upper1 || bpd < lower1) {
            return false
        }
        if (bps > upper2 || bps < lower2) {
            return false
        }
        return true
    }
    return true

}

export function limitValue(value, upper, lower) {
    if (Number(value) > 0) {
        if (value > upper || value < lower) {
            return false
        }
        return true
    }
    return true

}

export function bornstatusFormat(week, day) {
    if (week === "" || week === null || week === undefined) {
        return null
    }
    if (week === 0 && day === 0) {
        return null
    }
    return week.toString() + "+" + day.toString() + ">"
}


export function valueFormat(value) {
    if (value === "" || value === null || value === undefined || value === 0) {
        return null
    }
    return value.toString() + ">"
}

export function WeightListFilter(weightList, PickerDate) {
    const dateTime = PickerDate + (24 * 60 * 60 * 1000)
    var weightValue = 0
    if (Array.isArray(weightList)) {
        for (let index = 0; index < weightList.length; index++) {
            const element = weightList[index];
            const elementTime = moment(element.rec_dat.toString().trim() + element.rec_tim.toString().trim(), "YYYYMMDDHHmm").valueOf()
            if (Number(element.value2) > 0) {
                if (elementTime < dateTime) {
                    weightValue = Number(element.value2) * 1000
                    break;
                }
            }
        }
    }
    if (weightValue === 0) {
        return null
    }
    return weightValue
}

export function CalculateAgeFromPickerDate(BornStatus, PickerDate) {
    const startDate = moment(BornStatus.date);
    const endDate = moment(PickerDate);
    const minus = endDate.diff(startDate, 'days');
    console.log(BornStatus)
    if (BornStatus === undefined || BornStatus.date === undefined || BornStatus.week === undefined || BornStatus.day === undefined) {
        return "未知年齡"
    }
    if (BornStatus.week >= 37) {
        const month = Math.floor((minus) / 30);
        const day = Math.floor((minus) % 30);
        return month > 0 ? `${month}月+${day}天` : `${day}天`
    }

    const tempWeek = Math.floor(BornStatus.week + (BornStatus.day + minus) / 7);
    const tempDay = Math.floor((BornStatus.day + minus) % 7);
    if (tempWeek >= 40) {
        const overDay = (tempWeek - 40) * 7 + tempDay
        const month = Math.floor(overDay / 30)
        const day = overDay % 30
        return month > 0 ? `矯齡${month}月${day}天` : `矯齡${day}天`
    } else {
        const week = tempWeek
        const day = tempDay
        return `${week}+${day}`
    }
}

export function CalculateAge(BornStatus) {
    const startDate = moment(BornStatus.date);
    const endDate = moment();
    const minus = endDate.diff(startDate, 'days');
    // console.log(BornStatus)
    if (BornStatus === undefined || BornStatus.date === undefined || BornStatus.week === undefined || BornStatus.day === undefined) {
        return "未知年齡"
    }
    if (BornStatus.week >= 37) {
        const month = Math.floor((minus) / 30);
        const day = Math.floor((minus) % 30);
        return month > 0 ? `${month}月+${day}天` : `${day}天`
    }

    const tempWeek = Math.floor(BornStatus.week + (BornStatus.day + minus) / 7);
    const tempDay = Math.floor((BornStatus.day + minus) % 7);
    if (tempWeek >= 40) {
        const overDay = (tempWeek - 40) * 7 + tempDay
        const month = Math.floor(overDay / 30)
        const day = overDay % 30
        return month > 0 ? `矯齡${month}月${day}天` : `矯齡${day}天`
    } else {
        const week = tempWeek
        const day = tempDay
        return `${week}+${day}`
    }
}

export function WardgrewBtnStatus(BornStatus) {
    const startDate = moment(BornStatus.BIRTHDAY);
    const endDate = moment();
    const minus = endDate.diff(startDate, 'days');
    // console.log(BornStatus)
    if (BornStatus === undefined || BornStatus.BIRTHDAY === undefined || BornStatus.WEEK_BORN === undefined || BornStatus.DAY_BORN === undefined) {
        return 0
    }
    if (BornStatus.WEEK_BORN >= 37) {
        return 1
    }

    const tempWeek = Math.floor(BornStatus.WEEK_BORN + (BornStatus.DAY_BORN + minus) / 7);
    const tempDay = Math.floor((BornStatus.DAY_BORN + minus) % 7);
    if (tempWeek >= 40) {
        return 1
    } else {
        return 0
    }
}


export function statecolor(state) {
    if (state === 'HR') {
        return "rgb(100,140,60)"
    }
    if (state === 'BP') {
        return "rgb(193,37,40) "
    }

    if (state === 'ABP') {
        return "#C12528"
    }
    if (state === 'NBP') {
        return "#C12528"
    }
    if (state === 'SpO2') {
        return "rgb(60,105,160) "
    }
    if (state === 'BT') {
        return "rgb(127,61,181)"
    }
    if (state === 'RR') {
        return "rgb(200,110,40) "
    }
    return null
}

//parse flag
export function Respiratorswitch(Respirator) {
    if (Respirator === '呼(非侵入)') {
        return '非侵入呼吸器'
    }
    if (Respirator === '呼(侵入)') {
        return '侵入性呼吸器'
    }
    return '呼吸器'
}

export function wardstatestringformat(item) {
    switch (item.item) {
        case "Respirator":
            return Respiratorswitch(item.data)
        case "TPN":
            return "TPN"
        case "NPO":
            return "NPO"
        case "HD":
            return "HD"
        case "Abx":
            return "Abx"
        case "CVC":
            return "CVC"
        case "Xanth":
            return "Xanth"
        case "Phototherapy":
            return "照光"
        case "DNR":
            return "DNR"
        default:
            return item.item
    }
}

