export function getRandom(number) {
    return Math.floor(Math.random() * 5) + number;
};

export function getRandomfornumber(number) {
    return Math.floor(Math.random() * number);
};

export function arraychange(list) {
    var myNewArray = []
    for (var i = 0; i < list.length; ++i) {
        for (var j = 0; j < list[i].length; ++j) {
            myNewArray.push(list[i][j]);
        }
    }
    return myNewArray
}

export function WeightList(weightlist) {
    if (weightlist === undefined || weightlist === null) {
        return "--"
    }
    const findata = weightlist.filter(function (value, index) {
        return value.type === "TW"
    })
    if (findata.length === 0) {
        return 0
    }
    else return findata
}

export function BornWeight(weightlist, birthday) {
    if (weightlist === undefined || weightlist === null) {
        return "--"
    }
    if (Array.isArray(weightlist)) {
        const findata = weightlist.filter(function (value, index) {
            return value.rec_dat === birthday && value.type === "TW"
        })
        if (findata.length === 0) {
            return 0
        }
        else return parseInt(Number(findata[findata.length - 1].value2) * 1000)
    }
    else {
        return parseInt(Number(weightlist.value2) * 1000)
    }
}

export function NewestWeight(weightlist) {
    if (weightlist === undefined) {
        return "--"
    }
    if (weightlist.length === 0) {
        return 0
    }
    if (Array.isArray(weightlist)) {
        const findata = weightlist.filter(function (value, index) {
            return value.type === "TW"
        })
        if (findata.length === 0) {
            return "--"
        }
        return parseInt(Number(findata[0].value2) * 1000)
    }
    else {
        return parseInt(Number(weightlist.value2) * 1000)
    }

}

export function XM2JSON(data) {
    const xm2 = new X2JS()
    if (data === undefined) {
        return []
    }
    const status = xm2.xml2js(data).outmsg.ret
    if (Number(status) === 0) {
        const parsedata = xm2.xml2js(data).outmsg.resconts.rescont
        if (!Array.isArray(parsedata)) {
            return [parsedata]
        }
        return parsedata
    }
    return []
}

export function QMNCXM2JSON(data) {
    const xm2 = new X2JS()
    if (data === undefined) {
        return []
    }
    const status = xm2.xml2js(data).outmsg.ret
    if (Number(status) === 0) {
        const parsedata = xm2.xml2js(data).outmsg.resconts.rescont
        return parsedata
    }
    return []
}

export function Doc(DocList, type) {
    if (DocList === undefined || DocList === null || DocList === "") {
        return ""
    }
    const Main = DocList.filter(function (element, index) {
        return element.PDTYPE === type
    })
    if (Main.length === 0) {
        return ""
    }
    console.log(Main[0])
    return Main[0].PDDOCNMC.toString().trim() + " " + Main[0].PDMDNO.toString().trim().replace("DOC", "")
}

export function selectweight(List, date) {
    if (List === undefined || List === null) {
        return "--"
    }
    var array = []
    for (let index = 0; index < List.length; index++) {
        const element = List[index];
        if (moment(element.rec_dat, 'YYYYMMDD').valueOf() < date) {
            array.push(element)
        }
    }
    if (array.length === 0) {
        return 0
    }
    return Number(array[0].value2) * 1000
}


