import React, { Component } from 'react';
import { Table, Icon } from 'antd';

class Systemlogtable extends Component {
    state = {
        MouseClick: 0
    }

    MouseClickTable(record) {
        this.setState({
            MouseClick: record
        })
    }

    render() {

        const powerName = ['唯讀使用者', '一般使用者', '系統管理員']
        const loginlog_table = this.props.loginlogList.map(
            (info, index) => {
                let managerName = ''
                let dateTime = info.time.split("T")[0] + ' ' + info.time.split("T")[1].split(".")[0]

                managerName = powerName[info.role]

                return {
                    key: index,
                    time: dateTime,
                    event: 'User ' + info.name + '(' + managerName + ') login system'
                }
            }
        )

        const apilog_table = this.props.apilogList.map((data, index) => {
            if (data !== null) {
                let date = data.split("- -")[1].split("]")[0].split("[")[1].split(" ")[0].split("/")
                const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
                for (let j = 0; j < months.length; j++) {
                    if (date[1] === months[j]) {
                        date[1] = months.indexOf(months[j]) + 1
                    }
                }
                if (date[1] < 10) {
                    date[1] = '0' + date[1]
                }
                let time = date[2].split(":")[0] + "-" + date[1] + "-" + date[0] + " " + date[2].split(":")[1] + ":" + date[2].split(":")[2] + ":" + date[2].split(":")[3]
                let content = data.split("- -")[1].split("]")[1]

                return {
                    key: index,
                    time: time,
                    event: content
                }
            }
            return null
        })

        const columns = [
            {
                title: '記錄時間',
                dataIndex: 'time',
                key: 'time',
                width: "1%"
            },
            {
                title: '事件',
                dataIndex: 'event',
                key: 'event',
                width: "10%"
            }
        ]

        // if (loginlog_table.length === 0 || apilog_table.length === 0) {
        //     return (
        //         <div style={{ display: "flex", justifyContent: "center", alignItems: 'center' }}>
        //             <Icon type="loading" />
        //         </div>
        //     );
        // }

        return (
            <div style={{ marginTop: '20px' }}>
                {
                    this.props.optionValue ? <Table columns={columns} dataSource={apilog_table} onRow={record => {
                        return {
                            onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                            onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                        }
                    }} pagination={{
                        onChange: (page, pageSize) => {
                            this.props.getPages(page)
                        }
                    }} /> :
                        <Table columns={columns} dataSource={loginlog_table} onRow={record => {
                            return {
                                onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                                onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                            }
                        }} pagination={{
                            onChange: (page, pageSize) => {
                                this.props.getPages(page)
                            }
                        }} />
                }
            </div>
        )
    }
}

export default Systemlogtable
