import React, { Component } from 'react';
import RTbreathetablecell from './RTbreathtablecell'
import moment from 'moment';
// import * as FilterReg from '../../Function/FiliterReg'
// import moment from 'moment'

class RTbreathetable extends Component {
    createRTbreathetable = () => {
        const { DataList, caseinfo, searchbar } = this.props

        // const DataList = [
        //     {Mode: "OETT+PC", Setting: "30/20/24/5",Date: "20200304", Time: 2000},
        //     {Mode: "OETT+PC", Setting: "30/20/24/5",Date: "20200304", Time: 1709},
        //     {Mode: "OETT+PC", Setting: "30/20/24/5",Date: "20200304", Time: 1500},
        //     {Mode: "OETT+PC", Setting: "30/20/24/5",Date: "20200304", Time: 1400},
        //      {Mode: "OETT+SIMV+PS", Setting: "30/20/24/5", Date: "20200304", Time: 1233},
        //     {Mode: "OETT+SIMV+PS", Setting: "30/20/24/5", Date: "20200305", Time: 1017},
        //      {Mode: "OETT+PC", Setting: "30/20/24/5", Date: "20200305", Time: "0700"},
        // ]       
        const RTbreathetable = DataList.map(
            (value, index, array) => {
                let isSameDay = false;
                if (index < DataList.length - 1) {
                    isSameDay = moment(value.Date, "YYYYMMDD").isSame(moment(DataList[index + 1].Date, "YYYYMMDD"), 'day');
                }
                if (index === DataList.length - 1) {
                    isSameDay = true;
                }
                return <RTbreathetablecell key={index} value={value} caseinfo={caseinfo} searchbar={searchbar} isSameDay={isSameDay}></RTbreathetablecell>
            }
        )

        return DataList.length !== 0 ? RTbreathetable :
            <div style={{ display: "grid", gridTemplateColumns: "90px 1fr 3fr" }}>
                <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                    無資料
                </div>
            </div>
    }

    render() {
        const title = ["時間", "模式", "設定"]
        const { searchbar } = this.props
        const titleCellRender = title.map(function (element, index, arr) {
            return (
                <div key={index} style={{ display: "flex", alignItems: "center", borderCollapse: "collapse", justifyContent: "center" }}>
                    <div style={{ fontSize: "14px" }}>{element}</div>
                </div>
            );
        })

        return (
            <div>
                <div style={{ height: "30px", display: "grid", gridTemplateColumns: "90px 1fr 3fr", backgroundColor: "rgba(226, 237, 243,.5)", justifyItems: "center" }}>
                    {titleCellRender}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: searchbar ? "calc(100vh - 305px)" : "120px", maxHeight: searchbar ? "calc(100vh - 300px)" : "400px", overflow: "auto" }}>
                        {this.createRTbreathetable()}
                    </div>
                </div>
            </div>
        );
    }
}

export default RTbreathetable
