import React, { Component } from "react";
import Flootsheetio from "./Flootsheetio";
// import Flowsheetvitalsign from './Flootsheetvitalsign'
import Vitalxaxis from "../../Module/Vitalxaxis";
import { DatePicker, Button, Icon } from "antd";
import moment from "moment";
import * as ApiProvider from "../../API/ApiProvider";
import "./Flootsheetlayout.scss";
import X2JS from "x2js";
import * as Wardstate from "../../Function/Wardstate";
import locale from "antd/es/date-picker/locale/zh_TW";
import { pregancyweeksanddays } from "../../Function/Wardbedfunction";
import { daysdiff } from "../../Function/Timeformat";
import * as WardState from "../../Function/Wardstate";

class Flowsheetlayout extends Component {
  state = {
    PickerDate: moment(moment().format("YYYYMMDD")).valueOf(),
    preCID: null,
    QSPE: [],
    isLoading: false,
    // itemlist: []
  };
  componentDidMount() {
    const { caseinfo } = this.props;
    const CID = caseinfo.CASEID;
    const date = moment(moment().format("YYYYMMDD")).valueOf();
    this.setState({ preCID: CID, PickerDate: date });
    this.ApiData(CID, date);
  }
  componentDidUpdate() {
    const CID = this.props.caseinfo.CASEID;
    const preCID = this.state.preCID;
    if (CID !== preCID) {
      const date = this.state.PickerDate;
      this.setState({ preCID: CID, PickerDate: date });
      this.ApiData(CID, date);
    }
  }
  ApiData(CID, DateTime) {
    const st = moment(DateTime + 7 * 60 * 60 * 1000).format("YYYYMMDDHHmm");
    const et = moment(DateTime + 31 * 60 * 60 * 1000).format("YYYYMMDDHHmm");
    this.setState({ isLoading: true });
    const apiQSPE = ApiProvider.getQSPE(CID, st, et)
      .then(data => {
        const dataJson = Wardstate.XM2JSON(data.data);
        // const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
        if (Array.isArray(dataJson)) {
          this.setState({ QSPE: dataJson });
        } else {
          this.setState({
            QSPE: [dataJson]
          });
        }
      })
      .catch(err => {
        console.log(
          "Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err)
        );
      });

    Promise.all([apiQSPE]).then(() => { this.setState({ isLoading: false }) }).catch(() => { this.setState({ isLoading: false }) });
  }
  onDateChange = (date, dateString) => {
    const CID = this.props.caseinfo.CASEID;
    this.setState({ preCID: CID, PickerDate: date.valueOf() });
    this.ApiData(CID, date.valueOf());
  };

  trainDay = e => {
    const { caseinfo } = this.props;
    const { PickerDate } = this.state;
    const bron = caseinfo.PBASINGNINFO.PBIRTHDT;
    const bronDate = moment(moment(bron).format("YYYYMMDD")).valueOf();

    const day = 24 * 60 * 60 * 1000;

    switch (e.target.value) {
      case "B":
        const bTime = PickerDate - day;
        const bTemp = bTime - 6 * day;
        bronDate <= bTemp && this.onDateChange(moment(bTime));
        break;
      case "N":
        const nTime = moment(moment().format("YYYYMMDD"));
        this.onDateChange(nTime);
        break;
      case "A":
        const aTime = PickerDate + day;
        const aTemp = moment(moment().format("YYYYMMDD")).valueOf();
        aTime <= aTemp && this.onDateChange(moment(aTime));
        break;
      default:
        break;
    }
  };


  render() {
    const { caseinfo, status } = this.props;
    const { PickerDate } = this.state
    const data = this.state.QSPE; //特殊護理事件
    console.log(data)
    const splist = data.filter(function (value, index, array) {
      if (value === undefined) {
        return null;
      }
      const str = value.special;
      return !(str.match("@v") !== null || str.match("@V") !== null);
    });
    const specialListRender = splist.map(function (value, index, arr) {
      const time = moment(value.rec_dat.toString().trim() + value.rec_tim.toString().trim(), "YYYYMMDDHHmm").format("HH:mm")
      return (
        <div key={index} style={{ display: "flex", color: "rgba(59, 151, 225, 1)" }}>
          {time}
          {" "}
          {value.special.toString().trim()}
        </div>
      );
    });
    function valueFormat(value) {
      if (value === "" || value === null || value === undefined) {
        return "--";
      }
      return value.toString();
    }

    const weightList = caseinfo.WEIGHT;
    const Birthday = caseinfo.PBASINGNINFO.PBIRTHDT;
    const BornWeight = WardState.BornWeight(weightList, Birthday);
    const DayWeight = WardState.selectweight(weightList, PickerDate);
    return (
      <div>
        <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between", width: "70vw" }}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <DatePicker
              getCalendarContainer={triggerNode => triggerNode.parentNode}
              disabledDate={current => {
                return (
                  current > moment().startOf("day") ||
                  current <
                  moment(
                    caseinfo.PBASINGNINFO.PBIRTHDT.toString(),
                    "YYYYMMDD"
                  ).endOf()
                );
              }}
              width={120}
              value={moment(this.state.PickerDate)}
              allowClear={false}
              onChange={this.onDateChange}
            />
            <div
              style={{
                display: "grid",
                gridTemplateColumns: "1fr 1fr 1fr",
                gridColumnGap: "1px",
                padding: "2px 10px 0px 10px"
              }}
            >
              <Button
                value="B"
                shape="circle"
                icon="left-circle"
                onClick={this.trainDay}
              />
              <Button
                value="N"
                shape="circle"
                icon="minus-circle"
                onClick={this.trainDay}
              />
              <Button
                value="A"
                shape="circle"
                icon="right-circle"
                onClick={this.trainDay}
              />
            </div>
          </div>
          <div style={{ fontSize: "16px" }}>
            當日年齡: {WardState.bornstatusFormat(status.week, status.day)} <spam style={{ fontWeight: "600", color: "black" }}>{WardState.CalculateAgeFromPickerDate(status, this.state.PickerDate)}</spam>
          </div>
          <div style={{ fontSize: "16px" }}>
            當日體重: {WardState.valueFormat(status.BornWeight)}<spam style={{ fontWeight: "600", color: "black" }}>{WardState.WeightListFilter(caseinfo.WEIGHT, this.state.PickerDate)}g</spam>
          </div>
        </div>
        <div>
          <div
            style={{
              backgroundColor: "#71b0e46e",
              borderTopLeftRadius: "4px",
              borderTopRightRadius: "4px",
              height: "36px",
              padding: "0px 16px",
              display: "flex",
              alignItems: "center"
            }}
          >
            <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>
              特殊護理事件：
            </t2>
            <t2
              style={{
                width: "20px",
                height: "20px",
                borderRadius: "99em",
                background: "black",
                color: "white",
                display: "flex",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              {splist.length}
            </t2>
          </div>
          <div style={{ maxHeight: "100px", width: "100%", overflow: "auto", paddingLeft: "16px" }}>
            {specialListRender}
          </div>
        </div>
        <div className="flootsheetlayout">
          {
            this.state.isLoading
              ? (
                <Icon type="loading" />
              )
              : (
                <Flootsheetio
                  PickerDate={this.state.PickerDate}
                  caseinfo={caseinfo}
                ></Flootsheetio>
              )
          }
        </div>
      </div>
    );
  }
}

export default Flowsheetlayout;
