import React, { Component } from 'react';
import { Progress, Select, DatePicker } from 'antd';
import Systemlogtable from './Systemlogtable'
import * as ApiProvider from '../API/ApiProvider';

class Systemlog extends Component {

    state = { startTime: "", endTime: "", totalSpace: 0, usingSpace: 0, percent: 0, option: 0, loginlogList: [], apilogList: [], page: 1 }

    componentDidMount() {
        ApiProvider.getSpace()
            .then(data => {
                const totalSpace = data.data[0].total / 1024 / 1024 / 1024
                const usingSpace = (data.data[0].total - data.data[0].unuse) / 1024 / 1024 / 1024
                const percent = (usingSpace / totalSpace) * 100
                this.setState({ totalSpace: totalSpace.toFixed(2), usingSpace: usingSpace.toFixed(2), percent: percent.toFixed(2) })
            })
            .catch(err => { })
        this.handleChange = this.handleChange.bind(this)
    }

    onChange(date, dateString) {
        this.setState({ startTime: dateString[0], endTime: dateString[1] }, () => {
            if (this.state.option === 0) {
                this.queryLoginLogdata()
            } else if (this.state.option === 1) {
                this.queryApiLogdata()
            }
        });
    }

    queryLoginLogdata() {
        if (this.state.startTime !== "" && this.state.endTime !== "") {
            const st = new Date(this.state.startTime.split('-')[0], this.state.startTime.split('-')[1] - 1, this.state.startTime.split('-')[2]).toISOString().split('.')[0];
            const et = new Date(this.state.endTime.split('-')[0], this.state.endTime.split('-')[1] - 1, this.state.endTime.split('-')[2]).toISOString().split('.')[0];

            ApiProvider.getLoginlog(st, et)
                .then(data => {
                    this.setState({ loginlogList: data.data, apilogList: [] })
                })
                .catch(err => { })
        }
    }

    queryApiLogdata() {
        if (this.state.startTime !== "" && this.state.endTime !== "") {
            ApiProvider.getApilog(this.state.startTime)
                .then(data => {
                    this.setState({ apilogList: data.data, loginlogList: [] })
                })
                .catch(err => { })
        }
    }

    getPages(pages) {
        this.setState({ page: pages })
    }

    handleChange(value) {
        this.setState({ option: value })
    }
    render() {
        const { Option } = Select;
        const { RangePicker } = DatePicker;
        const onChange = this.onChange.bind(this);

        return (
            <div>
                <div style={{ fontSize: '22px' }}>系統空間使用狀況</div>
                <div style={{ marginTop: "10px" }}><Progress percent={this.state.percent} showInfo={false} /></div>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: "10px" }}>
                    <div>
                        <div>已使用空間/總空間</div>
                        <div>{this.state.usingSpace}GB({this.state.percent}%)/{this.state.totalSpace}GB</div>
                    </div>
                    <div>
                        <div>收案中的個案資料0.9GB(10.5%)</div>
                        <div>0.9GB(10.5%)</div>
                    </div>
                </div>
                <div style={{ marginTop: '20px' }}>
                    <div style={{ fontSize: '22px' }}>Event Log</div>
                    <div style={{ marginTop: "10px" }}>
                        <Select placeholder='選擇log' style={{ width: 200 }} onChange={this.handleChange.bind(this)}>
                            <Option value={0}>Login log</Option>
                            <Option value={1}>API access log</Option>
                        </Select>
                        <RangePicker getCalendarContainer={triggerNode => triggerNode.parentNode} onChange={onChange} style={{ marginLeft: "10px", width: "250px" }} />
                    </div>
                    <div style={{ marginTop: '10px' }}>
                        <Systemlogtable loginlogList={this.state.loginlogList} apilogList={this.state.apilogList} optionValue={this.state.option} getPages={this.getPages.bind(this)} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Systemlog
