import React, { Component } from 'react'
import * as FilterReg from '../../Function/FiliterReg'
import moment from 'moment'
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider'
import Chronicrttablecell from './ChronicRTTableCell'
import * as QSPEList from '../../Resource/QSPE'

const TimeLine = (interval, index) => {
    const lineStyle = {
        width: '',
        color: '',
    }
    if (interval === 0) {
        if (index === 0 || (index % 24) === 0) {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        } else if (index % 8 === 0) {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        }
        else {
            lineStyle.width = '1px';
            lineStyle.color = '#eeeeee';
            return lineStyle;
        }
    }
    if (interval === 1) {
        if (index === 0 || (index % 6) === 0) {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        } else if (index % 2 === 0) {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        }
        else {
            lineStyle.width = '1px';
            lineStyle.color = '#eeeeee';
            return lineStyle;
        }
    }
    if (interval === 2) {
        if (index === 0 || (index % 3) === 0) {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        } else {
            lineStyle.width = '2px';
            lineStyle.color = 'rgba(0,0,0,0.2)';
            return lineStyle;
        }
    }
    if (interval === 3) {
        lineStyle.width = '2px';
        lineStyle.color = 'rgba(0,0,0,0.2)';
        return lineStyle;
    }
}

class Chronicrttable extends Component {
    state = {
        QSPE: [],
        preCID: null,
        prePickerdate: null,
        preInterval: null
    }
    componentDidMount() {
        const { caseinfo, PickerDate, select_interval } = this.props
        this.setState({ preCID: caseinfo.CASEID, prePickerdate: PickerDate, preInterval: select_interval })
        const timer = this.timerFormat(PickerDate, select_interval)
        const start = moment(timer.StartTime).format("YYYYMMDDHHmm")
        const end = moment(timer.EndTime).format("YYYYMMDDHHmm")
        this.getQSPE(caseinfo.CASEID, start, end)
    }
    componentDidUpdate() {
        const { caseinfo, PickerDate, select_interval } = this.props
        const { preCID, prePickerdate, preInterval } = this.state
        if (caseinfo.CASEID !== preCID || PickerDate !== prePickerdate || preInterval !== select_interval) {
            const { caseinfo, PickerDate } = this.props
            this.setState({ preCID: caseinfo.CASEID, prePickerdate: PickerDate, preInterval: select_interval })
            const timer = this.timerFormat(PickerDate, select_interval)
            const start = moment(timer.StartTime).format("YYYYMMDDHHmm")
            const end = moment(timer.EndTime).format("YYYYMMDDHHmm")
            this.getQSPE(caseinfo.CASEID, start, end)
        }

    }
    getQSPE(CID, st, et) {
        const xm2 = new X2JS()
        ApiProvider.getQSPE(CID, st, et)
            .then(data => {
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(parsedata)) {
                    this.setState({ QSPE: parsedata })
                }
                else {
                    this.setState({ QSPE: [parsedata] })
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        // this.setState({ QSPE: QSPEList.data })
    }

    timerFormat(PickerDate, Interval) {
        const EndTime = PickerDate + (31 * 60 * 60 * 1000)
        if (Interval === 0) {
            return {
                StartTime: EndTime - (24 * 60 * 60 * 1000),
                EndTime: EndTime,
                Interval: 24,
                grid: 1
            }
        }
        if (Interval === 1) {
            return {
                StartTime: EndTime - (3 * 24 * 60 * 60 * 1000),
                EndTime: EndTime,
                Interval: 18,
                grid: 4
            }

        }
        if (Interval === 2) {
            return {
                StartTime: EndTime - (7 * 24 * 60 * 60 * 1000),
                EndTime: EndTime,
                Interval: 21,
                grid: 8
            }

        }
        return {
            StartTime: EndTime - (14 * 24 * 60 * 60 * 1000),
            EndTime: EndTime,
            Interval: 14,
            grid: 24
        }
    }



    table_content() {
        function formatDataList(startTime, interval, list, grid) {
            var timerList = []
            for (let index = 0; index < interval; index++) {
                const start = startTime + (index * grid * 60 * 60 * 1000)
                const end = startTime + ((index + 1) * grid * 60 * 60 * 1000)
                const listFilter = list.filter(function (info, index, arr) {
                    const time = moment(info.Date.toString() + info.Time.toString(), "YYYYMMDDHHmm").valueOf()
                    return start <= time && time < end
                })
                timerList.push({
                    length: listFilter.length,
                    element: listFilter,
                    startTime: start,
                    endTime: end
                })
            }
            return timerList
        }

        const { select_interval, PickerDate } = this.props
        const QSPE = this.state.QSPE
        const dataList = FilterReg.QSPERegList(QSPE)
        if (dataList === null || dataList === undefined || dataList.length === 0) {
            return null
        }
        const timer = this.timerFormat(PickerDate, select_interval)
        const calldata = formatDataList(timer.StartTime, timer.Interval, dataList, timer.grid)
        const cellRender = calldata.map(function (value, index, arr) {
            return (
                <Chronicrttablecell lineColor={TimeLine(select_interval, index)} key={index} cellelement={value}></Chronicrttablecell>
            );
        })
        return cellRender
    }
    render() {
        const { select_interval, PickerDate } = this.props
        const timer = this.timerFormat(PickerDate, select_interval)
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", height: "40px" }}>
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center", background: "rgba(59, 151, 255,.4)" }}>
                    <t2>呼吸治療設定</t2>
                </div>
                {/* <div style={{ display: "flex", paddingLeft: "16px", alignItems: "center" }}>
                    <t2>呼吸治療設定</t2>
                </div> */}
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + timer.Interval + ",1fr)" }}>
                    {this.table_content()}
                </div>
            </div>
        );
    }
}

export default Chronicrttable
