import React, { Component } from 'react';
import Bedinformation from './Bedinfo'
import { DatePicker, Button } from 'antd';
import moment from 'moment';
import * as apiProvide from '../API/ApiProvider'
import { yyyymmddformat } from '../Function/Timeformat'
import './WardBedinformation.scss'

class Wardbedinformation extends Component {
    state = {
        subselectdate: null,
        selectid: null,
        PickerDate: moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf(),
        bedinfo: []
    }
    componentDidMount() {
        this.apiData(this.state.PickerDate)
    }
    apiData(PickerDate) {
        apiProvide.getWardDebInfo(moment(PickerDate).format("YYYY-MM-DD"))
            .then(data => {
                this.setState({ bedinfo: data.data })
            })
            .catch(err => { console.log(err) })
    }
    onChangeDate = (date, datestring) => {
        this.setState({
            PickerDate: date.valueOf(),
            selectid: null
        }, () => { this.apiData(this.state.PickerDate) })
    }
    callbackFunction = (idnumber) => {
        this.setState({
            selectid: idnumber.idkey,
            subselectdate: idnumber.time
        })
    }
    transferListlayout(trans) {
        const RenderCell = trans.map(function (element, index, arr) {
            return (
                <div className='wardbedinfoitemcontextgridRow'>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>{element.caseid}</t2>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>{element.pnamec}</t2>
                </div>
            );
        })
        return (
            <div>
                <div className='wardbedinfoitemcontextgridRow'>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>病歷號碼</t2>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>姓名</t2>
                </div>
                {RenderCell}
            </div>
        );
    }
    infoitem() {
        const selectDateitem = this.state.subselectdate
        const bedinfo = this.state.bedinfo
        const filterdate = bedinfo.filter(function (info, index) {
            const tempDate = moment(info.Date, 'YYYYMMDD')
            return yyyymmddformat(tempDate) === yyyymmddformat(selectDateitem)
        })

        if (this.state.selectid === null) {
            return null
        }
        else {
            const inType = filterdate.length === 0
            const outType = filterdate.length === 0

            const transIn = filterdate.length === 0 ? 0 : filterdate[0].transoutIncountperson
            const transOut = filterdate.length === 0 ? 0 : filterdate[0].transoutOutcountperson

            return (
                <div>
                    <div className='wardbedinfoitemcontext'>
                        <div className='wardbedinfoitemcontextgrid'>
                            <div>
                                <t2 className='wardbedinfoitemcontexttitle'>轉入病人清單</t2>
                                <div className='wardbedinfoitemcontextgridlayout'>
                                    {inType ? <div className='nopatient'>無病人</div> : this.transferListlayout(transIn)}
                                </div>
                            </div>
                            <div>
                                <t2 className='wardbedinfoitemcontexttitle'>轉出病人清單</t2>
                                <div className='wardbedinfoitemcontextgridlayout'>
                                    {outType ? <div className='nopatient'>無病人</div> : this.transferListlayout(transOut)}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
            // }
        }

    }
    bedinfotitle() {
        const maparray = ['日期', '佔床數', '轉出', '轉入', '侵入式呼吸器', '非侵入式呼吸器', '開立PN']
        const title = maparray.map(function (info, index) {
            return (
                <div className='wardbedinfotablerowtitle'>
                    <t2>{info}</t2>
                </div>
            );
        })
        return title
    }
    weekbedinfo() {
        var infolist = []
        const infoList = this.state.bedinfo
        const dateTime = this.state.PickerDate
        for (let i = 0; i < 7; i++) {
            const localtime = dateTime - (i * 24 * 60 * 60 * 1000);
            const filterdate = infoList.filter(function (info, index) {
                const tempDate = moment(info.Date, 'YYYYMMDD').valueOf()
                return tempDate === localtime
            })
            // console.log(filterdate)
            if (filterdate.length === 0) {
                infolist.push(
                    <div >
                        <Bedinformation data={null} key={i} localtime={localtime} selectkey={this.state.selectid} idkey={i} parentCallback={this.callbackFunction}></Bedinformation>
                    </div>
                )
            }
            else {
                infolist.push(
                    <div >
                        <Bedinformation data={filterdate[0]} key={i} localtime={localtime} selectkey={this.state.selectid} idkey={i} parentCallback={this.callbackFunction}></Bedinformation>
                    </div>
                )
            }
        }
        infolist.reverse()
        return infolist

    }

    trainDay = (e) => {
        // const { caseinfo } = this.props
        const { PickerDate } = this.state
        // const bron = caseinfo.PBASINGNINFO.PBIRTHDT
        // const bronDate = moment(moment(bron).format('YYYYMMDD')).valueOf()

        const day = 24 * 60 * 60 * 1000

        switch (e.target.value) {
            case 'B':
                const bTime = PickerDate - day
                // const bTemp = bTime - (6 * day)
                // bronDate <= bTemp && this.onChange(moment(bTime))
                this.onChangeDate(moment(bTime))
                break;
            case 'N':
                const nTime = moment(moment().format('YYYYMMDD'))
                this.onChangeDate(nTime)
                break;
            case 'A':
                const aTime = PickerDate + day
                const aTemp = moment(moment().format('YYYYMMDD')).valueOf()
                aTime <= aTemp && this.onChangeDate(moment(aTime))
                break;
            default:
                break;
        }
    }

    render() {
        return (
            <div style={{ marginTop: "10px" }}>
                <div className='wardbrdinfomationtitle'>
                    <centerstyle>
                        <t2 style={{ fontSize: "16px", color: "rgba(0, 0, 0, .7)" }}>病房每日統計</t2>
                        <t2 style={{ fontSize: "14px", fontStyle: "italic", color: "rgba(0, 0, 0, .7)", paddingLeft: "10px" }}>每日上午七點統計</t2>
                    </centerstyle>
                    <div style={{ display: "grid", gridTemplateColumns: "140px 120px", gridColumnGap: "5px" }}>
                        <DatePicker getCalendarContainer={triggerNode => triggerNode.parentNode} showToday={true} disabledDate={(current) => { return current && current > moment().startOf('day') }} value={moment(this.state.PickerDate)} format={"YYYY/MM/DD"} onChange={this.onChangeDate} allowClear={false} />
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridColumnGap: "1px", marginLeft: "10px" }}>
                            <Button value="B" shape="circle" icon="left-circle" onClick={this.trainDay} />
                            <Button value="N" shape="circle" icon="minus-circle" onClick={this.trainDay} />
                            <Button value="A" shape="circle" icon="right-circle" onClick={this.trainDay} />
                        </div>
                    </div>
                </div>
                <div className='wardbedinformationcontext'>
                    <div>
                        {this.bedinfotitle()}
                    </div>
                    {this.weekbedinfo()}
                </div>
                {this.infoitem()}
            </div>
        );
    }
}

export default Wardbedinformation;
