import React, { Component } from 'react'
import './ChronicWeightTable.scss'
import moment from 'moment'
import * as QTWPER from '../../Resource/QTWPER'
// import * as ApiProvider from '../../API/ApiProvider'

class Chronicweighttable extends Component {
    // 0:一天 1:三天 2:一週 3:兩週
    state = { QTWPER: null }
    componentDidMount() {
        const { caseinfo } = this.props
        const weight = caseinfo.WEIGHT
        this.setState({ QTWPER: weight })
        // this.setState({ QTWPER: QTWPER.data.outmsg.resconts.rescont })
    }

    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    celltable() {
        function changegridnumber(key) {
            switch (key) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state, openTimeLine } = this.props
        const number = changegridnumber(chronic_state);
        function TimeLine(interval, index) {
            if (interval === 0) {
                if (index === 0 || (index % 24) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else if (index % 8 === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                }
                else {
                    return '1px rgba(220,220,220,1) solid';
                }
            }
            if (interval === 1) {
                if (index === 0 || (index % 6) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else if (index % 2 === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                }
                else {
                    return '1px rgba(220,220,220,1) solid';
                }
            }
            if (interval === 2) {

                if (index === 0 || (index % 3) === 0) {
                    return '2px rgba(0,0,0,0.2) solid';
                } else {
                    return '2px rgba(0,0,0,0.2) solid';
                }
            }
            if (interval === 3) {
                return '2px rgba(0,0,0,0.2) solid';
            }
        }
        function checkLineColor(openTimeLine, chronic_state, index) {
            if (openTimeLine) {
                return TimeLine(chronic_state, index);
            }
        }
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div key={index} className='cell' style={{ borderLeft: checkLineColor(openTimeLine, chronic_state, index) }}></div>
            )

        }
        return arr
    }
    weightRectRender() {
        function TimerFormat(interval, pickerdate) {
            const endTime = pickerdate + (31 * 60 * 60 * 1000)
            if (interval === 0) {
                return {
                    startTime: endTime - (24 * 60 * 60 * 1000),
                    endTime: endTime,
                    interval: 24,
                    grid: 1
                }
            }
            if (interval === 1) {
                return {
                    startTime: endTime - (3 * 24 * 60 * 60 * 1000),
                    endTime: endTime,
                    interval: 18,
                    grid: 4
                }
            }
            if (interval === 2) {
                return {
                    startTime: endTime - (7 * 24 * 60 * 60 * 1000),
                    endTime: endTime,
                    interval: 21,
                    grid: 8
                }
            }
            return {
                startTime: endTime - (14 * 24 * 60 * 60 * 1000),
                endTime: endTime,
                interval: 14,
                grid: 24
            }
        }
        const { chronic_state, caseinfo, PickerDate } = this.props
        // const QTPWERList = caseinfo.WEIGHT
        const QTPWERList = this.state.QTWPER
        if (QTPWERList === undefined || QTPWERList === null) {
            return null
        }
        const WeightList = QTPWERList.filter(function (info, index, arr) {
            return info.type === "TW"
        })

        const timer = TimerFormat(chronic_state, PickerDate)
        const width = document.body.clientWidth - 220
        const widthRate = width / (timer.endTime - timer.startTime)
        var WeightListFormat = []
        const sortWeightList = WeightList.sort(function (a, b) {
            const aTime = moment(a.rec_dat.toString() + a.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
            const bTime = moment(b.rec_dat.toString() + b.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
            return aTime > bTime ? 1 : -1;
        });
        var lastelement = null
        for (let index = 0; index < sortWeightList.length; index++) {
            const element = WeightList[index];
            const elementTime = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
            if (timer.startTime > elementTime) {
                const elementValue = parseInt(Number(element.value2) * 1000)
                lastelement = { Time: elementTime, Value: elementValue }
            }
            console.log(lastelement)
            if (timer.startTime <= elementTime && elementTime <= timer.endTime) {
                const elementValue = parseInt(Number(element.value2) * 1000)
                WeightListFormat.push({
                    Time: elementTime,
                    Value: elementValue
                })
            }
        }
        console.log(WeightListFormat)
        if (lastelement !== null) {
            WeightListFormat.splice(0,0,lastelement)
            lastelement = null
        }
        console.log(WeightListFormat)
        const sortWeight = WeightListFormat.sort(function (a, b) {
            return a.Time > b.Time ? 1 : -1;
        });
        var shift = 0
        var elementWidthList = []
        for (let index = 0; index < sortWeight.length; index++) {
            const element = sortWeight[index];
            if (index === 0) {
                if (element.Time < timer.startTime) {
                    shift = 0
                }
                else {
                    shift = (element.Time - timer.startTime) * widthRate
                }
            }
            if (sortWeight.length - 1 === index) {
                const time1 = element.Time
                const time2 = timer.endTime
                const elementWidth = (time2 - time1) * widthRate
                elementWidthList.push({
                    Value: element.Value,
                    width: elementWidth
                })
            }
            else {
                const time1 = sortWeight[index].Time
                const time2 = sortWeight[index + 1].Time
                const elementWidth = (time2 - time1) * widthRate
                elementWidthList.push({
                    Value: element.Value,
                    width: elementWidth
                })
            }
        }
        return {
            shift: shift,
            elementWidthList: elementWidthList
        }
    }
    render() {
        const { chronic_state, caseinfo, PickerDate } = this.props
        const weight = caseinfo.WEIGHT
        console.log(caseinfo)
        // const weight = this.state.QTWPER
        if (weight === null) { return null }
        function FormatEndTimeStamp(start, interval) {
            if (interval === 0) {
                return start - (24 * 60 * 60 * 1000)
            }
            if (interval === 1) {
                return start - (3 * 24 * 60 * 60 * 1000)

            }
            if (interval === 2) {
                return start - (7 * 24 * 60 * 60 * 1000)

            }
            if (interval === 3) {
                return start - (14 * 24 * 60 * 60 * 1000)

            }
        }
        const end = PickerDate + (31 * 60 * 60 * 1000)
        const start = FormatEndTimeStamp(end, chronic_state)
        const datafilter = weight.filter(function (value, index, arr) {
            const timeString = value.rec_dat.toString() + value.rec_tim.toString()
            const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
            return start < time && time < end
        })
        const datafilterTime = datafilter.map(function (value, index, arr) {
            const timeString = value.rec_dat.toString() + value.rec_tim.toString()
            const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
            return time
        })
        const diff = end - start
        var widthArray = []
        if (datafilter.length === 0) {
            // const element = weight[0]
            // widthArray.push({
            //     width: 100,
            //     weight: (element.value2) * 1000
            // })
        }
        if (datafilter.length === 1) {
            for (let index = 0; index < datafilter.length; index++) {
                const element = datafilter[index];
                const timeString = element.rec_dat.toString() + element.rec_tim.toString()
                const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
                const width = parseInt(100 * (time - start) / diff)
                widthArray.push({
                    width: width,
                    weight: Number(element.value2) * 1000
                })
            }
        }
        else {
            for (let index = 0; index < datafilter.length - 1; index++) {
                const element = datafilter[index];
                if (index === 0) {
                    const timeString = element.rec_dat.toString() + element.rec_tim.toString()
                    const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
                    const width = parseInt(100 * (time - start) / diff)
                    widthArray.push({
                        width: width,
                        weight: Number(element.value2) * 1000
                    })
                }
                else if (index === (datafilterTime.length - 1)) {
                    const timeString = element.rec_dat.toString() + element.rec_tim.toString()
                    const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
                    const width = parseInt(100 * (end - time) / diff)
                    widthArray.push({
                        width: width,
                        weight: Number(element.value2) * 1000
                    })
                }
                else {
                    const element2 = datafilter[index + 1]
                    const timeString2 = element2.rec_dat.toString() + element2.rec_tim.toString()
                    const time2 = moment(timeString2, "YYYYMMDDHHmm").valueOf()
                    const timeString = element.rec_dat.toString() + element.rec_tim.toString()
                    const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
                    const width = parseInt(100 * (time2 - time) / diff)
                    widthArray.push({
                        width: width,
                        weight: Number(element.value2) * 1000
                    })
                }
            }
        }
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'chronicweighttableoneday'
                case 1:
                    return 'chronicweighttablethreeday'
                case 2:
                    return 'chronicweighttableoneweek'
                case 3:
                    return 'chronicweighttabletwoweek'
                default:
                    return null
            }
        }
        const list = this.weightRectRender()
        if (list === null || list === undefined) {
            return null
        }
        const render = list.elementWidthList.map(function (value, index, arr) {
            return (
                <div key={index} style={{ border: "1px black solid", borderCollapse: "collapse", width: value.width + '%', height: "20", backgroundColor: "#3B97E1", color: "white", display: "flex", justifyContent: "center", alignItems: 'center' }}>{Math.round(value.Value)}g</div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", position: 'relative' }}>
                <div style={{ display: "flex", justifyContent: "left", alignItems: "center", background: "rgba(59, 151, 255,.4)", paddingLeft: "10px" }}>
                    <t2>體重</t2>
                </div>
                <div className={changegridstyle(chronic_state)}>{this.celltable()}</div>
                <div style={{ position: 'absolute', left: 140, paddingTop: "15px" }}>
                    <div style={{ paddingLeft: list.shift + "px", display: "flex", width: 'calc(100vw - 220px)', overflow: "hidden" }}>
                        {render}
                    </div>
                </div>
            </div>
        );
    }
}

export default Chronicweighttable
