import React, { Component } from 'react';
import './Examchart.scss';
import * as d3 from "d3";
import moment from 'moment';

class Examchart extends Component {
    state = {
        change: false
    }
    componentDidUpdate() {

        const { svg_id } = this.props
        d3.select(".tooltip").remove()
        d3.select("#" + svg_id).remove()
        this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
    }

    decideValue = (value) => {
        const tempValue = typeof (value) !== 'String' ? value.toString() : value
        const { max, min } = this.props
        if (tempValue === null || tempValue === undefined) {
            return null
        }
        if (tempValue.indexOf('<') != -1) {
            return min
        } else if (tempValue.indexOf('>') != -1) {
            return max
        } else {
            return tempValue
        }
    }

    drawChart() {
        const { id_key, svg_id, max, min, lineChart } = this.props

        const sT = new Date(moment(lineChart[0].date, 'YYYYMMDD').valueOf() - 24 * 60 * 60 * 1000)
        const endT = new Date(moment(lineChart[lineChart.length - 1].date, 'YYYYMMDD') + 24 * 60 * 60 * 1000)

        const fixLineChart = lineChart.filter(data => {
            return data.value !== "" && data.value !== null && data.value !== undefined
        })

        // 邊界
        const margin = { top: 20, right: 20, bottom: 30, left: 50 },
            width = 1000 - margin.left - margin.right,
            height = 500 - margin.top - margin.bottom;

        let x = d3.scaleTime().domain([sT, endT]).range([0, width - margin.left - margin.right])
        let y = d3.scaleLinear().domain([min, max]).range([height, 0])

        var div = d3.select("#" + id_key).append("div")
            .attr("class", "tooltip")
            .style("opacity", 0);

        let svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_id)
            .style("height", height + margin.top + margin.bottom)
            .style("width", width + margin.left + margin.right)
            .append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        svg.append('g')
            .call(d3.axisBottom(x).ticks(5).tickFormat(d3.timeFormat('%m/%d')))
            .attr("transform", `translate(0,${height})`)
            .style('stroke', 'black')
            .style('stroke-width', 1);

        svg.append('g')
            .call(d3.axisLeft(y))
            .style('stroke', 'black')
            .style('stroke-width', 1);

        let line = d3.line()
            .x(d => x(moment(d.date, 'YYYYMMDDHHmm').toDate()))
            .y(d => {
                const tempValue = this.decideValue(d.value)
                return y(tempValue)
            })
            .curve(d3.curveMonotoneX);

        let lines = svg.append('g').attr('class', 'lines');

        lines.selectAll("circle")
            .data(fixLineChart)
            .enter()
            .append('g')
            .append("circle")
            .attr("class", "lines-circle")
            .attr("cy", d => {
                const tempValue = this.decideValue(d.value)
                return y(tempValue)
            })
            .attr("cx", d => x(moment(d.date, 'YYYYMMDDHHmm').toDate()))
            .attr('r', 5)
            .on("mouseover", function (d) {
                div.transition()
                    .duration(100)
                    .style("opacity", .9);
                div.html(`檢驗時間 : ${d.date} <br/> 檢驗數值 : ${d.value}`)
                    .style("left", (d3.event.pageX - 200) + "px")
                    .style("top", (d3.event.pageY - 150) + "px");
            })
            .on("mouseout", function (d) {
                div.transition()
                    .duration(300)
                    .style("opacity", 0);
            })
            .style('opacity', 1)
            .style('fill', "blue");

        lines
            .selectAll('.line-group')
            .data(fixLineChart)
            .enter()
            .append('g')
            .attr('class', 'line-group')
            .append('path')
            .attr('class', 'line')
            .attr('d', line(fixLineChart))
            .style('stroke', 'black')
            .style('fill', 'none');
    }

    render() {
        function formatString(formatString) {
            console.log(formatString)
            if (formatString === "Ca++") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>Ca<sup>2+</sup></span>
            }
            if (formatString === "Mg++") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>Mg<sup>2+</sup></span>
            }
            if (formatString === "pCO2") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>pCO<sub>2</sub></span>
            }
            if (formatString === "pO2") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>pO<sub>2</sub></span>
            }
            if (formatString === "HCO3") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>HCO<sub>3</sub></span>
            }
            if (formatString === "FiO2") {
                return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>FiO<sub>2</sub></span>
            }
            if (formatString == 'Band' || formatString == 'Seg' || formatString == 'Lym') {
                return (
                    <div>
                        <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>{formatString}</span>
                        <span style={{ color: "rgba(0, 0, 0, 0.65)", fontSize: "16px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>%</span>
                    </div>
                )
            }
            return <span style={{ color: "rgba(0, 0, 0, 0.85)", fontSize: "20px", fontWeight: "500", lineHeight: "22px", wordWrap: "break-word" }}>{formatString}</span>
        }
        const { id_key, lineCharttitle } = this.props
        console.log(lineCharttitle)
        const title = lineCharttitle == 'Band' || lineCharttitle == 'Seg' || lineCharttitle == 'Lym' ? true : false
        return (
            <div>
                <div style={{ padding: "0px 24px 16px", color: "rgba(0, 0, 0, 0.65)", background: "#fff", borderBottom: "1px solid #e8e8e8", borderRadius: "4px 4px 0 0" }}>
                    {formatString(lineCharttitle)}
                </div>
                <div id={id_key}></div>
            </div>
        );
    }
}

export default Examchart;
