import React, { Component } from 'react';
import RTbreathetable from './RTbreathetable'
// import { DatePicker } from 'antd';
import * as FilterReg from '../../Function/FiliterReg'
import X2JS from 'x2js';
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as QSPEList from '../../Resource/QSPE'

class RTbreathe extends Component {
    state = {
        preCID: null,
        QSPE: []
    }
    componentDidMount() {
        const { caseinfo, PickerDateRange } = this.props
        this.setState({ preCID: caseinfo.CASEID, preST: PickerDateRange[0], preET: PickerDateRange[1] })
        this.getQSPEApi(caseinfo.CASEID, PickerDateRange[0], PickerDateRange[1])

    }
    componentDidUpdate() {
        const { caseinfo, PickerDateRange } = this.props
        const { preCID, preST, preET } = this.state
        const CID = caseinfo.CASEID
        const ST = PickerDateRange[0]
        const ET = PickerDateRange[1]
        if (CID !== preCID || ST !== preST || ET !== preET) {
            this.setState({ preCID: caseinfo.CASEID, preST: PickerDateRange[0], preET: PickerDateRange[1] })
            this.getQSPEApi(caseinfo.CASEID, PickerDateRange[0], PickerDateRange[1])
        }
    }
    getQSPEApi(CID, st, et) {
        const xm2 = new X2JS()
        const start = moment(st).format("YYYYMMDDHHmm")
        const end = moment(et).format("YYYYMMDDHHmm")
        ApiProvider.getQSPE(CID, start, end)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const result = xm2.xml2js(data.data).outmsg
                if (Number(result.ret) === 0) {
                    const resultData = result.resconts.rescont
                    if (Array.isArray(resultData)) {
                        this.setState({ QSPE: resultData })
                    }
                    else {
                        this.setState({ QSPE: [resultData] })
                    }
                }
                return []
            })
            .catch(err => {
                console.log("getQSPE NG " + JSON.stringify(err))
            });
        // this.setState({ QSPE: QSPEList.data })
    }

    createRTbreathetable = () => {
        const { caseinfo, PickerDateRange, searchbar } = this.props
        const { QSPE } = this.state
        let DataList = FilterReg.QSPERegList(QSPE)

        //這邊不用再分時間
        DataList = DataList.filter(data => {
            let time = moment(data.Date + data.Time, "YYYYMMDDHHmm").valueOf()
            return PickerDateRange[0] <= time && time <= PickerDateRange[1]
        })

        if (Object.keys(DataList).length > 1) {
            DataList = DataList.sort((a, b) => {
                let tempTimeA = moment(a.Date + a.Time, "YYYYMMDDHHmm").valueOf()
                let tempTimeB = moment(b.Date + b.Time, "YYYYMMDDHHmm").valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })
        }

        return <RTbreathetable DataList={DataList} caseinfo={caseinfo} searchbar={searchbar}></RTbreathetable>
    }

    render() {
        const { searchbar } = this.props
        return (
            <div >
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>
                        {searchbar ? "呼吸器設定紀錄" : "近三日呼吸器設定"}
                    </t2>
                </div>
                {this.createRTbreathetable()}
            </div>
        );
    }
}

export default RTbreathe
