import React, { Component } from 'react';
import * as d3 from "d3";
import '../Patientview/CrossSectionView/Crosssectionvitalsignchart.scss'
import { yyyymmddformat } from '../Function/Timeformat'
import moment from 'moment';

class Vitalxaxisbot extends Component {
    state = {
        change: false,
        viewwidth: window.innerWidth - 221
    }
    componentDidUpdate() {

        const { svg_id } = this.props
        d3.select("#" + svg_id).remove()
        this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
        window.addEventListener('resize', this.debounceWindowSize);
    }
    componentWillUnmount() {
        console.log("123123123123")
    }
    debounce(func, wait, immediate) {
        var timeout;
        return () => {
            var context = this, args = arguments;
            var later = function () {
                timeout = null;
                if (!immediate) func.apply(context, args);
            };
            var callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if (callNow) func.apply(context, args);
        };
    }

    debounceWindowSize = this.debounce(this.recalcWindowSize, 500);

    componentWillUnmount() {
        window.removeEventListener('resize', this.debounceWindowSize);
    }

    recalcWindowSize() {
        this.setState({ viewwidth: window.innerWidth - 221 });
    }

    drawChart() {
        function intervaldays(select_date, select_interval) {
            switch (select_interval) {
                //一天
                case 0:
                    return moment(select_date).add(7, 'hours').valueOf();
                //三天
                case 1:
                    return moment(select_date).subtract(2, 'days').add(7, 'hours').valueOf();
                //一週
                case 2:
                    return moment(select_date).subtract(6, 'days').add(7, 'hours').valueOf();
                //兩週
                case 3:
                    return moment(select_date).subtract(13, 'days').add(7, 'hours').valueOf();
                default:
                    return null
            }
        }

        const { id_key, svg_id, select_interval, select_date } = this.props;
        const Start_time = intervaldays(select_date, select_interval)
        const End_time = moment(select_date).add(1, 'day').startOf('day').add(7, 'hours').valueOf();
        var xaxis = []
        if (select_interval === 0) {
            xaxis.push(Start_time);
            const time = moment(Start_time).add(1, 'day').startOf('day').valueOf();
            xaxis.push(time)
        }
        if (select_interval === 1) {
            xaxis.push(Start_time);
            for (let index = 1; index < 4; index++) {
                const time = moment(Start_time).add(index, 'days').startOf('day').valueOf();
                xaxis.push(time)
            }
        }

        if (select_interval === 2) {
            xaxis.push(Start_time);
            for (let index = 1; index < 8; index++) {
                const time = moment(Start_time).add(index, 'days').startOf('day').valueOf();
                xaxis.push(time)
            }
        }

        if (select_interval === 3) {
            xaxis.push(Start_time);
            for (let index = 1; index < 7; index++) {
                const time = moment(Start_time).add(2 * index, 'days').startOf('day').valueOf();
                xaxis.push(time)
            }
        }
        var x = d3.scaleLinear()
            .domain([Start_time, End_time])
            .range([0, this.state.viewwidth - 1])


        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_id)
            // .attr("viewbox", [0, 0, viewwidth + 60, 25])
            .style("width", this.state.viewwidth)
            .style("height", 25)
            .append("g")
            .attr("transform", "translate(" + 14 + "," + -9 + ")");


        function multiFormat(date) {
            return moment(date).format("MM/DD")
        }


        var xAxis = d3.axisBottom(x)
            .tickValues(xaxis)
            .tickSize(0, 0)
            .tickPadding(10)
            .tickFormat(function (d) { return multiFormat(d); });


        svg.append("g")
            .call(xAxis)
            .style("font-size", "11px")
            .attr("class", "axis");

    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default Vitalxaxisbot;
