import React, { Component } from 'react';
import { format_bednumber } from '../../Commonfunction'
import editlogo from '../../Image/svg/edit2.svg'
import { Modal, Input, DatePicker } from 'antd';
import './UnpreviewWardCardDetail.scss'
import { bednumberformat } from '../../Function/Wardbedfunction'

class Unpreviewwardcarddetail extends Component {
    state = { visible: false, powerLogin: 0 };

    showModal = () => {
        this.setState({
            visible: true,
            databool: false,
            data: null
        });
    };

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    handleOk(string) {

        const bednumber = document.getElementById("bednumber").value;
        const hisnumber = document.getElementById("hisid").value;
        const birweek = document.getElementById("birweek").value;
        const birthday = string;
        const weight = document.getElementById("weight").value;
        const statedata = {
            "BedNumber": bednumber,
            "id": hisnumber,
            "birweek": birweek,
            "birthday": birthday,
            "weight": weight
        }
        this.setState({
            visible: false,
            data: statedata,
            databool: false
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    // savestatus(string) {
    // }

    render() {
        function modedisplay(mode) {
            if (mode) {
                return "none"
            }
            else {
                return "block"
            }
        }
        const number = this.props.bednumber
        const { previewmode } = this.props
        return (
            <div style={{ height: '100%' }}>
                <div style={{ height: '40px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', backgroundColor: "rgba(215, 238, 255, 1)", padding: "9px 10px" }}>
                    <t3 style={{ color: "rgba(61, 119, 181, 1)" }}>NI-{format_bednumber(number)}</t3>
                    <div className='wardcardeditbtn' onClick={this.showModal} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", cursor: "pointer" }}>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>
                    <Modal
                        width={270}
                        title="填寫病床資訊"
                        visible={this.state.visible}
                        onOk={() => this.handleOk("2019/12/12")}
                        onCancel={this.handleCancel}
                    >
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr 1fr", gridRowGap: "10px" }}>
                            <div className='modulecontent'>
                                <centerstyle>
                                    <t2>病床號碼:</t2>
                                </centerstyle>
                                <div style={{ display: "flex", alignItems: "center" }}>
                                    <div>{bednumberformat(number)}</div>
                                </div>
                            </div>
                            <div className='modulecontent'>
                                <centerstyle>
                                    <t2>病歷號碼:</t2>
                                </centerstyle>
                                <Input id="hisid_wardcard" />
                            </div>
                            <div className='modulecontent'>
                                <centerstyle>
                                    <t2>出生週數:</t2>
                                </centerstyle>
                                <div style={{ display: "flex", alignItems: "center" }}>
                                    <Input id='week' placeholder="20" style={{ marginRight: "5px" }}></Input>
                                    <t2 style={{ marginRight: "5px" }}>週</t2>
                                    <Input id='days' placeholder="20" style={{ marginRight: "5px" }}></Input>
                                    <t2>日</t2>
                                </div>
                            </div>
                            <div className='modulecontent'>
                                <div style={{ display: 'flex', justifyContent: "space-between", alignItems: "cneter", width: "69px" }}>
                                    <t2>生</t2>
                                    <t2>日:</t2>
                                </div>
                                <DatePicker getCalendarContainer={triggerNode => triggerNode.parentNode} placeholder="選擇日期" id="birthday" />
                            </div>
                            <div className='modulecontent'>
                                <centerstyle>
                                    <t2>出生體重:</t2>
                                </centerstyle>
                                <div style={{ display: 'flex' }}><Input placeholder="120" id="weight" />&nbsp;g</div>
                            </div>
                        </div>
                    </Modal>
                </div>
                <div style={{ height: 'calc(100% - 40px)',display: 'flex', justifyContent: 'center', alignItems: 'center', padding: "10px" }}>
                    <div style={{ fontSize: '28px', color: "rgba(0,0,0,0.1)" }}>
                        空床
                    </div>
                </div>
                {/* <div style={{ display: modedisplay(previewmode) }}>
                    <div style={{ height: '30px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: "10px" }}></div>
                </div> */}
            </div>
        );
    }
}

export default Unpreviewwardcarddetail;
