import React, { Component } from 'react'
import rtlogo from '../../Image/svg/RT.svg'
import { Tooltip } from 'antd'
import moment from 'moment'

class Chronicrttablecell extends Component {
    ToolTipRender() {
        const { cellelement } = this.props
        const startTime = moment(cellelement.startTime).format("MM/DD HH:mm")
        const endTime = moment(cellelement.endTime).format("MM/DD HH:mm")
        const RTListRender = cellelement.element.map(function (info, index, arr) {
            const time = moment(info.Date + info.Time, "YYYYMMDDHHmm").format("MM/DD HH:mm")
            const Mode = info.Mode
            const Setting = info.Setting
            return (
                <div>
                    <div>Time:{time}</div>
                    <div>Mode:{Mode}</div>
                    <div style={{ display: "flex" }}>
                        <div>Setting:</div>
                        <div>{Setting}</div>
                    </div>
                </div>
            );
        })

        return (
            <div>
                <div>{startTime}~{endTime}</div>
                {RTListRender}
            </div>
        );
    }
    render() {
        function checkLineColor(lineColor) {
            if(lineColor === null){
                return '';
            }else{
                return `${lineColor.width} solid ${lineColor.color}`;
            }
        }
        function changetext(text) {
            if (text === 0) {
                return '--'
            }
            return <img src={rtlogo} alt="rtlogo" />
        }
        const { cellelement } = this.props
        if (cellelement.length === 0) {
            return <div style={{ display: "flex", justifyContent: "center", alignItems: 'center', border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse',borderLeft: checkLineColor(this.props.lineColor) }}>--</div>
        }
        return (
            <Tooltip placement="right" autoAdjustOverflow={true} title={this.ToolTipRender()} overlayStyle={{ maxWidth: "800px", background: "white" }}>
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', cursor: "pointer", borderLeft: checkLineColor(this.props.lineColor) }}>
                    {changetext(cellelement.length)}
                </div>
            </Tooltip >
        );
    }
}
export default Chronicrttablecell