import React, { Component } from 'react';
import moment from 'moment';
import { Icon } from 'antd'

const calcBorderLeft = (intervalStatus, index, Status) => {
    if (Status === "Crossection") {
        return '1px solid #eeeeee';
    }
    if (intervalStatus === 0) {
        if (index % 24 === 0) {
            return '2px solid rgba(0,0,0,0.2)';
        } else if (index % 8 === 0) {
            return '2px solid rgba(0,0,0,0.2)';
        } else {
            return '1px solid #eeeeee';
        }
    } else if (intervalStatus === 1) {
        if (index % 6 === 0) {
            return '2px solid rgba(0,0,0,0.2)';
        } else if (index % 2 === 0) {
            return '2px solid rgba(0,0,0,0.2)';
        } else {
            return '1px solid #eeeeee';
        }
    } else if (intervalStatus === 2) {
        if (index % 3 === 0) {
            return '2px solid rgba(0,0,0,0.2)';
        } else {
            return '2px solid rgba(0,0,0,0.2)';
        }
    } else {
        return '2px solid rgba(0,0,0,0.2)';
    }
}

class IOpageCell extends Component {
    render() {
        function showFloat(value) {
            if (typeof (value) === 'number' && value % 1 === 0) {
                return value
            }
            return parseFloat(value).toFixed(1)
        }

        const { IntervalStatus, Status, data, weightList, index } = this.props
        // console.log(weightList)
        const startTime = data.start
        const endTime = data.end
        const element = data.element
        const urine = data.urine
        var weightData = 0
        if (Array.isArray(weightList)) {
            for (let index = 0; index < weightList.length; index++) {
                const element = weightList[index];
                const elementTime = moment(element.rec_dat.toString().trim() + element.rec_tim.toString().trim(), "YYYYMMDDHHmm").valueOf()
                // console.log(moment(elementTime).format("YYYYMMDDHHmmss"))
                // console.log(moment(endTime).format("YYYYMMDDHHmmss"))
                if (weightData === 0) {
                    if (elementTime <= endTime) {
                        weightData = Number(element.value2)
                    }
                }
            }
        }
        var urineValue = 0
        if (Array.isArray(element)) {
            for (let index = 0; index < element.length; index++) {
                const info = element[index];
                const value = Number(info.out_val2)
                urineValue = urineValue + value
            }
        }
        if (weightData !== 0 && urineValue !== 0 && urine !== 0) {
            // console.log((urineValue / urine) / weightData)
            const cellValue = parseFloat((urineValue / urine) / weightData).toFixed(1)
            return (
                <div key={index} style={{
                    borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #eeeeee", borderCollapse: 'collapse',
                    borderLeft: calcBorderLeft(IntervalStatus, index, Status),
                    backgroundColor: Status !== "Crossection" ?
                        (parseFloat(cellValue) === '' ? 'white' : "rgba(175,75,25,0.05)")
                        :
                        (index === 3 ? "rgba(175,75,25,0.2)"
                            :
                            "rgba(175,75,25,0.05)")
                }}>
                    {(parseFloat(cellValue) === '' && Status !== "Crossection") || <div style={{
                        fontWeight: (Status === "Crossection" && index === 3) ? "600" : "",
                        color: (parseFloat(cellValue) < 0.5 || parseFloat(cellValue) > 5) ? "#FF0000" : "black"
                    }}>
                        {(parseFloat(cellValue) === '' || parseFloat(cellValue) === 0) ? '--'
                            :
                            parseFloat(cellValue) >= 0 ? `${showFloat(cellValue)}` : showFloat(cellValue)}
                    </div>}
                </div>
            )
        }
        return (
            <div key={index} style={{
                borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center", border: "1px solid #eeeeee", borderCollapse: 'collapse',
                borderLeft: calcBorderLeft(IntervalStatus, index, Status),
                backgroundColor: Status !== "Crossection" ? "white"
                    :
                    index === 3 ? "rgba(175,75,25,0.2)"
                        :
                        "rgba(175,75,25,0.05)"
            }}>
                {Status === "Crossection" && <div style={{
                    fontWeight: index === 3 ? "600" : "",
                    color: "black"
                }}>--</div>}
            </div>
        )
    }

}

export default IOpageCell
