const Interval_grid = (number) => {
    switch (number) {
        case 0:
            return 24
        case 1:
            return 18
        case 2:
            return 21
        case 3:
            return 14
        default:
            return null
    }
}


const timeCheck = (interval, pickerDate) => {
    console.log(interval, pickerDate)
    // 選的日期7:00距離目前時間差距
    const duration = moment.duration(moment().diff(moment(pickerDate).startOf('day').hour(7)));
    // console.log(duration)
    // 如果選擇的日期在今天之前，那就直接選擇最後一格
    if (moment(pickerDate).isBefore(moment().startOf('day'))) {
        return Interval_grid(interval);
    } else if (interval === 0) { // day 目前距離幾個小時就幾格
        const hours = Math.floor(duration.asHours());
        return hours > 0 ? hours : 0;
    } else if (interval === 1) { // three day 每4小時一格，基礎 12 格是今天 7:00
        const step = Math.floor(duration.asHours() / 4);
        return 12 + step;
    } else if (interval === 2) { // one week 每8小時一格，基礎 18 格是今天 7:00
        const step = Math.floor(duration.asHours() / 8);
        // console.log(step)
        return 18 + step;
    } else if (interval === 3) { // two week 直接看倒數第2格
        return 13;
    }
}


