    debounce(func, wait, immediate) {
        var timeout;
        return () => {
            var context = this, args = arguments;
            var later = function () {
                timeout = null;
                if (!immediate) func.apply(context, args);
            };
            var callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if (callNow) func.apply(context, args);
        };
    }

    debounceWindowSize = this.debounce(this.recalcWindowSize, 500);

    componentDidMount() {
        const { select_interval, PickerDate } = this.props;
        console.log(PickerDate,select_interval)
        if (select_interval === 0) {
            this.onClickCheckgrid_one(timeCheck(select_interval, PickerDate));
        } else if (select_interval === 1) {
            this.onClickCheckgrid_three(timeCheck(select_interval, PickerDate));
        } else if (select_interval === 2) {
            this.onClickCheckgrid_oneweek(timeCheck(select_interval, PickerDate));
        } else if (select_interval === 3) {
            this.onClickCheckgrid_twoweek(timeCheck(select_interval, PickerDate));
        }
        window.addEventListener('resize', this.debounceWindowSize);
        this.setState({ preInterval: select_interval, prePickerDate: PickerDate });
    }

    componentDidUpdate() {
        const { select_interval, PickerDate } = this.props;
        const { preInterval, prePickerDate } = this.state;
        console.log(PickerDate, prePickerDate)
        if (select_interval !== preInterval || PickerDate !== prePickerDate) {
            console.log(select_interval)
            this.setState({ preInterval: select_interval, prePickerDate: PickerDate });
            if (select_interval === 0) {
                this.onClickCheckgrid_one(timeCheck(select_interval, PickerDate));
            } else if (select_interval === 1) {
                this.onClickCheckgrid_three(timeCheck(select_interval, PickerDate));
            } else if (select_interval === 2) {
                this.onClickCheckgrid_oneweek(timeCheck(select_interval, PickerDate));
            } else if (select_interval === 3) {
                this.onClickCheckgrid_twoweek(timeCheck(select_interval, PickerDate));
            }
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.debounceWindowSize);
    }

