import React, { Component } from 'react';
import { Input, Button } from 'antd';
import editlogo from '../Image/svg/Edit.svg'
import './Wardnote.scss'
import deleteimg from '../Image/svg/delete.svg'
import detail from '../Image/svg/details.svg'
import { Tooltip } from 'antd';
import * as ApiProvider from '../API/ApiProvider';
import moment from 'moment'

class WardcardNote extends Component {
    _isMounted = false;
    state = {
        editstate: false,
        AnnoceRawData: [],
        AnnoceDisplayData: [],
        inputContent: undefined,
        powerLogin: 0
    }
    componentWillUnmount() {
        this._isMounted = false;
    }
    componentDidMount() {
        this.onInputChange = this.onInputChange.bind(this);
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.queryAllAnnounce();
    }
    queryAllAnnounce() {
        ApiProvider.getAllAnnounce()
            .then(result => {
                console.log("getAllAnnounce OK " + JSON.stringify(result));
                this.setState({
                    AnnoceRawData: result.data,
                    AnnoceDisplayData: result.data
                })
            })
            .catch(err => {
                console.log("getAllAnnounce NG " + JSON.stringify(err));
            });
    }
    canceledit() {
        //取消前先拿取server資料
        this._isMounted = true
        this.setState({
            inputContent: undefined,
            editstate: false,
            AnnoceDisplayData: JSON.parse(JSON.stringify(this.state.AnnoceRawData))
        });
    }

    okeditlist() {
        //確定上傳
        this.setState({
            editstate: false
        });
        ApiProvider.createAnnounce(this.state.AnnoceDisplayData)
            .then(result => {
                console.log("createAnnounce OK " + JSON.stringify(result));
                this.queryAllAnnounce();
            })
            .catch(err => {
                console.log("createAnnounce NG " + JSON.stringify(err));
            });
    }

    editlist() {
        this.setState({
            editstate: !this.state.editstate,
            AnnoceDisplayData: (this.state.editstate) ? JSON.parse(JSON.stringify(this.state.AnnoceDisplayData)) : JSON.parse(JSON.stringify(this.state.AnnoceRawData))
        });
    }

    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }

    AddNewannouce() {
        const announce = this.state.AnnoceDisplayData;

        const doc = sessionStorage.getItem('user')
        const newAnnounce = {
            context: this.state.inputContent,
            time: +(new Date()),
            editor: doc + "醫生",
            transinno: doc + moment().format("YYYYMMDDHHmmss").toString()
        }
        announce.push(newAnnounce)
        this.setState({
            AnnoceDisplayData: announce,
            inputContent: undefined
        });
    }

    ondeleteannounce(element) {
        const announce = this.state.AnnoceDisplayData;
        const index = announce.findIndex(item => item === element);
        announce.splice(index, 1);
        this.setState({ AnnoceDisplayData: announce });
    }
    announcelist(announcelist, status) {
        var list = []
        if (announcelist === null || announcelist.length === 0) {
            list.push(
                <div className='announcelistcontext'>無公告</div>
            )
        }
        for (let index = 0; index < announcelist.length; index++) {
            const element = announcelist[index];
            list.push(
                <div className='announcelistcontext'>
                    <t2>{index + 1}.{element.context}</t2>
                    {this.editstatus(element, status)}
                </div>
            )
        }
        return list
    }
    editstatus(element, status) {
        const time = moment(element.time, "YYYY-MM-DD[T]HH:mm:ss").format('YYYY-MM-DD HH:mm:ss')
        const localTime = moment.utc(time).toDate()
        const string = element.editor + '醫師於' + moment(localTime).format("MM/DD HH:mm") + '更新'
        if (status) {
            return (
                <img src={deleteimg} alt='deletelogo' onClick={() => this.ondeleteannounce(element)}></img>
            );
        }
        return (
            <Tooltip placement="top" title={string}>
                <img src={detail} alt='detaillogo'></img>
            </Tooltip>
        );
    }
    render() {

        function changehight(state) {
            if (!state) {
                return 'wardnotecontextdisedit'
            }
            else {
                return 'wardnotecontextonedit'

            }
        }
        let annData = this.state.AnnoceDisplayData

        annData = annData.sort((a, b) => {
            let tempTimeA = moment(a.time, "YYYY-MM-DD[T]HH:mm:ss").valueOf()
            let tempTimeB = moment(b.time, "YYYY-MM-DD[T]HH:mm:ss").valueOf()
            return tempTimeA < tempTimeB ? 1 : -1
        })

        return (
            <div>
                <div className='wardnotetitle'>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>病房公告</t2>
                    <div onMouseUp={() => this.editlist()} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", width: "18px", cursor: "pointer" }}>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>
                </div>
                <div>
                    <div className={changehight(this.state.editstate)}>
                        {this.announcelist(annData, this.state.editstate)}
                    </div>
                    <div style={{ display: this.state.editstate ? "block" : "none", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "c" }}>
                        <div style={{ height: "50px", backgroundColor: "rgba(238,238,238,1)", display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <Input placeholder="新增公告內容" style={{ marginLeft: "16px", width: "315px" }} value={this.state.inputContent} onChange={this.onInputChange} />
                            <Button type="primary" style={{ marginRight: "16px" }} onClick={() => this.AddNewannouce()}>
                                新增
                            </Button>
                        </div>
                        <div style={{ height: "50px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <Button style={{ margin: "10px", width: "90px" }} onClick={() => this.canceledit()}>取消</Button>
                            <Button type="primary" style={{ margin: "10px", width: "90px" }} onClick={() => this.okeditlist()}>儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default WardcardNote;
