import moment from "moment"

export function OPABGNDTtimeformat(OPABGNDT) {
    function valueten(value) {
        if (value < 10) {
            return "0" + value
        }
        return value
    }
    if (OPABGNDT === NaN || OPABGNDT === null || OPABGNDT === undefined) {
        return "--"
    }
    const year = parseInt(OPABGNDT / 10000) + 1911
    const month = parseInt((OPABGNDT % 10000) / 100)
    const day = parseInt(OPABGNDT % 100)
    const date = year.toString() + valueten(month) + valueten(day)
    return moment(date, "YYYYMMDD").format("YYYY/MM/DD")
}