import React, { Component } from 'react';
import WardlifeChart from './Wardlifechart'
import Wardmonthafter from './Wardmonthafter'
import * as Wardstate from '../Function/Wardstate'


class Wardgrewchart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            test_items_interval: 0,
            preHID: null,
            preWeek: null,
            preDay: null
        };
    }
    componentDidMount() {

        const { grewinfo, caseinfo } = this.props
        if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined && caseinfo.HISID !== undefined && caseinfo.HISID !== null) {
            this.setState({ preHID: caseinfo.HISID, test_items_interval: Wardstate.WardgrewBtnStatus(grewinfo), preWeek: grewinfo.WEEK_BORN, preDay: grewinfo.DAY_BORN })
        }
    }
    componentDidUpdate() {

        const { grewinfo, caseinfo } = this.props
        const { preHID, preWeek, preDay } = this.state
        if (grewinfo.WEEK_BORN !== undefined && grewinfo.DAY_BORN !== undefined && caseinfo.HISID !== undefined && caseinfo.HISID !== null) {
            if (preHID !== caseinfo.HISID || grewinfo.WEEK_BORN !== preWeek || grewinfo.DAY_BORN !== preDay) {
                this.setState({ preHID: caseinfo.HISID, test_items_interval: Wardstate.WardgrewBtnStatus(grewinfo), preWeek: grewinfo.WEEK_BORN, preDay: grewinfo.DAY_BORN })
            }
        }
    }
    switch_monthy() {
        const { Data, WeightList, grewinfo, caseinfo } = this.props
        const switch_data = this.state.test_items_interval
        const width = (window.screen.availWidth - 650) / 2, height = 350
        switch (switch_data) {
            case 0:
                return <WardlifeChart width={width} height={height} grewinfo={grewinfo} weightList={WeightList} id_key={"wardlife"} svg_key={"wardlife_svg"} gender={Data.PSEX}></WardlifeChart>
            case 1:
                return <Wardmonthafter width={width} height={height} grewinfo={grewinfo} weightList={WeightList} id_key={"wardlife_after"} svg_key={"wardlife_after_svg"} gender={Data.PSEX}></Wardmonthafter>
            default:
                return null;
        }
    }
    //測驗項目功能_區間按鈕樣式
    SwitchTestItemInterval(testiteminterval) {
        const time_selectbtn_style = {
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "25px",
            padding: "0px 5px",
            borderRadius: "4px",
            backgroundColor: "#001a6dbb",
            color: "white",
            fontSize: "1rem",
            borderWidth: "1px",
            borderStyle: "solid",
            borderColor: "#001a6d88",
            cursor: "pointer"
        }
        const time_unselectbtn_style = {
            display: "flex", justifyContent: "center",
            alignItems: "center", height: "25px",
            padding: "0px 5px",
            borderRadius: "4px", backgroundColor: "white",
            color: "#001a6d88", fontSize: "1rem",
            borderWidth: "1px", borderStyle: "solid",
            borderColor: "#001a6d88",
            cursor: "pointer"
        }
        console.log(this.state.test_items_interval)
        if (testiteminterval === this.state.test_items_interval) {
            return time_selectbtn_style
        }
        else return time_unselectbtn_style
    }
    //測驗項目功能_區間按鈕選擇
    Onchangetimeinterval(changestate) {
        this.setState({ test_items_interval: changestate })
    }
    render() {
        // const switch_data = this.state.test_items_interval
        return (
            <div>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: 'center', backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>生長曲線圖</t2>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "5px", alignItems: "center" }}>
                        <div style={this.SwitchTestItemInterval(0)} onMouseUp={() => this.Onchangetimeinterval(0)}>40週前</div>
                        <div style={this.SwitchTestItemInterval(1)} onMouseUp={() => this.Onchangetimeinterval(1)}>40週後</div>
                    </div>
                </div>
                {this.switch_monthy()}
            </div>
        );
    }
}
export default Wardgrewchart
