import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Treatmentordertablecell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';

class Treatmentordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false,
        remark: this.props.info.remark,
        powerLogin: 0
    }

    componentDidMount() {
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.seachRemark()
    }

    seachRemark = () => {
        const { info } = this.props
        ApiProvider.getOneRemark(this.props.caseinfo.TRANSINID, info.ORDSEQNO, "treatment").then(result => {
            console.log("getOneRemark OK " + JSON.stringify(result));
            this.setRemark(result.data[0].remark)
        }).catch(err => {
            console.log("getOneRemark NG " + JSON.stringify(err));
        });
    }

    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        this.setState({
            onEdit: false,
        });
    };

    setRemark = (value) => {
        this.setState({
            remark: value !== undefined ? value : '',
        });
    }

    render() {
        function formatStatus(ORSTATUS) {
            if (Number(ORSTATUS) === 30 || Number(ORSTATUS) === 90) {
                return "生效中"
            }
            if (Number(ORSTATUS) === 60) {
                return "已完成"
            }
            if (Number(ORSTATUS) === 80) {
                return "已取消"
            }
            return ORSTATUS
        }
        const { info } = this.props

        const Name = info.ORPROCED
        const QD = info.ORFREQN
        const Status = formatStatus(info.ORSTATUS)
        const infoRemark = info["15"].toString().trim()
        const ST = moment(info.ORBGNDT.toString(), 'YYYY-MM-DD').format("MM/DD")
        const ET = moment(info.ORENDDT.toString(), 'YYYY-MM-DD').format("MM/DD")

        const remarkValue = {
            "createTime": ST,
            "name": Name,
            "remarkId": info.ORDSEQNO,
            "remark": this.state.remark,
            "transinId": this.props.caseinfo.TRANSINID,
            "type": "treatment",
            "editor": sessionStorage.getItem('user')
        }

        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className="treatmentordercell" style={{ minheight: this.state.remark === '' ? '40px' : '60px', borderTop: '#989898 1px solid', borderBottom: '#989898 1px solid' }}>
                <t3 className='cellstyle' style={{ minheight: this.state.remark === '' ? '40px' : '60px' }}>{ST} - {ET}</t3>
                <div style={{ display: "grid", gridTemplateRows: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "1fr" : "1fr 20px"] }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 40px 70px" }}>
                        <t3 className='cellstyle' style={{ color: "black", fontWeight: 600, borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>{Name}</t3>
                        <t3 className='textHidden' style={{ borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>{infoRemark}</t3>
                        <t3 className='cellstyle' style={{ borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>{QD}</t3>
                        <t3 className='cellstyle' style={{ borderLeft: "rgba(238, 238, 238, 1) 1px solid" }}>{Status}</t3>
                    </div>
                    <t3 className='cellstyle' style={{ borderTop: "rgba(238, 238, 238, 1) 1px solid", fontWeight: 600, color: "rgb(0,120,0", overflow: "hidden", height: "20px", display: [this.state.remark === undefined || this.state.remark === "" ? "none" : ""] }}>{this.state.remark}</t3>
                    <div style={this.state.onMove ?
                        { display: this.state.powerLogin == 1 ? "flex" : "none", justifyContent: "center", alignItems: "center", position: 'absolute', right: 0, height: [this.state.remark === null || this.state.remark === undefined || this.state.remark === "" ? "100%" : "100%"], background: "transparent", width: "77px" }
                        :
                        { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: this.state.remark === '' ? '30px' : '40px', height: this.state.remark === '' ? '30px' : '40px', borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal remarkValue={remarkValue} visible={this.state.onEdit} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}
export default Treatmentordertablecell
